    private static _container: PIXI.Container;
    private static _list: Array<Effect>;
    private static _frames: Array<Array<PIXI.Texture>>;



    public constructor(container: PIXI.Container)
    {
        Effects._container = container;
        Effects._list = new Array<Effect>();

        // convert animation names into textures for all effect types
        Effects._frames = [];
        for(var i = 0; i < Effects.AnimationNames.length; i++)
        {
            Effects._frames[i] = [];
            for(var j = 0; j < Effects.AnimationNames[i].length; j++)
                Effects._frames[i].push( PIXI.Texture.fromImage(Effects.AnimationNames[i][j]) );
        }
    };


    public static killAll():void
    {
        for(var i = Effects._list.length - 1; i >= 0; --i)
        {
            let e: Effect = Effects._list[i];
            e.destroy();
            Effects._container.removeChild(e);
            Effects._list.splice(i, 1);
        }
    }
    

    public static destroy():void
    {
        Effects.killAll();
        Effects._list = null;
        Effects._frames = null;
        Effects._container = null;
    }


    public static addCount(count: number, container: PIXI.Container, type: EffectType, x: number, y: number, z: number, vx?: number, vy?:number): void
    {
        for(let i: number = 0; i < count; ++i)
        {
            let e:Effect = this.add(container, type, x, y, z);
            if (vx != undefined) e._vx += vx;
            if (vy != undefined) e._vy += vy;
        }
    }


    public static add(container: PIXI.Container, type: EffectType, x: number, y: number, z: number, onUpdate?: Function): Effect
    {
        let e: Effect = new Effect(container, Effects._frames[type]);
        e.init(type, x, y, z, onUpdate);
        Effects._list.push(e);
        return e;
    };


    public static addDelayed(delay: number, container: PIXI.Container, type: EffectType, x: number, y: number, z: number, onUpdate?: Function): Effect
    {
        let when:number = Game._now + delay;
        let e: Effect = new Effect(container, Effects._frames[type]);
        e.init(type, x, y, z, onUpdate);
        e._startTime = when;
        e.visible = false;
        Effects._list.push(e);
        return e;
    };


    public static update(): void
    {
        if (!Effects._list)
            return;
        
        for(var i = Effects._list.length - 1; i >= 0; --i)
        {
            let e: Effect = Effects._list[i];
            if (e._startTime != 0)
            {
                if (Game._now > e._startTime)
                {
                    e.visible = true;
                    e._startTime = 0;
                    if (e._onVisible) e._onVisible.call(e);
                }
            }
            if (e.visible && !e.animate())
            {
                e.destroy();
                Effects._list.splice(i, 1);
            }
        }
    };





