/*
 * The various API supported by APIWrapper
 */
export enum APIType {
    NONE,
    POGO
}


/**
 * Possible devices the interactive is being run on
 */
export enum Devices { 
    DESKTOP, 
    TABLET, 
    PHONE 
}

/**
 * Possible layouts the interactive may take
 */
//export enum Layouts {
//    MOBILE = 0,
//    DESKTOP = 1
//}

/**
 * Game states
 */
export enum StateNames { 
    ASSET_LOADER = 0,
    //AUDIO_SPLASH,
    TITLE_SCREEN,
    SIGN_IN,
    GAME
}


/**
 * Movement directions
 */
export const enum Direction
{
    None = 0,
    Vertical,
    Horizontal,
    Both
};


/**
 * Game event types - triggered at various score thresholds
 */
export enum GameEventType
{
    None = 0,
    LevelUp,
    ExtraMoves,
    Multiplier,
};


export enum PieceAnimations
{
    Wait = 0,
    Hovered,
    IdleBlink,
    IdleHop,
    DragUp,
    DropDown,
    StackOpen,
    StackOpenWait,
    StackClose,
    SpinRemove,    // frames: 0080, [0082, 0084, 0086, 0088, 0090, 0092], 0094, 0095, 0096, 0097, 0099, 0100
    Grow,          // grow from megamerge, wobble+bounce then grow
    GrowFast,      // grow from merge, minimal animation
};


export enum PieceColours
{
    Blue = 0,
    Green,
    Yellow
}


export enum BoostModes
{
    None = 0,
    BoostPaint,
    BoostDestroy,
    BoostGrow
}



