import {GameSettings} from "../settings/GameSettings";
import {APIWrapper} from "../util/APIWrapper";
import {LoadSave} from "./LoadSave";
import {Audio} from "../ui/Audio";



// player preferences in a single object ready for load/save
export class PrefObject
{
    public ready:boolean;
    public musicMute:boolean;
    public musicVolume:number;
    public sfxMute:boolean;
    public sfxVolume:number;
    public showTutorial:boolean;
    public showFUE:boolean;



    constructor()
    {
        this.ready = false;
        this.setFromSettings();
    }


    // set all values to their current in-game settings
    public setFromSettings()
    {
        this.musicMute = GameSettings.musicMute;
        this.musicVolume = GameSettings.musicVolume;
        this.sfxMute = GameSettings.sfxMute;
        this.sfxVolume = GameSettings.sfxVolume;
        this.showTutorial = GameSettings.showTutorial;
        this.showFUE = GameSettings.showFUE;
    }


    // restore saved preferences
    public setFromSavedData(jsonData: string):void
    {

        let data:any = JSON.parse(jsonData);
        this.musicMute = data.musicMute;
        this.musicVolume = data.musicVolume;
        this.sfxMute = data.sfxMute;
        this.sfxVolume = data.sfxVolume;
        this.showTutorial = data.showTutorial;
        this.showFUE = data.showFUE;

        // indicate the data has been set (after potential async recovery)
        this.ready = true;
    }


    // save current preferences
    // NOTE: uses the preference changed callback to set them in GameSettings
    // and then transfers them to this object afterwards
    public setFromPreferences():void
    {
        // get all the current preferences
        let prefs:any = APIWrapper.getPreferences();
        if (prefs)
        {
            for(let name in prefs)
            {
                // apply each preference to the GameSettings
                LoadSave.prefChanged(name, prefs[name]);
            }
        }

        // add preferences that are missing
        // (setting these should trigger the preferences changed callback)
        if (!prefs || !prefs["musicMute"])
        {
            APIWrapper.setPreference("musicMute", GameSettings.musicMute);
        }
        if (!prefs || !prefs["musicVolume"])
        {
            APIWrapper.setPreference("musicVolume", GameSettings.musicVolume);
        }
        if (!prefs || !prefs["sfxMute"])
        {
            APIWrapper.setPreference("sfxMute", GameSettings.sfxMute);
        }
        if (!prefs || !prefs["sfxVolume"])
        {
            APIWrapper.setPreference("sfxVolume", GameSettings.sfxVolume);
        }
        if (!prefs || !prefs["showTutorial"])
        {
            APIWrapper.setPreference("showTutorial", GameSettings.showTutorial);
        }
        if (!prefs || !prefs["showFUE"])
        {
            APIWrapper.setPreference("showFUE", GameSettings.showFUE);
        }

        // get the new values out of GameSettings into this class
        this.setFromSettings();

        // indicate the data has been set
        this.ready = true;
    }


    // save this data to preferences
    public saveToPreferences():void
    {
        APIWrapper.setPreference("musicMute", this.musicMute);
        APIWrapper.setPreference("musicVolume", this.musicVolume);
        APIWrapper.setPreference("sfxMute", this.sfxMute);
        APIWrapper.setPreference("sfxVolume", this.sfxVolume);
        APIWrapper.setPreference("showTutorial", this.showTutorial);
        APIWrapper.setPreference("showFUE", this.showFUE);
    }


    // apply these preferences to the game
    public apply():void
    {
        GameSettings.showFUE = this.showFUE;
        GameSettings.showTutorial = this.showTutorial;
        GameSettings.musicMute = this.musicMute;
        GameSettings.sfxMute = this.sfxMute;
        GameSettings.musicVolume = this.musicVolume;
        GameSettings.sfxVolume = this.sfxVolume;
        Audio.set();
    }


    // return a JSON string representation of this class values
    public toJson():string
    {
        return JSON.stringify(this, function(key, value)
            {
                // don't include the 'ready' field
                if (key == "ready")
                    return undefined;
                return value;
            });
    }

}



