import {GameSettings} from "../settings/GameSettings";



export class Background
{
    private _container: PIXI.Container;
    private _bgTop: PIXI.Sprite;
    private _bgRight: PIXI.Sprite;
    private _bgLeft: PIXI.Sprite;
    private _bgBottomRight: PIXI.Sprite;


    public constructor(container: PIXI.Container)
    {
        this._container = container;

        // create the sprites
        this._bgTop = new PIXI.Sprite( PIXI.Texture.fromImage("assets/BG_up_part") );
        this._bgRight = new PIXI.Sprite( PIXI.Texture.fromImage("assets/BG_right_part") );
        this._bgBottomRight = new PIXI.Sprite( PIXI.Texture.fromImage("assets/BG_right_down_part") );
        this._bgLeft = new PIXI.Sprite( PIXI.Texture.fromImage("assets/BG_right_part") );
        // set scale and positions
        this.resize(null);
        // add them to the parent container
        this._container.addChild(this._bgTop);
        this._container.addChild(this._bgRight);
        this._container.addChild(this._bgLeft);
        this._container.addChild(this._bgBottomRight);
    }


    public destroy():void
    {
        this._bgTop.destroy();
        this._bgRight.destroy();
        this._bgBottomRight.destroy();
        this._bgLeft.destroy();
    }


    public resize(playAreaRect: PIXI.Rectangle):void
    {
        if (this._bgTop && this._bgRight && this._bgLeft && this._bgBottomRight)
        {
            let scale: number = GameSettings.width / 1280;    // width of BG_up_part

            this._bgTop.scale.set(scale);
            this._bgTop.anchor.set(0);
            this._bgTop.x = 0;
            this._bgTop.y = 0;

            scale = GameSettings.height / (766 + 270);        // height of BG_right_part + height of BG_right_down_part

            this._bgRight.scale.set(scale);
            this._bgRight.anchor.set(1, 0);
            this._bgRight.x = GameSettings.width;
            this._bgRight.y = 0;

            this._bgLeft.scale.set(-scale, scale);
            this._bgLeft.anchor.set(1, 0);
            this._bgLeft.x = 0;
            this._bgLeft.y = 0;

            this._bgBottomRight.scale.set(scale);
            this._bgBottomRight.anchor.set(1, 1);
            this._bgBottomRight.x = GameSettings.width;
            this._bgBottomRight.y = GameSettings.height;

            // display or hide the left edge trim based on proximity of the playarea left edge
            if (playAreaRect == null)
                this._bgLeft.visible = false;
            else
                this._bgLeft.visible = (playAreaRect.x > 64);
        }
    }

}


