import {GameSettings} from "../settings/GameSettings";
import {SimpleButton} from "../ui/SimpleButton";
import {State} from "./State";
import {StateManager} from "./StateManager";
import {StateNames} from "../const/Enums";
import {Events} from "../const/Events";
import {SignIn} from "../ui/SignIn";
import {LoadSave} from "../util/LoadSave";



export class SignInState extends State
{
    private _container: PIXI.Container;
    private _backgroundImage: PIXI.Sprite;
    private _signIn: SignIn;


    
    /**
     * Calls the base State constructor
     * @param {StateManager} controller  A reference to the state controller
     * @param {PIXI.Container} stage        A reference the stage, the main PIXI container
     */
    public constructor(controller: StateManager, stage: PIXI.Container)
    {
        super(controller, stage);
    }
    

    public init():void
    {
        this._container = new PIXI.Container;
        this._stage.addChild(this._container);

        this._backgroundImage = new PIXI.Sprite(PIXI.Texture.fromImage("intro/IntroPanel_back"));
        this._backgroundImage.anchor.set(0.5);
        this._container.addChild(this._backgroundImage);

        this._signIn = new SignIn(this._container, this.onSignUpClosed.bind(this));

        this.resize();
    }

    
    private onSignUpClosed():void
    {
        // trigger the load again, to get player profile if he logged in
        LoadSave.load();
        this._manager.startState(StateNames.TITLE_SCREEN);
    }
    
    
    public update():void
    {
    }
    

    public resize():void
    {
        let scale = Math.max(GameSettings.width / this._backgroundImage.texture.width, GameSettings.height / this._backgroundImage.texture.height);

        this._backgroundImage.x = GameSettings.width * 0.5;
        this._backgroundImage.y = GameSettings.height * 0.5;
        this._backgroundImage.scale.set(scale);
        if (this._signIn) this._signIn.resize();
    }
    

    public destroy():void
    {
        this._backgroundImage.destroy();
        this._signIn.destroy();
        this._container.destroy();
    }
}



