import {GameSettings} from "../settings/GameSettings";
import {Direction, BoostModes} from "../const/Enums";



export class BoardInput
{
    public static _dragLine: PIXI.Point;

    private static _dragging: Direction;
    private static _dragStart: PIXI.Point;
    private static _dragEnabled: boolean;
    private static _boostSelection: BoostModes;

    private static _container: PIXI.Container;
    private static _emitter: PIXI.utils.EventEmitter;



    public constructor(container: PIXI.Container, emitter: PIXI.utils.EventEmitter)
    {
        BoardInput._container = container;
        BoardInput._emitter = emitter;

        BoardInput._dragging = Direction.None;
        BoardInput._dragStart = null;
        BoardInput._dragLine = null;
        BoardInput._dragEnabled = false;

        BoardInput._boostSelection = BoostModes.None;


        BoardInput._emitter.on("drag_enable", function(value: boolean)
            {
                BoardInput._dragEnabled = value;
            });

        BoardInput._emitter.on("boost_select", function(value: BoostModes)
            {
                BoardInput._boostSelection = value;
            });
    }


    public static destroy():void
    {
        BoardInput._emitter.removeListener("drag_enable");
        BoardInput._emitter.removeListener("boost_select");
    }


    // traverse the hierarchy to calculate the total scale factor of a display object
    public static getGlobalScale(_do: PIXI.DisplayObject):PIXI.Point
    {
        let s: PIXI.Point = new PIXI.Point(1, 1);
        let o: PIXI.DisplayObject = _do;
        while(o)
        {
            s.x *= o.scale.x;
            s.y *= o.scale.y;
            o = o.parent;
        }
        return s;
    }


    // private static getGlobalPosition(_from: PIXI.DisplayObject, _to: PIXI.DisplayObject):PIXI.Point
    // {
    //     let gp: PIXI.Point = _from.getGlobalPosition();
    //     let p: PIXI.Point = _to.getGlobalPosition();
    //     let scale: PIXI.Point = BoardInput.getGlobalScale(_to.parent);
    //     gp.x -= p.x;
    //     gp.y -= p.y;
    //     gp.x /= scale.x;
    //     gp.y /= scale.y;
    //     return gp;
    // }


    //
    // input event handlers called from BoardSquare on input events
    //

    public static dragStart(data: PIXI.interaction.InteractionData, boardPos: PIXI.Point)
    {
        if (BoardInput._boostSelection != BoostModes.None)
        {
            BoardInput._emitter.emit("board_clicked_for_boost", boardPos.clone());
        }
        else if (BoardInput._dragEnabled && BoardInput._dragStart == null)
        {
            BoardInput._dragStart = data.global.clone();    //data.getLocalPosition(BoardInput._container);
            BoardInput._dragLine = boardPos.clone();
            BoardInput._emitter.emit("drag_start");
        }
    }


    public static dragMove(data: PIXI.interaction.InteractionData)
    {
        if (BoardInput._dragStart)
        {
            // total offset from dragStart location
            let pos: PIXI.Point = data.global.clone();    //data.getLocalPosition(BoardInput._container);
            let x: number = pos.x - BoardInput._dragStart.x;
            let y: number = pos.y - BoardInput._dragStart.y;

            if (BoardInput._dragging == Direction.None)
            {
                // far enough to trigger?
                if (x * x + y * y < 6 * 6)
                    return;

                // horiz or vertical?
                if (Math.abs(x) > Math.abs(y))
                {
                    BoardInput._dragging = Direction.Horizontal;
                    BoardInput._dragLine.x = -1;        // remember only the y
                }
                else
                {
                    BoardInput._dragging = Direction.Vertical;
                    BoardInput._dragLine.y = -1;        // remember only the x
                }
            }

            // move the entire row or column by an offset amount
            if (BoardInput._dragging == Direction.Horizontal)
            {
                BoardInput._emitter.emit("drag_horiz", x);
            }

            if (BoardInput._dragging == Direction.Vertical)
            {
                BoardInput._emitter.emit("drag_vert", y);
            }
        }
    }


    public static dragEnd(data: PIXI.interaction.InteractionData)
    {
        if (BoardInput._dragStart)
        {
            // finalise the position of the row or column to snap into the nearest squares from their offset positions
            if (BoardInput._dragging == Direction.Horizontal)
            {
                BoardInput._emitter.emit("lock_horiz", BoardInput._dragLine.y);
            }

            if (BoardInput._dragging == Direction.Vertical)
            {
                BoardInput._emitter.emit("lock_vert", BoardInput._dragLine.x);
            }

            BoardInput._dragging = Direction.None;
            BoardInput._dragStart = null;
            BoardInput._emitter.emit("drag_ended");
        }
    }


    public static isDragging():boolean
    {
        return (BoardInput._dragging != Direction.None);
    }


    public static mouseOver(data: PIXI.interaction.InteractionData, boardPos: PIXI.Point)
    {
        BoardInput._emitter.emit("start_hover", boardPos.clone());    
    }


    public static mouseOut(data: PIXI.interaction.InteractionData, boardPos: PIXI.Point)
    {
        BoardInput._emitter.emit("end_hover", boardPos.clone());    
    }

}


