import {MissionEvents, MissionData, MissionDescriptions} from "../util/MissionData";
import {SimpleButton} from "./SimpleButton";
import {Audio, Sfx} from "./Audio";
import {GameSettings} from "../settings/GameSettings";
import {Events} from "../const/Events";
import {LoadSave} from "../util/LoadSave";



// show a single mission's data
export class MissionItem extends PIXI.Container
{
    public data: MissionData;

    public _title: PIXI.Text;
    public _textStyle: PIXI.TextStyle;
    public _trophy: PIXI.Sprite;
    public _pedestal: PIXI.Sprite;
    public _counts: PIXI.Text;
    public _box: PIXI.Graphics;
    public _description: PIXI.Text;

    public _type: string;
    public _required: number;
    //public _value: number;



    constructor(data: MissionData)
    {
        super();

        this.data = data;
        this._type = this.data._type;
        this._required = this.data._required;
        //this._value = this.data._goalNum;

        this._textStyle = new PIXI.TextStyle({
            fontFamily: "GrilledCheese",
            fontSize: 24,
            fontWeight: 'normal',
            fill: ['#ffffff'],
            stroke: 0x000000,
            strokeThickness: 3,
            dropShadow: false,
            align: 'center',
            leading: -2,
            wordWrap: true,
            wordWrapWidth: 170
        });

        this._title = new PIXI.Text(this.data._name, this._textStyle);
        this._title.anchor.set(0.5);
        this._trophy = new PIXI.Sprite(PIXI.Texture.fromImage("popups/Trophy_room/trophy/" + this.data._imgUrl));
        this._trophy.anchor.set(0.5, 1.0);
        this._trophy.y = this._title.y + 210;
        if (this.data._completed < this._required)
        {
            this._trophy.tint = 0x000000;
            this._trophy.alpha = 0.40;
            // NOTE: we cannot add a GlowFilter to an alpha blended trophy, it recolours the entire shape instead of adding an edge outline
            //this._trophy.filters = [ new GlowFilter(2, 1, 1, 0xffffff, 1)];
        }
        this._pedestal = new PIXI.Sprite(PIXI.Texture.fromImage("popups/Trophy_room/trophy/podium"));
        this._pedestal.anchor.set(0.5)
        this._pedestal.y = this._trophy.y + 10;
        this._counts = new PIXI.Text(this.data._completed + "/" + this._required, this._textStyle);
        this._counts.anchor.set(0.5);
        this._counts.y = this._pedestal.y + 50;
        this._box = new PIXI.Graphics();
        this._box.lineStyle(3, 0xffffff, 0.5);
        this._box.beginFill(0x00005f, 0.5);
        this._box.drawRoundedRect(-90, -70, 180, 140, 5);
        this._box.endFill();
        this._box.y = this._counts.y + 30 + 70;
        this._description = new PIXI.Text(this.data._description, this._textStyle);
        this._description.anchor.set(0.5, 0.0);
        this._description.y = -70 + 5;

        // create hierarchy
        this._box.addChild(this._description);
        this.addChild(this._title);
        this.addChild(this._pedestal);
        this.addChild(this._trophy);
        this.addChild(this._counts);
        this.addChild(this._box);
    }


    public updateProgress():void
    {
        if (this.data._completed < this._required)
        {
            this._trophy.tint = 0x000000;
            this._trophy.alpha = 0.40;
        }
        else
        {
            this._trophy.tint = 0xffffff;
            this._trophy.alpha = 1.00;
        }
        this._counts.text = this.data._completed.toString() + "/" + this._required.toString();
    }

}



// popup to show the currently active missions
export class MissionPopup extends PIXI.Container
{
    private _parent: PIXI.Container;

    private _blanker: PIXI.Graphics;
    private _background: PIXI.Sprite;

    private _closeButton: SimpleButton;
    private _titleText: PIXI.Text;
    private _titleTextStyle: PIXI.TextStyle;

    private _pageBack: PIXI.Sprite;
    private _pageNumber: PIXI.Text;
    private _pageTextStyle: PIXI.TextStyle;
    private _prevPage: SimpleButton;
    private _nextPage: SimpleButton;

    private _playButton: SimpleButton;
    private _playTextStyle: PIXI.TextStyle;
    private _activeMissionItems: Array<MissionItem>;

    private _page: number;
    private readonly _pages: number = 4;



    constructor(parent: PIXI.Container)
    {
        super();
        this._parent = parent;

        this._page = 0;

        // the blanker, goes behind the popup
        this._blanker = new PIXI.Graphics();
        this._blanker.beginFill(0x000000, 0.75);
        this._blanker.drawRect(0, 0, 256, 256);
        this._blanker.endFill();
        this._blanker.visible = true;
        this._blanker.interactive = true;
        this._blanker.buttonMode = false;
        this._blanker.on(Events.CLICKED, function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); });
        this._blanker
                .on('pointerdown', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('pointerup', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('pointerupoutside', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('pointermove', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('mouseover', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('mouseout', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); });
        this.addChild(this._blanker);

        this.createTextStyles();
        this.createLayout();

        this.visible = false;
    }


    private createTextStyles():void
    {
        this._titleTextStyle = new PIXI.TextStyle({
            fontFamily: "GrilledCheese",
            fontSize: 60,
            fontWeight: 'bold',
            fill: ['#afff1f', '#8faf4f'],
            stroke: '#000000',
            dropShadow: true,
            dropShadowAlpha: 0.5,
            dropShadowAngle: Math.PI / 2,
            dropShadowBlur: 10,
            dropShadowColor: 'black',
            dropShadowDistance: 3,            
            align: 'center',
            leading: -8
        });
        this._playTextStyle = new PIXI.TextStyle({
            fontFamily: "GrilledCheese",
            fontSize: 50,
            fontWeight: 'normal',
            fill: ['#000000'],
            dropShadow: false,
            align: 'center',
            leading: -8
        });
        this._pageTextStyle = new PIXI.TextStyle({
            fontFamily: "GrilledCheese",
            fontSize: 40,
            fontWeight: 'normal',
            fill: ['#cfff7f'],
            dropShadow: false,
            align: 'center',
            leading: -8
        });
    }


    private createLayout():void
    {
        this._background = new PIXI.Sprite(PIXI.Texture.fromImage("popups/Missions/Missions_options_quit_panel_grey"));
        this._background.tint = 0x3f8fff;    // 0x5fdf1f;
        this._background.anchor.set(0.5);
        this._background.interactive = false;

        // close button
        this._closeButton = new SimpleButton(PIXI.Texture.fromImage("popups/Close_all/close_btn_up"));
        this._closeButton.events.on(Events.CLICKED, this.onCloseButtonClicked.bind(this));
        this._closeButton.setSfx(Sfx.button_rollover, Sfx.button_press);
        this._closeButton.x = 340;
        this._closeButton.y = -245;

        // title
        this._titleText = new PIXI.Text("Missions", this._titleTextStyle);
        this._titleText.anchor.set(0.5);
        this._titleText.x = 0;
        this._titleText.y = -275;

        // missions
        this._activeMissionItems = new Array<MissionItem>();
        for(let i:number = 0; i < GameSettings.openedTrophies.length; i++)
        {
            let m: MissionItem = new MissionItem(MissionDescriptions.data[GameSettings.openedTrophies[i]]);
            m.x = -240 + 240 * (i % 3);        // three items per page
            m.y = -190;
            this._background.addChild(m);
            this._activeMissionItems.push(m);
        }
        this.updateActiveProgress();
        this.showPageContents();

        // page counter
        this._pageBack = new PIXI.Sprite(PIXI.Texture.fromImage("popups/Missions/01"));
        this._pageBack.scale.set(0.80);
        this._pageBack.anchor.set(0.5);
        this._pageBack.alpha = 0.8;
        this._pageBack.x = 210;
        this._pageBack.y = -400;
        this._pageNumber = new PIXI.Text((this._page+1) + "/" + this._pages, this._pageTextStyle);
        this._pageNumber.anchor.set(0.5);
        this._pageNumber.x = this._pageBack.x;
        this._pageNumber.y = this._pageBack.y;

        // page buttons
        this._prevPage = new SimpleButton(PIXI.Texture.fromImage("popups/Missions/btn_Missions_arrow_up"));
        this._prevPage.events.on(Events.CLICKED, this.onPrevPageClicked.bind(this));
        this._prevPage.setSfx(Sfx.button_rollover, Sfx.button_press);
        this._prevPage.scale.set(-0.8, 0.8);    // flip horizontally
        this._prevPage.x = -410;
        this._prevPage.y = 0;
        this._nextPage = new SimpleButton(PIXI.Texture.fromImage("popups/Missions/btn_Missions_arrow_up"));
        this._nextPage.events.on(Events.CLICKED, this.onNextPageClicked.bind(this));
        this._nextPage.setSfx(Sfx.button_rollover, Sfx.button_press);
        this._nextPage.scale.set(0.8);
        this._nextPage.x = 410;
        this._nextPage.y = 0;

        // play button
        this._playButton = new SimpleButton(PIXI.Texture.fromImage("popups/Green_btn"));
        this._playButton.events.on(Events.CLICKED, this.onCloseButtonClicked.bind(this));
        this._playButton.addLabel("PLAY", this._playTextStyle);
        this._playButton.setSfx(Sfx.button_rollover, Sfx.button_press);
        this._playButton.x = 0;
        this._playButton.y = 345;

        // create hierarchy
        this._background.addChild(this._pageBack);
        this._background.addChild(this._pageNumber);
        this._background.addChild(this._closeButton);
        this._background.addChild(this._prevPage);
        this._background.addChild(this._nextPage);
        this._background.addChild(this._titleText);
        this._background.addChild(this._playButton);
        this.addChild(this._background);
        this._parent.addChild(this);
    }


    private tween(appear: boolean, onComplete?: any):void
    {
        //console.log("tween missions " + appear);
        if (appear)
        {
            Audio.playSfx(Sfx.option_panel_open);
            this.resize();
            this._background.x = this._background.y = 0;
            this._background.scale.set(2.0, 0.5);

            let bounceHalfTime = 100;
            let bounceTween = new TWEEN.Tween(this._background.scale);
            bounceTween.to({x: 1.0, y: 1.0}, bounceHalfTime);
            bounceTween.easing(TWEEN.Easing.Quadratic.InOut);

            let overshootTween = new TWEEN.Tween(this._background.scale);
            overshootTween.to({x: 0.85, y: 1.15}, bounceHalfTime);
            overshootTween.easing(TWEEN.Easing.Quadratic.Out);

            let overshoot2Tween = new TWEEN.Tween(this._background.scale);
            overshoot2Tween.to({x: 1.05, y: 0.95}, bounceHalfTime);
            overshoot2Tween.easing(TWEEN.Easing.Quadratic.Out);

            let settleTween = new TWEEN.Tween(this._background.scale);
            settleTween.to({x: 1.0, y: 1.0}, bounceHalfTime);
            settleTween.easing(TWEEN.Easing.Quadratic.Out);

            bounceTween.chain(overshootTween);
            overshootTween.chain(overshoot2Tween);
            overshoot2Tween.chain(settleTween);
            bounceTween.start();
        }
        else
        {
            Audio.playSfx(Sfx.option_panel_open);
            this.resize();
            let flyTween = new TWEEN.Tween(this._background);
            flyTween.to({x:-2000}, 150);
            flyTween.start();
            if (onComplete)
            {
                flyTween.onComplete(onComplete);
            }
        }
    }


    private onPrevPageClicked():void
    {
        if (this._page > 0)
            this._page--;
        else
            this._page = this._pages - 1;
        this._pageNumber.text = (this._page+1) + "/" + this._pages;
        this.showPageContents();
    }


    private onNextPageClicked():void
    {
        if (this._page < this._pages - 1)
            this._page++;
        else
            this._page = 0;
        this._pageNumber.text = (this._page+1) + "/" + this._pages;
        this.showPageContents();
    }


    private onCloseButtonClicked():void
    {
        let _that: MissionPopup = this;
        this.tween(false, function() {
                _that.visible = false;
            });
    }


    private updateActiveProgress():void
    {
        for(let i:number = 0; i < this._activeMissionItems.length; i++)
        {
            let m: MissionItem = this._activeMissionItems[i];
            m.updateProgress();
        }
    }


    private showPageContents():void
    {
        let f:number = this._page * 3;
        for(let i:number = 0; i < this._activeMissionItems.length; i++)
        {
            let m:MissionItem = this._activeMissionItems[i];
            m.visible = (i >= f && i < f + 3);
        }
    }


    public show(visible: boolean):void
    {
        this.resize();
        this.visible = visible;
        this.tween(true);
    }


    public destroy():void
    {
        this._blanker.removeAllListeners();
        this._blanker.destroy();
        this._closeButton.destroy();
        this._prevPage.destroy();
        this._nextPage.destroy();
        this._playButton.destroy();
        this._parent.removeChild(this);
        this._parent = null;
        super.destroy();
    }


    public resize():void
    {
        this.x = GameSettings.width / 2;
        this.y = GameSettings.height / 2;

        let scale:number = Math.min(GameSettings.width / this._background.width * 0.90, GameSettings.height / this._background.height * 0.90);
        this.scale.set(scale);

        this._blanker.x = -GameSettings.width / 2 / scale;
        this._blanker.y = -GameSettings.height / 2 / scale;
        this._blanker.width = GameSettings.width / scale;
        this._blanker.height = GameSettings.height / scale;
       
        // this._playButton.resize(scale);
    }

}



