import {GameSettings} from "../settings/GameSettings";
import {Events} from "../const/Events";
import {LoadSave} from "../util/LoadSave";
import {SimpleButton} from "./SimpleButton";
import {CheckBox} from "./CheckBox";
import {Audio, Sfx} from "./Audio";



export class Help extends PIXI.Container
{
    private _parent: PIXI.Container;
    private _emitter: PIXI.utils.EventEmitter;
    private _blanker: PIXI.Graphics;

    private _background: PIXI.Sprite;
    
    private _closeButton: SimpleButton;

    private _pageText: PIXI.Text;
    private _pageTextStyle: PIXI.TextStyle;
    private _nextButton: SimpleButton;
    private _prevButton: SimpleButton;

    private _contents: PIXI.Sprite;
    private _arrow: PIXI.Sprite;
    private _arrow2: PIXI.Sprite;

    private _tutorialText: PIXI.Sprite;
    private _tutorialButton: CheckBox;

    private _page: number;
    private _onComplete: any;



    constructor(container: PIXI.Container, emitter: PIXI.utils.EventEmitter)
    {
        super();
        this._parent = container;
        this._emitter = emitter;

        // the blanker, goes behind the popup
        this._blanker = new PIXI.Graphics();
        this._blanker.beginFill(0x000000, 0.75);
        this._blanker.drawRect(0, 0, 256, 256);
        this._blanker.endFill();
        this._blanker.visible = true;
        this._blanker.interactive = true;
        this._blanker.buttonMode = false;
        this._blanker.on(Events.CLICKED, function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); });
        this._blanker
                .on('pointerdown', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('pointerup', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('pointerupoutside', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('pointermove', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('mouseover', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('mouseout', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); });
        this.addChild(this._blanker);

        this._pageTextStyle = new PIXI.TextStyle({
            fontFamily: "GrilledCheese",
            fontSize: 36,
            fontWeight: 'bold',
            fill: ['#ffff1f','#cfcf00'],
            stroke: '#000000',
            strokeThickness: 2,
            dropShadow: true,
            dropShadowAlpha: 0.25,
            dropShadowAngle: Math.PI / 4,
            dropShadowBlur: 2,
            dropShadowColor: 'black',
            dropShadowDistance: 2,
            align: 'center',
            leading: -8
        });

        this._page = 1;
        this.buildPage(this._page);

        this._parent.addChild(this);
        this.resize();

        this._onComplete = null;

        let _that: Help = this;
        this._emitter.on("show_help", function()
            {
                _that.visible = true;
                _that.showTutorialButton();
                _that.tween(true);
            });
    }


    private changePage():void
    {
        let _that: Help = this;
        this.tween(false, function() {
                _that.removePage();
                _that.buildPage(_that._page);
                _that.tween(true);
            });
    }


    private buildPage(page: number):void
    {
        let _that: Help = this;

        this._background = new PIXI.Sprite(PIXI.Texture.fromImage("popups/How_to_play/help_panel_with_text"));
        this._background.anchor.set(0.5);
        this._background.interactive = true;
        this._background.buttonMode = false;

        // page number
        this._pageText = new PIXI.Text(page.toString() + "/4", this._pageTextStyle);
        this._pageText.anchor.set(0.5);
        this._pageText.x = 0;
        this._pageText.y = -375;

        // page buttons
        this._prevButton = new SimpleButton(PIXI.Texture.fromImage("popups/How_to_play/right_up"));
        this._prevButton.events.on(Events.CLICKED, function() { _that._page--; _that.changePage(); } );
        this._prevButton.scale.set(-0.8, 0.8);
        this._prevButton.setSfx(Sfx.button_rollover, Sfx.button_press);
        this._prevButton.x = this._pageText.x - 70;
        this._prevButton.y = this._pageText.y;

        this._nextButton = new SimpleButton(PIXI.Texture.fromImage("popups/How_to_play/right_up"));
        this._nextButton.events.on(Events.CLICKED, function() { _that._page++; _that.changePage(); } );
        this._nextButton.scale.set(0.8);
        this._nextButton.setSfx(Sfx.button_rollover, Sfx.button_press);
        this._nextButton.x = this._pageText.x + 70;
        this._nextButton.y = this._pageText.y;

        switch(page)
        {
            case 1:
                this._prevButton.visible = false;
                this._contents = new PIXI.Sprite(PIXI.Texture.fromImage("popups/How_to_play/01_all_elements_no_bg/Help1_4"));
                this._contents.y = 24;
                this._arrow = new PIXI.Sprite(PIXI.Texture.fromImage("popups/How_to_play/Help_arrow"));
                this._arrow.anchor.set(0, 0);
                this._arrow.x = -400;
                this._arrow.y = -140;
                this._arrow2 = new PIXI.Sprite(PIXI.Texture.fromImage("popups/How_to_play/Help_arrow"));
                this._arrow2.anchor.set(0, 0);
                this._arrow2.x = 40;
                this._arrow2.y = -110;
                break;
            case 2:
                this._contents = new PIXI.Sprite(PIXI.Texture.fromImage("popups/How_to_play/01_all_elements_no_bg/Help2_4"));
                this._contents.y = 16;
                break;
            case 3:
                this._contents = new PIXI.Sprite(PIXI.Texture.fromImage("popups/How_to_play/01_all_elements_no_bg/Help3_4"));
                this._contents.y = 32;
                break;
            case 4:
                this._nextButton.visible = false;
                this._contents = new PIXI.Sprite(PIXI.Texture.fromImage("popups/How_to_play/01_all_elements_no_bg/Help4_4"));
                this._contents.y = 16;
                break;
        }

        if (this._contents)
        {
            this._contents.anchor.set(0.5);
        }

        // close button
        this._closeButton = new SimpleButton(PIXI.Texture.fromImage("popups/Close_all/close_btn_up"));
        this._closeButton.events.on(Events.CLICKED, this.onCloseButtonClicked.bind(this));
        this._closeButton.setSfx(Sfx.button_rollover, Sfx.button_press);
        this._closeButton.x = 460;
        this._closeButton.y = -265;

        // tutorial text
        this._tutorialText = new PIXI.Sprite(PIXI.Texture.fromImage("popups/How_to_play/Show-tutorial-at-start"));
        this._tutorialText.anchor.set(0.5);
        this._tutorialText.x = -50;
        this._tutorialText.y = 365;
        this.showTutorialButton();

        // create display hierarchy
        if (this._contents)
        {
            if (this._arrow) this._contents.addChild(this._arrow);
            if (this._arrow2) this._contents.addChild(this._arrow2);
            this._background.addChild(this._contents);
        }
        this._background.addChild(this._pageText);
        this._background.addChild(this._prevButton);
        this._background.addChild(this._nextButton);
        this._background.addChild(this._closeButton);
        this._background.addChild(this._tutorialText);
        this._background.addChild(this._tutorialButton);
        this.addChild(this._background);
    }


    private removePage():void
    {
        if (this._contents) this._contents.destroy();
        this._contents = null;
        if (this._arrow) this._arrow.destroy();
        this._arrow = null;
        if (this._arrow2) this._arrow2.destroy();
        this._arrow2 = null;
        this._prevButton.destroy();
        this._nextButton.destroy();
        this._closeButton.destroy();
        this._tutorialButton.destroy();
        this._background.removeChild(this._pageText);
        this._background.removeChild(this._prevButton);
        this._background.removeChild(this._nextButton);
        this._background.removeChild(this._closeButton);
        this._background.removeChild(this._tutorialText);
        this._background.removeChild(this._tutorialButton);
        this.removeChild(this._background);
    }


    private tween(appear: boolean, onComplete?: any):void
    {
        //console.log("tween help " + appear);
        if (appear)
        {
            Audio.playSfx(Sfx.help_panel_open);
            this.resize();
            this._background.x = this._background.y = 0;
            this._background.scale.set(2.0, 0.5);

            let bounceHalfTime = 100;
            let bounceTween = new TWEEN.Tween(this._background.scale);
            bounceTween.to({x: 1.0, y: 1.0}, bounceHalfTime);
            bounceTween.easing(TWEEN.Easing.Quadratic.InOut);

            let overshootTween = new TWEEN.Tween(this._background.scale);
            overshootTween.to({x: 0.85, y: 1.15}, bounceHalfTime);
            overshootTween.easing(TWEEN.Easing.Quadratic.Out);

            let overshoot2Tween = new TWEEN.Tween(this._background.scale);
            overshoot2Tween.to({x: 1.05, y: 0.95}, bounceHalfTime);
            overshoot2Tween.easing(TWEEN.Easing.Quadratic.Out);

            let settleTween = new TWEEN.Tween(this._background.scale);
            settleTween.to({x: 1.0, y: 1.0}, bounceHalfTime);
            settleTween.easing(TWEEN.Easing.Quadratic.Out);

            bounceTween.chain(overshootTween);
            overshootTween.chain(overshoot2Tween);
            overshoot2Tween.chain(settleTween);
            bounceTween.start();
        }
        else
        {
            Audio.playSfx(Sfx.help_panel_open);
            this.resize();
            let flyTween = new TWEEN.Tween(this._background);
            flyTween.to({x:-2000}, 150);
            flyTween.start();
            if (onComplete)
            {
                flyTween.onComplete(onComplete);
            }
        }
    }


    private showTutorialButton():void
    {
        if (this._tutorialButton)
        {
            this._background.removeChild(this._tutorialButton);
            this._tutorialButton.events.off(Events.CLICKED, this.onTutorialButtonClicked.bind(this));
            this._tutorialButton.destroy();
        }

        this._tutorialButton = new CheckBox(PIXI.Texture.fromImage("popups/Check_mark/Check-Mark_bg"), GameSettings.showTutorial);
        this._tutorialButton.events.on(Events.CLICKED, this.onTutorialButtonClicked.bind(this));
        this._tutorialButton.setSfx(Sfx.button_rollover, Sfx.button_press);
        this._tutorialButton.x = this._tutorialText.x + 310;
        this._tutorialButton.y = this._tutorialText.y;
        this._background.addChild(this._tutorialButton);
    }


    private onCloseButtonClicked():void
    {
        let _that: Help = this;
        this.tween(false, function() {
                _that.visible = false;
                _that._emitter.emit("close_help");
                if (_that._onComplete) _that._onComplete.call(this);
                // once only...
                _that._onComplete = null;
            });
    }


    private onTutorialButtonClicked():void
    {
        GameSettings.showTutorial = this._tutorialButton.toggle();
        LoadSave.save();
    }


    public show(visible: boolean, onComplete?: any):void
    {
        this._onComplete = onComplete;
        this.visible = visible;
        this.showTutorialButton();
        if (!this.visible)
        {
            // if the Help is hidden and we have a callback, invoke it immediately
            if (this._onComplete) this._onComplete.call(this);
            this._onComplete = null;
        }
    }


    public destroy():void
    {
        this._blanker.removeAllListeners();
        this._emitter.removeAllListeners();

        this._prevButton.destroy();
        this._nextButton.destroy();
        this._pageText.destroy();
        this._closeButton.destroy();
        this._tutorialText.destroy();
        this._tutorialButton.destroy();

        this._background.destroy();
        this._blanker.destroy();
        this._parent.removeChild(this);
        this._parent = null;
        super.destroy();
    }


    public resize():void
    {
        this.x = GameSettings.width / 2;
        this.y = GameSettings.height / 2;

        let scale:number = Math.min(GameSettings.width / this._background.width * 0.90, GameSettings.height / this._background.height * 0.90);
        this.scale.set(scale);

        this._blanker.x = -GameSettings.width / 2 / scale;
        this._blanker.y = -GameSettings.height / 2 / scale;
        this._blanker.width = GameSettings.width / scale;
        this._blanker.height = GameSettings.height / scale;

        // this._tutorialText.scale.set(scale);
    }


}


