//
// FUE tutorial for Trizzle
//
// Pete Baron 2018
//
//
// a state machine triggered by game signals generates balloon messages
// area highlights, moving arrows, etc
//
// this extends a PIXI Container which must be added to the display
// hierarchy at an appropriate level (over everything except the mouse?)
//
import {GlowFilter} from "pixi-filters";
import {Game} from "../Game";
import {Effects, EffectType} from "./Effects";
import {GameUi} from "./GameUi";
import {GameSettings} from "../settings/GameSettings";
import {PlayArea, BoardLocation} from "../engine/PlayArea";
import {BoardSquare} from "../engine/BoardSquare";
import {Piece} from "../engine/Piece";
import {LoadSave} from "../util/LoadSave";



enum FUEStates
{
    NONE = 0,
    MATCH_COLOR_AND_SIZE,
    HIGHLIGHT_MATCHES,
    DONT_RUN_OUT_OF_MOVES,
    CHECK_OUT_THESE_BOOSTS,
    CHOOSE_THE_BOOST,
    CHOOSE_WHICH_DOLL_TO_BOOST,
    WAIT_FOR_LEVEL_UP,
    LEVEL_UP_AWARD,
    MOVES_UP_AWARD,
    SCORE_POINTS_TO_LEVEL_UP,
    EXIT_FUE
};


class Skrim extends PIXI.Graphics
{
    public _onClick: Function;
}


function spriteToFront(_sprite: PIXI.Sprite):void
{
    if (_sprite.parent)
    {
        var parent = _sprite.parent; 
        parent.removeChild(_sprite);
        parent.addChild(_sprite);
    }
}


export class FUE extends PIXI.Container
{
    public static inProgress: boolean;

    private _parent: PIXI.Container;
    private _emitter: PIXI.utils.EventEmitter;
    private _playArea: PlayArea;

    private state: FUEStates;
    private lastState: FUEStates;

    private _bubbleSmall: BubbleSprite;
    private _bubbleLarge: BubbleSprite;
    private _arrowDown: ArrowAnim;
    private _arrowDownAnim: Array<PIXI.Texture>;
    private _arrowUp1: PIXI.Sprite;
    private _arrowUp2: PIXI.Sprite;
    private _arrowRight: PIXI.Sprite;

    private _skrim: Skrim;
    private _skrimHoles: Array<PIXI.Rectangle>;

    private _bubbleText: PIXI.Text;
    private _bubbleTextStyle: PIXI.TextStyle;

    private _squareAttached: BoardSquare;
    private _whereAttached: PIXI.Point;
    private _dollPulse: Piece;
    private _squarePulse: BoardSquare;
    private _glowSquares: Array<BoardLocation>;

    private _timer: number;
    private _resizeCallback: Function;
    private _landscape: boolean;


    private static arrowDownAnimNames: Array<string> =
    [
        "gameplay/arrows/arrow_animation/arrow0020", "gameplay/arrows/arrow_animation/arrow0021", "gameplay/arrows/arrow_animation/arrow0022", "gameplay/arrows/arrow_animation/arrow0023", "gameplay/arrows/arrow_animation/arrow0024",
        "gameplay/arrows/arrow_animation/arrow0025", "gameplay/arrows/arrow_animation/arrow0026", "gameplay/arrows/arrow_animation/arrow0027", "gameplay/arrows/arrow_animation/arrow0028", "gameplay/arrows/arrow_animation/arrow0029",
        "gameplay/arrows/arrow_animation/arrow0030", "gameplay/arrows/arrow_animation/arrow0031", "gameplay/arrows/arrow_animation/arrow0032", "gameplay/arrows/arrow_animation/arrow0033", "gameplay/arrows/arrow_animation/arrow0034"
    ];



    constructor(_parent: PIXI.Container, _emitter: PIXI.utils.EventEmitter, _playArea: PlayArea)
    {
        super();
        this._parent = _parent;
        this._emitter = _emitter;
        this._playArea = _playArea;

        this.state = FUEStates.MATCH_COLOR_AND_SIZE;
        this.lastState = FUEStates.NONE;
        PlayArea.GetContainer().addChild(this);

        this._timer = 0;
        this._resizeCallback = null;
        this._landscape = true;

        // small devices appear to scale the text differently relative to the speech bubbles
        // we use a smaller font on these to prevent the text overlapping at the edges
        if (PIXI.utils.isMobile.phone)
        {
            this._bubbleTextStyle = new PIXI.TextStyle(
                {
                    fontFamily: "GrilledCheese",
                    fontSize: 36,
                    fontWeight: 'bold',
                    fill: ['#000000'],
                    dropShadow: false,
                    align: 'center',
                    leading: -7
                });
        }
        else
        {
            this._bubbleTextStyle = new PIXI.TextStyle(
                {
                    fontFamily: "GrilledCheese",
                    fontSize: 40,
                    fontWeight: 'bold',
                    fill: ['#000000'],
                    dropShadow: false,
                    align: 'center',
                    leading: -10
                });
        }

        this._arrowDownAnim = new Array<PIXI.Texture>();
        for(let i:number = 0, j:number = 0; j < FUE.arrowDownAnimNames.length; i++)
        {
            this._arrowDownAnim.push(PIXI.Texture.fromImage(FUE.arrowDownAnimNames[j]));
            // hold on first frame for a while
            if (i > 10) j++;
        }

        this.resize();
    }


    public static getGlobalScale(_do: PIXI.DisplayObject):PIXI.Point
    {
        let s: PIXI.Point = new PIXI.Point(1, 1);
        let o: PIXI.DisplayObject = _do;
        while(o)
        {
            s.x *= o.scale.x;
            s.y *= o.scale.y;
            o = o.parent;
        }
        return s;
    }

    public static getGlobalPosition(_from: PIXI.DisplayObject, _to: PIXI.DisplayObject):PIXI.Point
    {
        // convert square location to global, then from global to match 'this'
        let gp: PIXI.Point = _from.getGlobalPosition();
        let p: PIXI.Point = _to.getGlobalPosition();
        let scale: PIXI.Point = FUE.getGlobalScale(_to.parent);
        gp.x -= p.x;
        gp.y -= p.y;
        gp.x /= scale.x;
        gp.y /= scale.y;
        return gp;
    }


    private circleStars(x: number, y: number, type: EffectType)
    {
        Effects.add(this, type, x, y, 0);
        let interval: number = 50;
        Effects.addDelayed(interval * 1, this, type, x, y, 0);
        Effects.addDelayed(interval * 2, this, type, x, y, 0);
        Effects.addDelayed(interval * 3, this, type, x, y, 0);
        Effects.addDelayed(interval * 4, this, type, x, y, 0);
        Effects.addDelayed(interval * 5, this, type, x, y, 0);
        Effects.addDelayed(interval * 6, this, type, x, y, 0);
        Effects.addDelayed(interval * 7, this, type, x, y, 0);
        Effects.addDelayed(interval * 8, this, type, x, y, 0);
        Effects.addDelayed(interval * 9, this, type, x, y, 0);
        Effects.addDelayed(interval * 10, this, type, x, y, 0);
    }


    // return false when this class object should be destroyed
    public update():boolean
    {
        let _that: FUE = this;

        let pulse: number = 0;

        let repeat: boolean;
        do
        {
            let newState: boolean = (this.state != this.lastState);
            this.lastState = this.state;

            repeat = false;
            switch(this.state)
            {
                case FUEStates.MATCH_COLOR_AND_SIZE:
                    if (newState)
                    {
                        FUE.inProgress = true;

                        this._resizeCallback = function()
                        {
                            this._dollPulse = PlayArea.GetPiece(new PIXI.Point(1, 1));
                            this._squarePulse = PlayArea.GetBoardSquare(new PIXI.Point(1, 3));
                            this._squareAttached = PlayArea.GetBoardSquare(new PIXI.Point(1, 1));
                            // convert square location to global, then from global to match 'this'
                            this._whereAttached = FUE.getGlobalPosition(this._squareAttached, this);
                            this._bubbleSmall.x = this._whereAttached.x + 100;
                            this._bubbleSmall.y = this._whereAttached.y + 20;
                            this._arrowDown.x = this._whereAttached.x + 66;
                            this._arrowDown.y = this._whereAttached.y + 40;
                        }

                        // add blanker behind board covering all UI to force user to make a move first
                        GameUi.blankerClickAllowed = false;
                        PlayArea.getUi().showBoostBlanker(true);

                        // add speech bubble
                        this._bubbleSmall = new BubbleSprite(PIXI.Texture.fromImage("Bubble_01"));
                        this._bubbleSmall.anchor.set(0.3, 1.0);
                        this._bubbleSmall.scale.set(this.scale.x, this.scale.y);
                        this._bubbleSmall._onTop = true;
                        this._bubbleText = new PIXI.Text("Match color\nand size!", this._bubbleTextStyle);
                        this._bubbleText.x = 60;
                        this._bubbleText.y = -this._bubbleSmall.height + 20;
                        this._bubbleText.anchor.set(0.5, 0.0);

                        // add arrow
                        this._arrowDown = new ArrowAnim(this._arrowDownAnim);
                        this._arrowDown.anchor.set(0.5, 1.0);
                        this._arrowDown.scale.set(0.80);
                        this._arrowDown._onTop = true;
                        this._arrowDown.animationSpeed = 0.4;
                        this._arrowDown.play();

                        // create PIXI hierarchy
                        this._bubbleSmall.addChild(this._bubbleText);
                        this.addChild(this._arrowDown);
                        this.addChild(this._bubbleSmall);

                        // event listener for player making a move
                        let nextState: Function = function()
                        {
                            // after end-of-move signal, clean up and advance to next state
                            if (_that._bubbleText)
                            {
                                _that._arrowDown.destroy();
                                _that._bubbleText.destroy();
                                _that._bubbleText = null;
                                _that._bubbleSmall.destroy();
                                _that._dollPulse.filters = null;
                                _that._squarePulse.filters = null;
                                _that._emitter.off("piece_moved", nextState);
                                _that.state = FUEStates.HIGHLIGHT_MATCHES;
                            }
                            _that._resizeCallback = null;
                        };
                        this._emitter.on("piece_moved", nextState);

                        this._resizeCallback();
                    }
                    // move arrow from 2,2 to 2,4
                    if (this._arrowDown.currentFrame < 10)
                        this._arrowDown.y = this._arrowDown.currentFrame * 10 + this._whereAttached.y + 220;
                    //else
                        //this._arrowDown.y = (this._arrowDown.currentFrame - 10) * 1 + this._whereAttached.y + 220 + 10 * 8;
                    pulse = (Game._now % 1000) > 500 ? 8:2;
                    // pulse doll at 2,2
                    this._dollPulse.filters = [ new GlowFilter(pulse, 8,8, 0xafafaf, 1) ];
                    // pulse square at 2,4
                    this._squarePulse.filters = [ new GlowFilter(pulse, 8,8, 0xafafaf, 1) ];
                    break;

                case FUEStates.HIGHLIGHT_MATCHES:
                    // for all matches on the board, add glow effects to the squares involved
                    if (newState)
                    {
                        PlayArea.getUi().showBoostBlanker(false);
                        GameUi.blankerClickAllowed = true;

                        this._glowSquares = new Array<BoardLocation>();
                        let addGlowSquares: Function = function(score: number, list: Array<BoardLocation>)
                        {
                            _that._glowSquares = _that._glowSquares.concat(list);
                        }
                        this._emitter.on("match_score_add", addGlowSquares);

                        let removeGlow: Function = function()
                        {
                            // after end-of-merge signal, remove glows
                            for(let i: number = 0; i < _that._glowSquares.length; i++)
                            {
                                _that._glowSquares[i]._square.filters = null;
                            }
                            _that._glowSquares = null;
                            _that._emitter.off("match_score_add", addGlowSquares);
                            _that._emitter.off("merge_end", removeGlow);
                        }
                        this._emitter.on("merge_end", removeGlow);

                        let nextState: Function = function(enable)
                        {
                            // switch state when board has entirely finished updating and we're ready for the player's next move
                            if (enable)
                            {
                                _that._emitter.off("drag_enable", nextState);
                                _that.state = FUEStates.DONT_RUN_OUT_OF_MOVES;
                            }
                        }
                        this._emitter.on("drag_enable", nextState);
                    }
                    if (this._glowSquares)
                    {
                        // pulse glow effects
                        pulse = (Game._now % 500) > 250 ? 8:2;
                        for(let i: number = 0; i < this._glowSquares.length; i++)
                        {
                            this._glowSquares[i]._square.filters = [ new GlowFilter(pulse, 8,8, 0xafafaf, 1) ];
                        }
                    }
                    break;

                case FUEStates.DONT_RUN_OUT_OF_MOVES:
                    if (newState)
                    {
                        let moves: PIXI.Point;
                        let arrowTween1: TWEEN.Tween;
                        let arrowTween2: TWEEN.Tween;
                        let arrowTween3: TWEEN.Tween;
                        let arrowScale: TWEEN.Tween;

                        this._resizeCallback = function()
                        {
                            if (this._landscape)
                            {
                                moves = FUE.getGlobalPosition(PlayArea.GetBoardSquare(new PIXI.Point(4, 0)), this);
                            }
                            else
                            {
                                moves = FUE.getGlobalPosition(PlayArea.GetContainer(), this);
                                moves.x -= 78;
                                moves.y = (-GameSettings.height / 2) / FUE.getGlobalScale(this).y + 100;
                            }

                            this._arrowRight.x = moves.x - 200 + 189;
                            this._arrowRight.y = moves.y - 25;
                            arrowTween1 = new TWEEN.Tween(this._arrowRight);
                            arrowTween1.to({x: moves.x + 189 }, 750);
                            arrowTween1.easing(TWEEN.Easing.Quadratic.InOut);
                            arrowScale = new TWEEN.Tween(this._arrowRight.scale);
                            arrowScale.to({x: -0.75, y: 0.75}, 650);
                            arrowTween2 = new TWEEN.Tween(this._arrowRight);
                            arrowTween2.to({x: moves.x + 189 - 100}, 400);
                            arrowTween2.easing(TWEEN.Easing.Quadratic.InOut);
                            arrowTween3 = new TWEEN.Tween(this._arrowRight);
                            arrowTween3.to({x: moves.x + 189}, 500);
                            arrowTween3.easing(TWEEN.Easing.Quadratic.InOut);

                            TWEEN.removeAll();
                            arrowTween1.chain(arrowTween2);
                            arrowTween2.chain(arrowTween3);
                            arrowTween1.start();
                            arrowScale.start();

                            Effects.killAll();
                            this.circleStars(moves.x + 189 + 80, moves.y - 100 + 58, EffectType.CircleStar);

                            // add skrim to whole board except doll we just moved to 2,4 and the moves counter display
                            this._squareAttached = PlayArea.GetBoardSquare(new PIXI.Point(1, 3));
                            this._whereAttached = FUE.getGlobalPosition(this._squareAttached, this);

                            this.createSkrim(this, [ new PIXI.Rectangle(this._whereAttached.x - 22, this._whereAttached.y - 25, GameSettings.BoardSquareWide, GameSettings.BoardSquareHigh),
                                                     new PIXI.Rectangle(moves.x + 198, moves.y - 112, GameSettings.BoardSquareWide * 0.85, GameSettings.BoardSquareHigh * 0.85) ],
                                                     nextState);
                            this._bubbleSmall.x = this._whereAttached.x + 110;
                            this._bubbleSmall.y = this._whereAttached.y + 10;
                        }

                        let nextState = function()
                        {
                            if (_that._bubbleText)
                            {
                                TWEEN.removeAll();
                                arrowTween1 = null;
                                arrowTween2 = null;
                                arrowScale = null;
                                Effects.killAll();
                                _that._skrim.destroy();
                                _that._skrim = null;
                                _that._skrimHoles = null;
                                repeat = true;    // instantly process next state to avoid skrim-flicker
                                _that._arrowRight.destroy();
                                _that._bubbleText.destroy();
                                _that._bubbleText = null;
                                _that._bubbleSmall.destroy();
                                _that._emitter.off("drag_start", nextState);
                                _that._emitter.off("boost_select", nextState);
                                _that.state = FUEStates.CHECK_OUT_THESE_BOOSTS;
                            }
                            this._resizeCallback = null;
                        };

                        // add speech bubble from doll
                        this._bubbleSmall = new BubbleSprite(PIXI.Texture.fromImage("Bubble_01"));
                        this._bubbleSmall.anchor.set(0.3, 1.0);
                        this._bubbleSmall.scale.set(this.scale.x, this.scale.y);
                        this._bubbleSmall._onTop = true;
                        this._bubbleText = new PIXI.Text("Don't run\nout of moves!", this._bubbleTextStyle);
                        this._bubbleText.x = 57;
                        this._bubbleText.y = -this._bubbleSmall.height + 20;
                        this._bubbleText.anchor.set(0.5, 0.0);

                        // add right facing arrow pointing at moves counter
                        this._arrowRight = new BubbleSprite(PIXI.Texture.fromImage("gameplay/arrows/arrow03"));
                        this._arrowRight.anchor.set(0, 0.5);
                        this._arrowRight.scale.set(-0.25, 0.25);

                        // create PIXI hierarchy
                        this._bubbleSmall.addChild(this._bubbleText);

                        this._resizeCallback();

                        this.addChild(this._arrowRight);
                        this.addChild(this._bubbleSmall);

                        // wait for any player input, advance to next state
                        this._emitter.on("drag_start", nextState);
                        this._emitter.on("boost_select", nextState);
                    }
                    break;

                case FUEStates.CHECK_OUT_THESE_BOOSTS:
                    if (newState)
                    {
                        this._resizeCallback = function()
                        {
                            let p: PIXI.Point;
                            if (this._landscape)
                            {
                                this._squareAttached = PlayArea.GetBoardSquare(new PIXI.Point(4, 2));
                                p = FUE.getGlobalPosition(this._squareAttached, this);
                                // add skrim to whole game except the boosts bar
                                let boostBar = FUE.getGlobalPosition(PlayArea.GetBoardSquare(new PIXI.Point(4, 0)), this);
                                this.createSkrim(this, [ new PIXI.Rectangle(boostBar.x + 132, boostBar.y + 25, GameSettings.BoardSquareWide * 1.75, GameSettings.BoardSquareHigh * 3.75 - 45) ],
                                    nextState2);
                            }
                            else
                            {
                                // add skrim to whole game except the boosts bar
                                p = FUE.getGlobalPosition(PlayArea.GetContainer(), this);
                                p.x -= 78;
                                p.y = (GameSettings.height / 2) / FUE.getGlobalScale(this).y - 280;
                                //let boostBar = FUE.getGlobalPosition(PlayArea.GetBoardSquare(new PIXI.Point(2, 4)), this);
                                this.createSkrim(this, [ new PIXI.Rectangle(p.x - 180, p.y + 95, GameSettings.BoardSquareWide * 2.80, GameSettings.BoardSquareHigh * 1.25) ],
                                    nextState2);
                                this._squareAttached = PlayArea.GetBoardSquare(new PIXI.Point(2, 4));
                            }

                            // add speech bubble from top boost to left
                            this._whereAttached = p;
                            this._bubbleLarge.x = this._whereAttached.x + 70;
                            this._bubbleLarge.y = this._whereAttached.y + 120;
                        }

                        let cleanUp = function()
                        {
                            if (_that._bubbleText)
                                _that._bubbleText.destroy();
                            if (_that._skrim)
                                _that._skrim.destroy();
                            _that._skrim = null;
                            _that._skrimHoles = null;
                            repeat = true;    // instantly process next state to avoid skrim-flicker
                            _that._bubbleText = null;
                            _that._bubbleLarge.destroy();
                            _that._emitter.off("drag_start", nextState2);
                            _that._emitter.off("boost_select", nextState2_5);
                            _that._resizeCallback = null;
                        }
                        // wait for any player input, advance to next state
                        let nextState2 = function()
                        {
                            cleanUp();
                            _that.state = FUEStates.CHOOSE_THE_BOOST;
                        };
                        let nextState2_5 = function()
                        {
                            cleanUp();
                            if (_that._skrim)
                            {
                                _that._skrim.destroy();
                            }
                            _that._skrim = null;
                            _that._skrimHoles = null;
                            _that.state = FUEStates.CHOOSE_WHICH_DOLL_TO_BOOST;
                        };

                        // add skrim to whole game except the boosts bar
                        let boostBar = FUE.getGlobalPosition(PlayArea.GetBoardSquare(new PIXI.Point(4, 0)), this);
                        this.createSkrim(this, [ new PIXI.Rectangle(boostBar.x + 132, boostBar.y + 25, GameSettings.BoardSquareWide * 1.75, GameSettings.BoardSquareHigh * 3.75 - 45) ],
                            nextState2);

                        // add speech bubble
                        this._bubbleLarge = new BubbleSprite(PIXI.Texture.fromImage("Bubble_02"));
                        this._bubbleLarge.anchor.set(0.7, 1.0);
                        this._bubbleLarge.scale.set(this.scale.x, this.scale.y);
                        this._bubbleLarge._onTop = true;
                        this._bubbleText = new PIXI.Text("Check out these\nboosts!\nUse them to increase\nyour score!", this._bubbleTextStyle);
                        this._bubbleText.x = -84;
                        this._bubbleText.y = -this._bubbleLarge.height + 20;
                        this._bubbleText.anchor.set(0.5, 0.0);                    

                        this._resizeCallback();

                        // create PIXI hierarchy
                        this._bubbleLarge.addChild(this._bubbleText);
                        this.addChild(this._bubbleLarge);

                        // wait for any player input, advance to next state
                        this._emitter.on("drag_start", nextState2);
                        this._emitter.on("boost_select", nextState2_5);
                    }
                    break;

                case FUEStates.CHOOSE_THE_BOOST:
                    if (newState)
                    {
                        let arrowTween1: TWEEN.Tween;
                        let arrowTween2: TWEEN.Tween;
                        let arrowTween3: TWEEN.Tween;
                        let arrowScale: TWEEN.Tween;

                        this._resizeCallback = function()
                        {
                            let p: PIXI.Point;
                            if (this._landscape)
                            {
                                this._squareAttached = PlayArea.GetBoardSquare(new PIXI.Point(4, 2));
                                p = FUE.getGlobalPosition(this._squareAttached, this);
                                this._whereAttached = p;

                                this._bubbleLarge.x = this._whereAttached.x + 70;
                                this._bubbleLarge.y = this._whereAttached.y + 120;
                                this._arrowRight.x = this._whereAttached.x - 200 + 130;
                                this._arrowRight.y = this._whereAttached.y + 50;
                                this._arrowRight.visible = true;
                                // add skrim to whole game except the boosts bar
                                let boostBar = FUE.getGlobalPosition(PlayArea.GetBoardSquare(new PIXI.Point(4, 0)), this);
                                this.createSkrim(this, [ new PIXI.Rectangle(boostBar.x + 132, boostBar.y + 25, GameSettings.BoardSquareWide * 1.75, GameSettings.BoardSquareHigh * 3.75 - 45) ],
                                    nextState3_5);
                            }
                            else
                            {
                                // add skrim to whole game except the boosts bar
                                p = FUE.getGlobalPosition(PlayArea.GetContainer(), this);
                                p.x -= 78;
                                p.y = (GameSettings.height / 2) / FUE.getGlobalScale(this).y - 280;
                                //let boostBar = FUE.getGlobalPosition(PlayArea.GetBoardSquare(new PIXI.Point(2, 4)), this);
                                this.createSkrim(this, [ new PIXI.Rectangle(p.x - 180, p.y + 95, GameSettings.BoardSquareWide * 2.80, GameSettings.BoardSquareHigh * 1.25) ],
                                    nextState3_5);
                                this._squareAttached = PlayArea.GetBoardSquare(new PIXI.Point(2, 4));
                                this._whereAttached = p;

                                this._bubbleLarge.x = this._whereAttached.x + 70;
                                this._bubbleLarge.y = this._whereAttached.y + 120;
                                this._arrowRight.visible = false;
                            }

                            // add speech bubble from top boost to left, slightly higher than last one
                            arrowTween1 = new TWEEN.Tween(this._arrowRight);
                            arrowTween1.to({x: this._whereAttached.x + 130 }, 750);
                            arrowTween1.easing(TWEEN.Easing.Quadratic.InOut);
                            arrowScale = new TWEEN.Tween(this._arrowRight.scale);
                            arrowScale.to({x: -0.75, y: 0.75}, 650);
                            arrowTween2 = new TWEEN.Tween(this._arrowRight);
                            arrowTween2.to({x: this._whereAttached.x + 130 - 100}, 400);
                            arrowTween2.easing(TWEEN.Easing.Quadratic.InOut);
                            arrowTween3 = new TWEEN.Tween(this._arrowRight);
                            arrowTween3.to({x: this._whereAttached.x + 130}, 500);
                            arrowTween3.easing(TWEEN.Easing.Quadratic.InOut);

                            TWEEN.removeAll();
                            arrowTween1.chain(arrowTween2);
                            arrowTween2.chain(arrowTween3);
                            arrowTween1.start();
                            arrowScale.start();
                        }

                        let cleanUp = function()
                        {
                            TWEEN.removeAll();
                            arrowTween1 = null;
                            arrowTween2 = null;
                            arrowScale = null;
                            if (_that._skrim)
                                _that._skrim.destroy();
                            _that._skrim = null;
                            _that._skrimHoles = null;
                            repeat = true;    // instantly process next state to avoid skrim-flicker
                            _that._arrowRight.destroy();
                            _that._bubbleText.destroy();
                            _that._bubbleText = null;
                            _that._bubbleLarge.destroy();
                            _that._emitter.off("boost_select", nextState3);
                            _that._emitter.off("drag_start", nextState3_5);
                            _that._resizeCallback = null;
                        }
                        // wait for signal that player selected a boost type, advance to next state
                        let nextState3 = function()
                        {
                            if (_that._bubbleText)
                            {
                                cleanUp();
                                _that.state = FUEStates.CHOOSE_WHICH_DOLL_TO_BOOST;
                            }
                        };
                        // if player clicked elsewhere, skip next state entirely
                        let nextState3_5 = function()
                        {
                            if (_that._bubbleText)
                            {
                                cleanUp();
                                _that.state = FUEStates.WAIT_FOR_LEVEL_UP;
                            }
                        };

                        // add speech bubble
                        this._bubbleLarge = new BubbleSprite(PIXI.Texture.fromImage("Bubble_02"));
                        this._bubbleLarge.anchor.set(0.7, 1.0);
                        this._bubbleLarge.scale.set(this.scale.x, this.scale.y);
                        this._bubbleLarge._onTop = true;
                        this._bubbleText = new PIXI.Text("Choose the boost\nthat you want\nto use!", this._bubbleTextStyle);
                        this._bubbleText.x = -84;
                        this._bubbleText.y = -this._bubbleLarge.height + 40;
                        this._bubbleText.anchor.set(0.5, 0.0);                    

                        // add right facing arrow pointing at a boost
                        this._arrowRight = new BubbleSprite(PIXI.Texture.fromImage("gameplay/arrows/arrow03"));
                        this._arrowRight.anchor.set(0, 0.5);
                        this._arrowRight.scale.set(-0.25, 0.25);

                        this._resizeCallback();

                        // create PIXI hierarchy
                        this.addChild(this._arrowRight);
                        this._bubbleLarge.addChild(this._bubbleText);
                        this.addChild(this._bubbleLarge);

                        this._emitter.on("boost_select", nextState3);
                        this._emitter.on("drag_start", nextState3_5);
                    }
                    break;

                case FUEStates.CHOOSE_WHICH_DOLL_TO_BOOST:
                    if (newState)
                    {
                        this._resizeCallback = function()
                        {
                            this._squareAttached = PlayArea.GetBoardSquare(new PIXI.Point(4, 3));
                            this._whereAttached = FUE.getGlobalPosition(this._squareAttached, this);
                            this._bubbleSmall.x = this._whereAttached.x + 110;
                            this._bubbleSmall.y = this._whereAttached.y + 40;
                        }

                        // add blanker behind board covering all UI except selected boost button
                        PlayArea.getUi().showBoostBlanker(true);

                        // add speech bubble to square at 5,2 stem to left, top right corner

                        // add speech bubble from doll
                        this._bubbleSmall = new BubbleSprite(PIXI.Texture.fromImage("Bubble_01"));
                        this._bubbleSmall.anchor.set(0.3, 1.0);
                        this._bubbleSmall.scale.set(this.scale.x, this.scale.y);
                        this._bubbleSmall._onTop = true;
                        this._bubbleText = new PIXI.Text("Choose a doll\nto use the\nboost on!", this._bubbleTextStyle);
                        this._bubbleText.x = 55;
                        this._bubbleText.y = -this._bubbleSmall.height + 0;
                        this._bubbleText.anchor.set(0.5, 0.0);

                        // create PIXI hierarchy
                        this._bubbleSmall.addChild(this._bubbleText);
                        this.addChild(this._bubbleSmall);

                        this._resizeCallback();

                        // wait for signal that player selected a boost type, advance to next state
                        let nextState4 = function()
                        {
                            if (_that._bubbleText)
                            {
                                _that._bubbleText.destroy();
                                _that._bubbleText = null;
                                _that._bubbleSmall.destroy();
                                _that._emitter.off("board_clicked_for_boost", nextState4);
                                _that._emitter.off("cancel_boost", nextState4);
                                _that._emitter.off("boost_select", nextState4);
                                _that.state = FUEStates.WAIT_FOR_LEVEL_UP;
                            }
                            this._resizeCallback = null;
                        };
                        // wait for signal that boost was used, or that boost was cancelled
                        this._emitter.on("board_clicked_for_boost", nextState4);
                        this._emitter.on("cancel_boost", nextState4);
                        this._emitter.on("boost_select", nextState4);    // sent by GameUi blanker removal
                    }
                    break;

                case FUEStates.WAIT_FOR_LEVEL_UP:
                    if (newState)
                    {
                        let nextState: Function = function(level, moves, nextLevelScoreGoal):void
                        {
                            _that._emitter.off("level_changed", nextState);
                            _that.state = FUEStates.LEVEL_UP_AWARD;
                        };
                        // wait for signal level-up
                        this._emitter.on("level_changed", nextState);
                    }
                    break;

                case FUEStates.LEVEL_UP_AWARD:
                    if (newState)
                    {
                        this._resizeCallback = function()
                        {
                            if (this._landscape)
                            {
                                // add blanker for everything except the xp bar
                                let xpBar: PIXI.Point;
                                xpBar = FUE.getGlobalPosition(PlayArea.GetBoardSquare(new PIXI.Point(1, 0)), this);
                                this.createSkrim(this, [ new PIXI.Rectangle(xpBar.x - 58, xpBar.y - 110, GameSettings.BoardSquareWide * 1.43, GameSettings.BoardSquareHigh * 0.60) ]);
                                Effects.killAll();
                                this.circleStars(xpBar.x + 60, xpBar.y - 60, EffectType.EllipseStar);
                            }
                            else
                            {
                                let moves: PIXI.Point = FUE.getGlobalPosition(PlayArea.GetContainer(), this);
                                moves.x -= 84;
                                moves.y = (-GameSettings.height / 2) / FUE.getGlobalScale(this).y + 50;

                                // add blanker for everything except the xp bar
                                let xpBar: PIXI.Point;
                                xpBar = FUE.getGlobalPosition(PlayArea.GetBoardSquare(new PIXI.Point(1, 0)), this);
                                this.createSkrim(this, [ new PIXI.Rectangle(moves.x - 124, moves.y - 50, GameSettings.BoardSquareWide * 1.43, GameSettings.BoardSquareHigh * 0.60) ]);
                                Effects.killAll();
                                this.circleStars(moves.x, moves.y, EffectType.EllipseStar);
                            }
                        }
                        this._timer = 2000;
                        this._resizeCallback();
                    }

                    // wait for a little while
                    this._timer -= Game._elapsed;
                    if (this._timer <= 0)
                    {
                        Effects.killAll();
                        this._skrim.destroy();
                        this._skrim = null;
                        this._skrimHoles = null;
                        repeat = true;    // instantly process next state to avoid skrim-flicker
                        this.state = FUEStates.MOVES_UP_AWARD;
                        this._resizeCallback = null;
                    }
                    
                    break;

                case FUEStates.MOVES_UP_AWARD:
                    if (newState)
                    {
                        let moves: PIXI.Point;
                        this._resizeCallback = function()
                        {
                            if (this._landscape)
                            {
                                moves = FUE.getGlobalPosition(PlayArea.GetBoardSquare(new PIXI.Point(4, 0)), this);
                            }
                            else
                            {
                                moves = FUE.getGlobalPosition(PlayArea.GetContainer(), this);
                                moves.x -= 78;
                                moves.y = (-GameSettings.height / 2) / FUE.getGlobalScale(this).y + 100;
                            }

                            // add blanker for everything except moves remaining
                            this.createSkrim(this, [ new PIXI.Rectangle(moves.x + 198, moves.y - 112, GameSettings.BoardSquareWide * 0.85, GameSettings.BoardSquareHigh * 0.85) ]);
                            Effects.killAll();
                            this.circleStars(moves.x + 189 + 80, moves.y - 100 + 60, EffectType.CircleStar);
                        }

                        this._timer = 2000;
                        this._resizeCallback();
                    }

                    // wait for a little while
                    this._timer -= Game._elapsed;
                    if (this._timer <= 0)
                    {
                        Effects.killAll();
                        this._skrim.destroy();
                        this._skrim = null;
                        this._skrimHoles = null;
                        repeat = true;    // instantly process next state to avoid skrim-flicker
                        this.state = FUEStates.SCORE_POINTS_TO_LEVEL_UP;
                        this._resizeCallback = null;
                    }
                    break;

                case FUEStates.SCORE_POINTS_TO_LEVEL_UP:
                    let arrowy: number;
                    if (newState)
                    {
                        let moves: PIXI.Point;
                        this._resizeCallback = function()
                        {
                            // add speech bubble to doll
                            this._squareAttached = PlayArea.GetBoardSquare(new PIXI.Point(2, 3));
                            this._whereAttached = FUE.getGlobalPosition(this._squareAttached, this);

                            if (this._landscape)
                            {
                                moves = FUE.getGlobalPosition(PlayArea.GetBoardSquare(new PIXI.Point(4, 0)), this);
                                // add blanker for everything except level number AND moves remaining AND doll
                                let xpBar = FUE.getGlobalPosition(PlayArea.GetBoardSquare(new PIXI.Point(1, 0)), this);
                                this.createSkrim(this, [ new PIXI.Rectangle(xpBar.x - 60, xpBar.y - 106, 83, 87),
                                                         new PIXI.Rectangle(moves.x + 198, moves.y - 112, GameSettings.BoardSquareWide * 0.85, GameSettings.BoardSquareHigh * 0.85),
                                                         new PIXI.Rectangle(this._whereAttached.x - 22, this._whereAttached.y - 25, GameSettings.BoardSquareWide, GameSettings.BoardSquareHigh) ],
                                                         nextState5);
                            }
                            else
                            {
                                moves = FUE.getGlobalPosition(PlayArea.GetContainer(), this);
                                moves.x -= 78;
                                moves.y = (-GameSettings.height / 2) / FUE.getGlobalScale(this).y + 100;
                                // add blanker for everything except level number AND moves remaining AND doll
                                this.createSkrim(this, [ new PIXI.Rectangle(moves.x - 170, moves.y - 90, 83, 87),
                                                         new PIXI.Rectangle(moves.x + 198, moves.y - 112, GameSettings.BoardSquareWide * 0.85, GameSettings.BoardSquareHigh * 0.85),
                                                         new PIXI.Rectangle(this._whereAttached.x - 22, this._whereAttached.y - 25, GameSettings.BoardSquareWide, GameSettings.BoardSquareHigh) ],
                                                         nextState5);
                            }

                            this._bubbleSmall.x = this._whereAttached.x + 100;
                            this._bubbleSmall.y = this._whereAttached.y + 20;
                            // position the arrows
                            this._squareAttached = PlayArea.GetBoardSquare(new PIXI.Point(1, 0));
                            this._whereAttached = FUE.getGlobalPosition(this._squareAttached, this);
                            if (this._landscape)
                            {
                                this._arrowUp2.x = this._whereAttached.x + 644;
                            }
                            else
                            {
                                this._arrowUp2.x = this._whereAttached.x + 390;
                                this._whereAttached.y = moves.y + 100;
                            }
                            this._arrowUp1.x = this._whereAttached.x - 20;
                        }

                        // wait for any player input, advance to next state
                        let nextState5 = function()
                        {
                            if (_that._bubbleText)
                            {
                                _that._skrim.destroy();
                                _that._skrim = null;
                                _that._skrimHoles = null;
                                repeat = true;    // instantly process next state to avoid skrim-flicker
                                _that._arrowUp1.destroy();
                                _that._arrowUp2.destroy();
                                _that._bubbleText.destroy();
                                _that._bubbleText = null;
                                _that._bubbleSmall.destroy();
                                _that._emitter.off("drag_start", nextState5);
                                _that._emitter.off("boost_select", nextState5);
                                _that.state = FUEStates.EXIT_FUE;
                            }
                            _that._resizeCallback = null;
                        };


                        this._bubbleSmall = new BubbleSprite(PIXI.Texture.fromImage("Bubble_01"));
                        this._bubbleSmall.anchor.set(0.3, 1.0);
                        this._bubbleSmall.scale.set(this.scale.x * 1.35, this.scale.y * 1.35);
                        this._bubbleSmall._onTop = true;
                        this._bubbleText = new PIXI.Text("Score points to fill\nthe level bar and\nget more moves!", this._bubbleTextStyle);
                        this._bubbleText.x = 55;
                        this._bubbleText.y = -this._bubbleSmall.texture.height + 15;
                        this._bubbleText.scale.set(1.0 / 1.35);
                        this._bubbleText.anchor.set(0.5, 0.0);

                        // add up arrows underneath xp bar and moves remaining counter
                        this._arrowUp1 = new PIXI.Sprite(PIXI.Texture.fromImage("gameplay/arrows/arrow01"));
                        this._arrowUp1.scale.set(0.5, -0.5);
                        this._arrowUp1.anchor.set(0.5, 0);
                        this._arrowUp2 = new PIXI.Sprite(PIXI.Texture.fromImage("gameplay/arrows/arrow01"));
                        this._arrowUp2.scale.set(0.5, -0.5);
                        this._arrowUp2.anchor.set(0.5, 0);

                        this._resizeCallback();

                        // create PIXI hierarchy
                        this._bubbleSmall.addChild(this._bubbleText);
                        this.addChild(this._bubbleSmall);
                        this.addChild(this._arrowUp1);
                        this.addChild(this._arrowUp2);

                        // wait for any player input, advance to next state
                        this._emitter.on("drag_start", nextState5);
                        this._emitter.on("boost_select", nextState5);
                    }

                    // animate arrows up and down
                    pulse = Math.sin(Game._now * 0.01);
                    this._arrowUp1.y = this._whereAttached.y + 100 + pulse * 30;
                    this._arrowUp2.y = this._whereAttached.y + 100 + pulse * 30;

                    break;

                case FUEStates.EXIT_FUE:
                    GameSettings.showFUE = false;
                    LoadSave.save();
                    FUE.inProgress = false;
                    // return false to cause FUE to be destroyed
                    return false;

                case FUEStates.NONE:
                    break;
            }
        }while(repeat);
        return true;
    }


    private createSkrim(parent: any, holes: Array<PIXI.Rectangle>, onClick?: Function):void
    {
        if (this._skrim)
        {
            parent = this._skrim.parent;
            onClick = this._skrim._onClick;
            this._skrim.destroy();
        }

        this._skrim = new Skrim();
        this._skrim._onClick = onClick;
        this._skrimHoles = holes;

        this._skrim.interactive = true;
        this._skrim.buttonMode = false;        

        let s: PIXI.Point = FUE.getGlobalScale(parent);
        let w: number = GameSettings.width / s.x;
        let h: number = GameSettings.height / s.y;
        this._skrim.beginFill(0x000000)
            .drawPolygon([-w/2, -h/2, w/2, -h/2, w/2, h/2, -w/2, h/2]);
        for(let i:number = 0; i < holes.length; i++)
        {
            let hole: PIXI.Rectangle = holes[i];
            // ugly square holes
            // this._skrim.drawPolygon([hole.x, hole.y, hole.x + hole.width, hole.y, hole.x + hole.width, hole.y + hole.height, hole.x, hole.y + hole.height])
            //     .addHole();
            // doesn't work in our version of PIXI
            // this._skrim.drawCircle(hole.x + hole.width / 2, hole.y + hole.height / 2, hole.width)
            //     .addHole();
            this._skrim.drawPolygon(this.createCirclePolygon(hole.x + hole.width / 2, hole.y + hole.height / 2, hole.width, hole.height))
                .addHole();
        }
        this._skrim.alpha = 0.75;
        parent.addChild(this._skrim);

        if (this._skrim._onClick)
        {
            let clicked = function()
            {
                this._skrim.off("mouseup", clicked);
                this._skrim.off("touchend", clicked);
                this._skrim._onClick.bind(this)();
            }
            this._skrim.on("mouseup", clicked.bind(this));
            this._skrim.on("touchend", clicked.bind(this));
        }
    }


    private createCirclePolygon(cx: number, cy: number, w: number, h: number):Array<PIXI.Point>
    {
        let list:Array<PIXI.Point> = new Array<PIXI.Point>();
        for(let a: number = 0; a < 360; a+=10)
        {
            let r: number = a * Math.PI / 180.0;
            list.push(new PIXI.Point(cx + Math.sin(r) * w/2, cy + Math.cos(r) * h/2));
        }
        return list;
    }


    public destroy():void
    {
        this._parent.removeChild(this);
        this._parent = null;
        this._emitter = null;
    }


    public resize():void
    {
        let aspect: number = GameSettings.width / GameSettings.height;
        this._landscape = (aspect >= GameSettings.portraitRatio);

        let scalex: number = GameSettings.width / GameSettings.TotalWidth;
        let scaley: number = GameSettings.height / GameSettings.TotalHeight;
        let scale: number = Math.min(scalex, scaley);
        
        // do resize for the current state and orientation
        if (this._resizeCallback)
            this._resizeCallback.call(this);

        // remake the skrim for the new dimensions
        if (this._skrim) this.createSkrim(this, this._skrimHoles);
        // pop everything back in front of the skrim
        if (this._bubbleSmall) spriteToFront(this._bubbleSmall);
        if (this._bubbleLarge) spriteToFront(this._bubbleLarge);
        if (this._arrowUp1) spriteToFront(this._arrowUp1);
        if (this._arrowUp2) spriteToFront(this._arrowUp2);
        if (this._arrowRight) spriteToFront(this._arrowRight);
    }

}


class ArrowAnim extends PIXI.extras.AnimatedSprite
{
    public _onTop: boolean;

    constructor(textures: Array<PIXI.Texture>)
    {
        super(textures);
    }
}


class BubbleSprite extends PIXI.Sprite
{
    public _onTop: boolean;

    constructor(texture: PIXI.Texture)
    {
        super(texture);
    }
}



