import {FloatingText} from "./FloatingText";
import {Game} from "../Game";



export enum FloatingTextType
{
    Score = 0,
    BigScore,
    HugeScore,
    LAST_ONE
};



export class FloatingTexts
{
    private static readonly _scoreStyle = new PIXI.TextStyle({
            fontFamily: "GrilledCheese",
            fontSize: 36,
            //fontStyle: 'italic',
            fontWeight: 'bold',
            fill: ['#ffffff'],
            stroke: '#000000',
            strokeThickness: 5,
            dropShadow: false,
            dropShadowColor: '#000000',
            dropShadowBlur: 4,
            dropShadowAngle: Math.PI / 6,
            dropShadowDistance: 6,
            wordWrap: false,
            wordWrapWidth: 440
        });
    private static readonly _scoreStyleBig = new PIXI.TextStyle({
            fontFamily: "GrilledCheese",
            fontSize: 44,
            //fontStyle: 'italic',
            fontWeight: 'bold',
            fill: ['#ffffff'],
            stroke: '#000000',
            strokeThickness: 5,
            dropShadow: false,
            dropShadowColor: '#000000',
            dropShadowBlur: 4,
            dropShadowAngle: Math.PI / 6,
            dropShadowDistance: 6,
            wordWrap: false,
            wordWrapWidth: 440
        });
    private static readonly _scoreStyleHuge = new PIXI.TextStyle({
            fontFamily: "GrilledCheese",
            fontSize: 50,
            //fontStyle: 'italic',
            fontWeight: 'bold',
            fill: ['#ffffff'],
            stroke: '#000000',
            strokeThickness: 5,
            dropShadow: false,
            dropShadowColor: '#000000',
            dropShadowBlur: 4,
            dropShadowAngle: Math.PI / 6,
            dropShadowDistance: 6,
            wordWrap: false,
            wordWrapWidth: 440
        });

    public static readonly FloatingTextData: Array<any> =
    [
        // style: PIXI.TextStyle - the text styling
        // alpha: number - the starting alpha value
        // fade: number - delta alpha value
        // rotationMin: number - smallest initial rotation value
        // rotationMax: number or undefined - largest rotation value
        // rotationSpeedMin: number - smallest rotation speed
        // rotationSpeedMax: number or undefined - largest rotation speed
        // rotationDamping: number - amount of damping applied to rotation speed (multiplier)
        // lifeMin: number - shortest life span (ms)
        // lifeMax: number or undefined - longest left span (ms)
        // xMin, yMin, vxMin, vyMin, axMin, ayMin: number - smallest position offset/velocity/acceleration
        // xMax, yMax, vxMax, vyMax, axMax, ayMax: number or undefined - largest position offset/velocity/acceleration
        // frictionx, frictiony: number - amount of friction (multiplier)

        // MUST match FloatingTextTypes order
        {    // Score
            style: FloatingTexts._scoreStyle,
            yMin: -120,
            scalex: 0.5, scaley: 0.5,
            rescalex: 4.0, rescaley: 4.0,
            maxScalex: 1.0, maxScaley: 1.0,
            lifeMin: 600,
        },
        {    // BigScore
            style: FloatingTexts._scoreStyleBig,
            yMin: -120,
            scalex: 0.5, scaley: 0.5,
            rescalex: 3.0, rescaley: 3.0,
            maxScalex: 1.0, maxScaley: 1.0,
            lifeMin: 750,
        },
        {    // HugeScore
            style: FloatingTexts._scoreStyleHuge,
            yMin: -120,
            scalex: 0.5, scaley: 0.5,
            rescalex: 2.0, rescaley: 2.0,
            maxScalex: 1.0, maxScaley: 1.0,
            lifeMin: 900,
        },
    ];


    private static _container: PIXI.Container;
    private static _list: Array<FloatingText>;



    public constructor(container: PIXI.Container)
    {
        // create a duplicate container to the one provided
        FloatingTexts._container = new PIXI.Container();
        FloatingTexts._container.x = container.x;
        FloatingTexts._container.y = container.y;
        FloatingTexts._container.scale.set(container.scale.x, container.scale.y);
        // add it above the one provided
        container.parent.addChild(FloatingTexts._container);
        FloatingTexts._list = new Array<FloatingText>();
    };


    public static destroy():void
    {
        for(let i:number = FloatingTexts._list.length - 1; i >= 0; --i)
        {
            let e: FloatingText = FloatingTexts._list[i];
            e.destroy();
            FloatingTexts._container.removeChild(e);
        }
        FloatingTexts._list = null;
        FloatingTexts._container = null;
    }


    public static add(type: FloatingTextType, text: string, x: number, y: number, z: number): FloatingText
    {
        let e: FloatingText = new FloatingText(FloatingTexts._container);
        e.init(type, text, x, y, z);
        FloatingTexts._list.push(e);
        return e;
    };


    public static addDelayed(delay: number, type: FloatingTextType, text: string, x: number, y: number, z: number): void
    {
        let when:number = Game._now + delay;
        let e: FloatingText = this.add(type, text, x, y, z);
        e.visible = false;
        e._startTime = when;
    };


    public static update(): void
    {
        if (!FloatingTexts._list)
            return;
        
        for(let i:number = FloatingTexts._list.length - 1; i >= 0; --i)
        {
            let e: FloatingText = FloatingTexts._list[i];
            if (e._startTime != 0)
            {
                if (Game._now > e._startTime)
                {
                    e.visible = true;
                    e._startTime = 0;
                }
            }
            if (e.visible && !e.animate())
            {
                e.destroy();
                FloatingTexts._list.splice(i, 1);
            }
        }
    };


    public static delayed(): number
    {
        let c:number = 0;
        for(let i:number = FloatingTexts._list.length - 1; i >= 0; --i)
        {
            let e: FloatingText = FloatingTexts._list[i];
            if (e._startTime != 0)
                c++;
        }
        return c;
    }


};


