import {Game} from "../Game";


export enum CheatKey {
    ADD_SCORE_25 = 0,
    ADD_SCORE_50,
    ADD_SCORE_1000,
    ADD_SCORE_10000,
    ADD_MEGA_MATCH,
    UNLIMITED_MOVES,
    INSTANT_LOSS,
    RESET_PROGRESS,
    UNLOCK_ALL_TROPHIES,
    MOVE_FAKE_20,
    COUNT,
    NEW_GAME_HANDLER
}

export class Cheats {
    
    public static pressedKeys: boolean[] = [];

    public static init (): void {
        Cheats.pressedKeys = [];
        Cheats.resetKeys();

        document.addEventListener("keydown", Cheats.onKeyDown);
    }

    private static onKeyDown (e: KeyboardEvent) {
        let key: string = e.key.toLowerCase();
        switch (key) {
        case "1":
            Cheats.pressedKeys[CheatKey.ADD_SCORE_25] = true;
            break;
        case "2":
            Cheats.pressedKeys[CheatKey.ADD_SCORE_50] = true;
            break;
        case "3":
            Cheats.pressedKeys[CheatKey.ADD_SCORE_1000] = true;
            break;
        case "4":
            Cheats.pressedKeys[CheatKey.ADD_SCORE_10000] = true;
            break;
        case "m":
            Cheats.pressedKeys[CheatKey.ADD_MEGA_MATCH] = true;
            break;
        case "u":
            Cheats.pressedKeys[CheatKey.UNLIMITED_MOVES] = true;
            break;
        case "l":
            Cheats.pressedKeys[CheatKey.INSTANT_LOSS] = true;
            break;
        case "r":
            Cheats.pressedKeys[CheatKey.RESET_PROGRESS] = true;
            break;
        case "t":
            Cheats.pressedKeys[CheatKey.UNLOCK_ALL_TROPHIES] = true;
            break;
        case "f":
            Cheats.pressedKeys[CheatKey.MOVE_FAKE_20] = true;
            break;
        case "n":
            Cheats.pressedKeys[CheatKey.NEW_GAME_HANDLER] = true;
            break;
        }

    }

    public static resetKeys (): void {
        for (let i = 0; i < CheatKey.COUNT; ++i) {
            Cheats.pressedKeys[i] = false;
        }
    }

}



