    // when a boost button is pressed, a blanker darkens the entire UI except for that
    // selected boost button and the play area.  boostContainer is above the blanker so
    // we need to reparent the selected boost to that container to make it bright.
    constructor(topContainer: PIXI.Container, boostContainer: PIXI.Container, emitter: PIXI.utils.EventEmitter)
    {
        super();

        this._topContainer = topContainer;
        this._boostContainer = boostContainer;
        this._emitter = emitter;

        this._scoreLastLevel = 0;
        this._scoreNextLevel = 0;
        this._scoreShowing = -1;
        this._scoreTarget = 0;
        this._levelProgressShowing = -1;
        this._levelProgressTarget = 0;
        this._signIn = null;
        this._pendingTimers = 0;
        this._whichButtonHighlight = -1;

        // if the user clicks a blanker, does it dismiss or ignore the click?
        GameUi.blankerClickAllowed = true;

        // context for closures
        let _that: GameUi = this;

        //
        // update and show UI elements when signals are received
        //

        this._emitter.on("score_changed", function(value, change)
            {
                if (_that._scoreText)
                {
                    // animate visible score value to the new score value
                    _that._scoreTarget = value.toString();
                }
                // animate the level progress meter (values from 0..1)
                _that._levelProgressTarget = (_that._scoreTarget - _that._scoreLastLevel) / (_that._scoreNextLevel - _that._scoreLastLevel);

                if (_that._scoreTarget != _that._scoreShowing)
                {
                    _that.scaleBounce(_that._scoreText, 1.0, 1.5);
                }
            });
        // value = { level:0, nextLevelScoreGoal: 0 }
        this._emitter.on("level_changed", function(level, moves, nextLevelScoreGoal)
            {
                let levelUp = function()
                {
                    _that.scaleBounce(_that._levelNumberText, 1.0, 1.5);
                    if (level % 5 == 0)
                        Audio.playSfx(Sfx.level_up_with_cutecheer);
                    else
                        Audio.playSfx(Sfx.level_up);
                }

                _that._levelProgressTarget = (_that._scoreTarget - _that._scoreLastLevel) / (_that._scoreNextLevel - _that._scoreLastLevel);
                _that._scoreLastLevel = _that._scoreNextLevel;
                _that._scoreNextLevel = nextLevelScoreGoal;
                _that._levelNumberText.text = level.toString();

                // short delay before level up text bounce
                if (level != 1)
                {
                    setTimeout(levelUp, 1000);
                }
            });
        this._emitter.on("moves_changed", function(value, change, immediate)
            {
                let s: string = value.toString();
                // bind function to provide 'this' context
                let movesUp = function(scoreString: string, delayed: boolean = true)
                {
                    // add delay if text == " " which means this is the start of a new game
                    this.scaleBounce(this._movesText, 1.0, 1.5, (this._movesText.text == " " ? 600: 0));
                    this._movesText.text = scoreString;
                    if (change > 0)
                    {
                        if (value > 9)
                        {
                            // numbers turn red when there are less than 10 moves remaining but default to white over that number
                            this._movesText.style.fill = 0xffffff;
                        }

                        this._movesAdded.visible = true;
                        this.scaleBounce(this._movesAdded, 1.0, 1.5, 100, null, function() {
                                _that.scaleBounce(_that._movesAdded, 1.0, 1.5, 100, null, function() {
                                    _that.scaleBounce(_that._movesAdded, 1.0, 1.5, 100, null, function() {
                                        _that.shrinkAway(_that._movesAdded, 250, function() {
                                                _that._movesAdded.visible = false;
                                            })
                                    })
                                })
                            });
                        Audio.playSfx(Sfx.moves_inc);
                    }
                    if (delayed)
                        this._pendingTimers--;
                }

                if (_that._movesText.text != s)
                {
                    if (immediate === true || change < 0)
                    {
                        movesUp.call(_that, s, false);
                    }
                    else
                    {
                        // increase time for each additional pending timer to stop them overlapping incorrectly
                        setTimeout(movesUp.bind(_that, s, true), (change > 0 ? 2000: 500) + _that._pendingTimers * 200);
                        _that._pendingTimers++;
                    }
                }

                if (change < 0)
                {
                    if (value <= 9)
                    {
                        Audio.playSfx(Sfx.nine_moves_left);
                        // numbers turn red when there are less than 10 moves remaining
                        _that._movesText.style.fill = 0xffafaf;
                    }
                    else
                    {
                        Audio.playSfx(Sfx.moves_dec);
                    }
                }
            });
        this._emitter.on("megamatch_changed", function(value, change)
            {
                _that._megaMatchNumberText.text = value.toString();
                if (change != 0)
                {
                    // bounce each letter in the MEGA MATCH text
                    for(let i = 0; i < _that._megaMatchTitleText.length; i++)
                    {
                        _that.scaleBounce(_that._megaMatchTitleText[i], 1.0, 1.5, i * 40);
                    }
                    // bounce the megamatch count number
                    _that.scaleBounce(_that._megaMatchNumberText, 1.0, 1.5, 400);
                }
            });

        //
        // set the number of boosts from signals
        //

        this._emitter.on("show_boost_paint", function(value)
            {
                if (GameSettings.consoleLogging)
                    console.log("boost paint shown " + value);
                _that._boostPaintText.text = value.toString();
                _that._boostPaintTextHighlight.text = value.toString();
            });
        this._emitter.on("show_boost_destroy", function(value)
            {
                if (GameSettings.consoleLogging)
                    console.log("boost destroy shown " + value);
                _that._boostDestroyText.text = value.toString();
                _that._boostDestroyTextHighlight.text = value.toString();
            });
        this._emitter.on("show_boost_grow", function(value)
            {
                if (GameSettings.consoleLogging)
                    console.log("boost grow shown " + value);
                _that._boostGrowText.text = value.toString();
                _that._boostGrowTextHighlight.text = value.toString();
            });

        //
        // react to clicks that happen while a boost is enabled
        // 

        this._emitter.on("board_clicked_for_boost", function(value)
            {
                _that.boostClickedBoard(value);
            });
        this._emitter.on("cancel_boost", function(value)
            {
                _that.cancelBoost(value);
            });

        Tooltips.create(this._topContainer);
        this.createTextStyles();
        this.createLayout();
        this.resize(this.scale.x);
    }


