    private addMegaMatchText(x: number, y: number, mm: string, landscape: boolean):void
    {
        if (!this._megaMatchTitleText)
        {
            this._megaMatchTitleText = new Array<PIXI.Text>(mm.length);
        }

        for(let i = 0; i < mm.length; i++)
        {
            if (!this._megaMatchTitleText[i])
                this._megaMatchTitleText[i] = new PIXI.Text(mm[i], this._megaMatchTitleStyle);
            this._megaMatchTitleText[i].anchor.set(0.5);
            this._megaMatchTitleText[i].x = x + [0,19,36,54, 72 ,82,102,116,132,148][i];
            this._megaMatchTitleText[i].y = y;
            this.addChild(this._megaMatchTitleText[i]);
            if (!landscape && mm[i] == ' ')
            {
                x = 640 - 88;
                y += 25;
            }
        }
    }


