    private delayedCall(callback: Function, delay: number):void
    {
        setTimeout(callback.bind(this), delay);
    }


    private cancelBoost(delay?: number):void
    {
        if (delay == undefined)
            this.removePaintChooser();
        else
            this.delayedCall(this.removePaintChooser, 1000);
        this.removeBoostHighlight();
        this._blanker.visible = false;
        this._boostMode = BoostModes.None;
        this._emitter.emit("boost_select", this._boostMode);
        Audio.playSfx(Sfx.boost_activate);
    }


