    private removeBoostHighlight():void
    {
        // disable highlights
        this._boostPaintHighlight.visible = false;
        this._boostDestroyHighlight.visible = false;
        this._boostGrowHighlight.visible = false;
        this._whichButtonHighlight = -1;
    }


    private highlightBoostButton(whichButton: number):void
    {
        this.removeBoostHighlight();
        this._whichButtonHighlight = whichButton;

        // show the selected button as a highlight
        let sb: SimpleButton = null;
        let hb: PIXI.Sprite = null;
        switch(whichButton)
        {
            case 0:
                sb = this._boostPaintButton;
                hb = this._boostPaintHighlight;
                break;
            case 1:
                sb = this._boostDestroyButton;
                hb = this._boostDestroyHighlight;
                break;
            case 2:
                sb = this._boostGrowButton;
                hb = this._boostGrowHighlight;
                break;
        }
        // set the highlight position to match the original button
        // let p: PIXI.Point = sb.getGlobalPosition();
        hb.anchor.set(0.5);
        hb.x = sb.x;
        hb.y = sb.y;
        hb.visible = true;
    }


    private boostClickedBoard(boardPos: PIXI.Point):void
    {
        switch(this._boostMode)
        {
            case BoostModes.BoostPaint:
                this.choosePaint(boardPos);
                break;
            case BoostModes.BoostDestroy:
                this._emitter.emit("destroy_piece", boardPos);
                break;
            case BoostModes.BoostGrow:
                this._emitter.emit("grow_piece", boardPos);
                break;
        }
    }


    private choosePaint(boardPos: PIXI.Point):void
    {
        this._paintPiece = PlayArea.GetPiece(boardPos);
        if (this._paintPiece != null)
        {
            let ci: number = this._paintPiece._colourIndex;
            // create mini-panel over Piece with two paint colour buttons
            let otherColours: Array<Array<number>> = [[1,2],[0,2],[0,1]];
            this.createPaintChooser(otherColours[ci][0], otherColours[ci][1], ci);
            Audio.playSfx(Sfx.button_press);
            // disable board selection, but leave dragging disabled until colour is picked
            this._emitter.emit("boost_select", null);
        }
    }


    private createPaintChooser(c1: number, c2: number, hide: number):void
    {
        // show two paint cans only (cans are 52x64 on background 160x84)
        this._boostPaintCans[c1].x = -80 / 2;
        this._boostPaintCans[c1].y = 0;
        this._boostPaintCans[c1].visible = true;
        this._boostPaintCans[c2].x = 80 / 2;
        this._boostPaintCans[c2].y = 0;
        this._boostPaintCans[c2].visible = true;
        this._boostPaintCans[hide].visible = false;
        this._boostPaintBackground.visible = true;
        this.setPaintBackgroundPosition();
        this._paintInAction = false;
    }


    private setPaintBackgroundPosition():void
    {
        if (this._paintPiece && this._boostPaintBackground.visible)
        {
            let p: PIXI.Point = this._paintPiece.getGlobalPosition();
            this._boostPaintBackground.scale.set(this._boostContainer.scale.x, this._boostContainer.scale.y);
            this._boostPaintBackground.x = p.x;
            this._boostPaintBackground.y = p.y - GameSettings.BoardSquareHigh * 0.80 * this._boostContainer.scale.y;
        }
    }


    private removePaintChooser():void
    {
        if (this._boostPaintBackground.visible)
        {
            // turn all paint cans the right way up again for next time...
            for(let i:number = 0; i < 3; i++)
                this._boostPaintCans[i].rotation = 0;
            this._boostPaintBackground.visible = false;
        }
    }


    private rotateTo():void
    {
        for(let i:number = this._rotateList.length - 1; i >= 0; --i)
        {
            let o:any = this._rotateList[i];
            o.sprite.rotation += o.turn;
            if (--o.count <= 0)
            {
                this._rotateList.splice(i, 1);
            }
        }
    }

