    private formatBigNumber(value:number):string
    {
        var s = value.toString();
        if ( s.length <= 3 )
            return s;

        for( var i = s.length - 3; i > 0; i -= 3)
            s = s.slice(0, i) + "," + s.slice(i);

        return s;
    };


    private scaleBounce(container: any, revert: number, bounce: number, delay?: number, onUpdate?: any, onComplete?: any):void
    {
        let bounceHalfTime = 100;
        let bounceTween = new TWEEN.Tween(container.scale);
        bounceTween.to({x: bounce, y: bounce}, bounceHalfTime);
        bounceTween.easing(TWEEN.Easing.Quadratic.InOut);

        let overshootTween = new TWEEN.Tween(container.scale);
        overshootTween.to({x: revert * 0.75, y: revert * 0.75}, bounceHalfTime);
        overshootTween.easing(TWEEN.Easing.Quadratic.Out);

        let overshoot2Tween = new TWEEN.Tween(container.scale);
        overshoot2Tween.to({x: revert * 1.1, y: revert * 1.1}, bounceHalfTime);
        overshoot2Tween.easing(TWEEN.Easing.Quadratic.Out);

        let settleTween = new TWEEN.Tween(container.scale);
        settleTween.to({x: revert, y: revert}, bounceHalfTime);
        settleTween.easing(TWEEN.Easing.Quadratic.Out);

        bounceTween.chain(overshootTween);
        overshootTween.chain(overshoot2Tween);
        overshoot2Tween.chain(settleTween);

        if (delay) bounceTween.delay(delay);
        if (onUpdate)
        {
            bounceTween.onUpdate(onUpdate);
            overshootTween.onUpdate(onUpdate);
            overshoot2Tween.onUpdate(onUpdate);
            settleTween.onUpdate(onUpdate);
        }
        if (onComplete)
        {
            settleTween.onComplete(onComplete);
        }
        bounceTween.start();
    }


    private shrinkAway(container: any, duration: number, onComplete?: any):void
    {
        let shrinkTween = new TWEEN.Tween(container.scale);
        shrinkTween.to({x: 0, y: 0}, duration);
        shrinkTween.start();
    }


