                this._boostPanel.x = GameSettings.TotalWidth - GameSettings.SidePanelWidth * this._boostPanel.scale.x;
                this._boostContainer.scale.set(scale * this._boostPanel.scale.x, scale * this._boostPanel.scale.y);
