import {Effects, EffectType} from "./Effects";
import {Game} from "../Game";
import {Audio} from "./Audio";



export class Effect extends PIXI.extras.AnimatedSprite
{
    public _isDone: boolean;
    public _onTop: boolean;
    public _startTime: number;
    public _onVisible: Function;
    public _started: number;
    public _x: number;
    public _y: number;
    public _z: number;            // height above the ground
    public _sx: number;
    public _sy: number;
    private _type: number;
    private _life: number;
    private _fade: number;
    public _vx: number;
    public _vy: number;
    public _vz: number;
    private _ax: number;
    private _ay: number;
    private _az: number;
    public _frictionx: number;
    public _frictiony: number;
    public _frictionz: number;
    private _scalex: number;
    private _scaley: number;
    private _rotationSpeed: number;
    private _rotationDamping: number;
    private _dieOnLoop: boolean;
    private _looped: boolean;
    private _onUpdateData: Function;        // this is included in the Effect data
    private _hueAngle: number;
    private _hueRotSpeed: number;
    private _colorMatrix;

    private _container: PIXI.Container;
    private _onUpdate: Function;            // this might be passed when calling init()



    public constructor(container: PIXI.Container, frames: Array<PIXI.Texture>)
    {
        super(frames);
        this._container = container;
        this._onTop = true;                // default: Effect is always over the rest of its container
    }


    public init(type: number, x: number, y: number, z: number, onUpdate?: Function):void
    {
        this._type = type;
        this._x = x;
        this._y = y;
        this._z = z;
        this._sx = 0;
        this._sy = 0;
        this._onUpdate = onUpdate;
        this._started = 0;

        this._isDone = false;

        // { loop: false, speed: 0, startFrame: 0, rndFrame: 12, rotationMin: -15, rotationMax: 15, lifeMin: 1000, lifeMax: 1000 },
        let data: any = Effects.EffectData[this._type];

        if (data.onUpdate)
            this._onUpdateData = data.onUpdate;

        if (data.onVisible)
            this._onVisible = data.onVisible;

        if (data.loop != undefined)
            this.loop = data.loop;

        if (data.speed != undefined)
            this.animationSpeed = data.speed; else this.animationSpeed = 0.1;

        let startFrame: number = 0;
        if (data.startFrame != undefined)
        {
            if (data.rndFrame != undefined)
                startFrame = Math.floor(Math.random() * data.rndFrame) + data.startFrame;
            else
                startFrame = data.startFrame;
        }
    
        this.rotation = 0;
        if (data.rotationMin != undefined)
        {
            if (data.rotationMax != undefined)
            {
                this.rotation = Math.random() * (data.rotationMax - data.rotationMin) + data.rotationMin;
            }
            else
            {
                this.rotation = data.rotationMin;
            }
        }

        this._rotationSpeed = 0;
        if (data.rotationSpeedMin != undefined)
        {
            if (data.rotationSpeedMax != undefined)
            {
                this._rotationSpeed = Math.random() * (data.rotationSpeedMax - data.rotationSpeedMin) + data.rotationSpeedMin;
            }
            else
            {
                this._rotationSpeed = data.rotationSpeedMin;
            }
        }

        this._rotationDamping = 1;
        if (data.rotationDamping != undefined)
            this._rotationDamping = data.rotationDamping;

        this._life = 0;
        if (data.lifeMin != undefined)
        {
            if (data.lifeMax != undefined)
            {
                this._life = Game._now + Math.random() * (data.lifeMax - data.lifeMin) + data.lifeMin;
            }
            else
            {
                this._life = Game._now + data.lifeMin;
            }
        }

        if (data.xMin != undefined)
        {
            if (data.xMax != undefined)
            {
                this._x += Math.random() * (data.xMax - data.xMin) + data.xMin;
            }
            else
            {
                this._x += data.xMin;
            }
        }

        if (data.yMin != undefined)
        {
            if (data.yMax != undefined)
            {
                this._y += Math.random() * (data.yMax - data.yMin) + data.yMin;
            }
            else
            {
                this._y += data.yMin;
            }
        }

        if (data.zMin != undefined)
        {
            if (data.zMax != undefined)
            {
                this._z += Math.random() * (data.zMax - data.zMin) + data.zMin;
            }
            else
            {
                this._z += data.zMin;
            }
        }

        this._vx = 0;
        if (data.vxMin != undefined)
        {
            if (data.vxMax != undefined)
            {
                this._vx = Math.random() * (data.vxMax - data.vxMin) + data.vxMin;
            }
            else
            {
                this._vx = data.vxMin;
            }
        }

        this._vy = 0;
        if (data.vyMin != undefined)
        {
            if (data.vyMax != undefined)
            {
                this._vy = Math.random() * (data.vyMax - data.vyMin) + data.vyMin;
            }
            else
            {
                this._vy = data.vyMin;
            }
        }

        this._vz = 0;
        if (data.vzMin != undefined)
        {
            if (data.vzMax != undefined)
            {
                this._vz = Math.random() * (data.vzMax - data.vzMin) + data.vzMin;
            }
            else
            {
                this._vz = data.vzMin;
            }
        }

        this._ax = 0;
        if (data.axMin != undefined)
        {
            if (data.axMax != undefined)
            {
                this._ax = Math.random() * (data.axMax - data.axMin) + data.axMin;
            }
            else
            {
                this._ax = data.axMin;
            }
        }

        this._ay = 0;
        if (data.ayMin != undefined)
        {
            if (data.ayMax != undefined)
            {
                this._ay = Math.random() * (data.ayMax - data.ayMin) + data.ayMin;
            }
            else
            {
                this._ay = data.ayMin;
            }
        }

        this._az = 0;
        if (data.azMin != undefined)
        {
            if (data.azMax != undefined)
            {
                this._az = Math.random() * (data.azMax - data.azMin) + data.azMin;
            }
            else
            {
                this._az = data.azMin;
            }
        }

        this._frictionx = 1;
        if (data.frictionx != undefined)
            this._frictionx = data.frictionx;
        
        this._frictiony = 1;
        if (data.frictiony != undefined)
            this._frictiony = data.frictiony;

        this._frictionz = 1;
        if (data.frictionz != undefined)
            this._frictionz = data.frictionz;

        if (data.dieAtEnd != undefined)
            this._dieOnLoop = true;

        this.alpha = 1;
        if (data.alpha != undefined)
            this.alpha = data.alpha;

        this._fade = 0;
        if (data.fade != undefined)
            this._fade = data.fade;

        if (data.scalex)
            this.scale.x = data.scalex;

        if (data.scaley)
            this.scale.y = data.scaley;

        this._scalex = 0;
        if (data.rescalex != undefined)
            this._scalex = data.rescalex;

        this._scaley = 0;
        if (data.rescaley != undefined)
            this._scaley = data.rescaley;

        if (data.tint != undefined)
            this.tint = data.tint;

        // don't try to hue rotate on mobile, it's a Canvas operation and SLOOOOW
        if (!PIXI.utils.isMobile.any)
        {
            if (data.hueAngle != undefined || data.hueRotSpeed != undefined)
            {
                this._colorMatrix = new PIXI.filters.ColorMatrixFilter();
                this.filters = [this._colorMatrix];
            }
            if (data.hueAngle != undefined)
                if (data.hueAngle == -1)
                    this._hueAngle = Math.floor(Math.random() * 360.0);
                else
                    this._hueAngle = data.hueAngle;
            if (data.hueRotSpeed != undefined)
                this._hueRotSpeed = data.hueRotSpeed;
        }

        if (data.sfx != undefined)
            Audio.playSfx(data.sfx, data.vol);

        this.anchor.set(0.5);
        this.onLoop = this.animComplete;
        this.onComplete = this.animComplete;
        this.gotoAndPlay(startFrame);

        this.x = this._x;
        this.y = this._y - this._z;

        this._container.addChild(this);
    }


    public destroy():void
    {
        this._isDone = true;
        // make sure that any update function is called once when the effect is destoyed with _isDone = true
        if (this._onUpdate)
        {
            this._onUpdate.call(this);
            this._onUpdate = null;
        }
        this._container.removeChild(this);
        this._container = null;
    }


    private animComplete(): void
    {
        this._looped = true;
    }


    // return false if this effect should be destroyed
    public animate(): boolean
    {
        // don't do anything until we're visible
        if (!this.visible)
            return true;

        if (this._looped && this._dieOnLoop)
            return false;

        // remember the game time when we started to update
        if (this._started == 0)
            this._started = Game._now;

        // the update specified in animation data adds variations to the Effect behaviour
        if (this._onUpdateData)
        {
            this._onUpdateData.call(this);
        }

        // the update provided from init() adds the ability to trigger external events
        // e.g. when Paint is used on a doll, we want to trigger the colour change on frame 2 of the Effect animation
        if (this._onUpdate)
        {
            if (this._onUpdate.call(this))
                this._onUpdate = null;
        }

        if (!PIXI.utils.isMobile.any)
        {
            if (this._hueRotSpeed)
            {
                this._hueAngle += this._hueRotSpeed * Game._elapsed / 1000;
                // rotate the hue to create a fluctuating effect
                this._colorMatrix.hue(Math.floor(this._hueAngle));
            }
        }

        this._x += this._vx * Game._elapsed / 1000;
        this._vx = this._vx * this._frictionx + this._ax;
        this._y += this._vy * Game._elapsed / 1000;
        this._vy = this._vy * this._frictiony + this._ay;
        this._z += this._vz * Game._elapsed / 1000;
        if (this._z < 0)
        {
            // bounce off ground and apply friction to x/y velocities
            this._z = 0;
            this._vz = -this._vz * 0.25;
            this._vx *= 0.9;
            this._vy *= 0.9;
        }
        this._vz = this._vz * this._frictionz + this._az;

        this.rotation += this._rotationSpeed * Game._elapsed / 1000;
        this._rotationSpeed *= this._rotationDamping;

        if (this._scalex)
            this.scale.x += this._scalex * Game._elapsed / 1000;
        if (this._scaley)
            this.scale.y += this._scaley * Game._elapsed / 1000;

        this.alpha = Math.max(Math.min(this.alpha + this._fade * Game._elapsed / 1000, 1), 0);

        this.x = this._x + this._sx;
        this.y = this._y - this._z + this._sy;

        return (this._life == 0 || Game._now < this._life);
    }

};



