import {State} from "./State";
import {StateManager} from "./StateManager";
import {Engine} from "../engine/Engine";
import {BoardInput} from "../engine/BoardInput";
import {GameSettings, GameEvent} from "../settings/GameSettings";
import {StateNames, GameEventType} from "../const/Enums";
import {Audio} from "../ui/Audio";
import {Help} from "../ui/Help";
import {MissionPopup} from "../ui/MissionPopup";
import {MissionEvents} from "../util/MissionData";
import {Game} from "../Game";
import {APIWrapper} from "../util/APIWrapper";
import {Cheats} from "../util/Cheats";
import {CheatKey} from "../util/Cheats";
import {LoadSave} from "../util/LoadSave";
import {SaveData, SaveObject} from "../util/SaveData";
import {Inventory} from "../util/Inventory";
import {Wincodes} from "../util/Wincodes";




// level switching logic in here:
// destroy Engine and build a new one for the next level
export class GameState extends State
{
    private _container: PIXI.Container;
    private _engine: Engine;
    private _help: Help;
    private _missions: MissionPopup;
    private _emitter: PIXI.utils.EventEmitter;

    private _level: number;
    private _score: number;
    private _moves: number;
    private _megaMatches: number;
    private _multiplier: number;

    private _nextGameEvent: GameEvent;
    private _nextLevelEvent: GameEvent;

    private _saveObject: Inventory;

    private _unlimitedMovesEnabled: boolean; // Enabled by cheats

    /**
     * Calls the base State constructor
     * @param {StateManager} controller  A reference to the state controller
     * @param {PIXI.Container} stage     A reference the stage, the main PIXI container
     */
    public constructor (controller: StateManager, stage: PIXI.Container)
    {
        super(controller, stage);
    }
    

    public init():void
    {
        APIWrapper.gameStarted();
        Game.getSeed();
        Inventory.init();
        
        this._level = 1;
        this._score = 0;
        this._multiplier = 1;
        this._moves = GameSettings.InitialMoves;
        this._megaMatches = 0;

        this._nextGameEvent = this.findNextEvent(null, this._score, -GameEventType.LevelUp);
        this._nextLevelEvent = this.findNextEvent(null, this._score, GameEventType.LevelUp);

        // the container at this level holds a whole game instance
        this._container = new PIXI.Container;
        this._stage.addChild(this._container);

        // local context reference for closures
        let _that: GameState = this;

        // add an event emitter to decouple things like score notification and drag/drop events
        this._emitter = this._manager.events;
        this._emitter.on("match_score_add", function(value: number)
            {
                _that._score += value;
                _that.triggerEvents.bind(_that)();
                _that._emitter.emit("score_changed", _that._score, value);
            });
        this._emitter.on("mega_match_add", function(value: number)
            {
                _that._megaMatches += value;
                _that._emitter.emit("megamatch_changed", _that._megaMatches, value);
            });
        this._emitter.on("move_made", function()
            {
                if (_that._unlimitedMovesEnabled) {
                    // do nothing
                }
                else {
                    _that._moves = Math.max(_that._moves - 1, 0);
                    _that._emitter.emit("moves_changed", _that._moves, -1);
                }
            });
        this._emitter.on("boost_used", function(boostType: number)
            {
                if (GameSettings.consoleLogging)
                    console.log("consume powerup " + boostType);
                switch(boostType)
                {
                    case 0:
                        APIWrapper.consumePowerUp("powerup_paint", Inventory.powerup_paint, function(response)
                            {
                                if (response.stats[0].id == "powerup_paint")
                                {
                                    Inventory.powerup_paint = response.stats[0].value;
                                    if (Inventory.powerup_paint < 0) Inventory.powerup_paint = 0;
                                    _that._emitter.emit("show_boost_paint", Inventory.powerup_paint);
                                }

                                if (GameSettings.consoleLogging)
                                    if (response.stats[0].failStatus)
                                        console.log("Use paint boost failed: " + response.stats[0].failStatus);
                                    
                                SaveData.save();
                                Inventory.load();
                                _that._emitter.emit("show_boost_paint", Inventory.powerup_paint);
                            });
                        break;
                    case 1:
                        APIWrapper.consumePowerUp("powerup_remove_doll", Inventory.powerup_remove_doll, function(response)
                            {
                                if (response.stats[0].id == "powerup_remove_doll")
                                {
                                    Inventory.powerup_remove_doll = response.stats[0].value;
                                    if (Inventory.powerup_remove_doll < 0) Inventory.powerup_remove_doll = 0;
                                    _that._emitter.emit("show_boost_destroy", Inventory.powerup_remove_doll);
                                }

                                if (GameSettings.consoleLogging)
                                    if (response.stats[0].failStatus)
                                        console.log("Use destroy boost failed: " + response.stats[0].failStatus);
                                    
                                SaveData.save();
                                Inventory.load();
                                _that._emitter.emit("show_boost_destroy", Inventory.powerup_remove_doll);
                            });
                        break;
                    case 2:
                        APIWrapper.consumePowerUp("powerup_size_up", Inventory.powerup_size_up, function(response)
                            {
                                if (response.stats[0].id == "powerup_size_up")
                                {
                                    Inventory.powerup_size_up = response.stats[0].value;
                                    if (Inventory.powerup_size_up < 0) Inventory.powerup_size_up = 0;
                                    _that._emitter.emit("show_boost_grow", Inventory.powerup_size_up);
                                }

                                if (GameSettings.consoleLogging)
                                    if (response.stats[0].failStatus)
                                        console.log("Use grow boost failed: " + response.stats[0].failStatus);
                                    
                                SaveData.save();
                                Inventory.load();
                                _that._emitter.emit("show_boost_grow", Inventory.powerup_size_up);
                            });
                        break;
                }
            });
        this._emitter.on("show_titles", function()
            {
                // switch to the title screen state (automatically destroys this state)
                _that._manager.startState(StateNames.TITLE_SCREEN);
            });
        this._emitter.on("show_trophy_room", function()
            {
                // switch to the title screen state and show the trophy room immediately
                GameSettings.showTrophyRoom = true;
                _that._manager.startState(StateNames.TITLE_SCREEN);
            });
        this._emitter.on("use_extra_lives", function()
            {
                Inventory.load();
                _that._moves += 5;
                _that._emitter.emit("moves_changed", _that._moves, 5, true);
                _that._emitter.emit("gain_moves", 5);
                _that._engine.getPlayArea().extraMoves();
            });
        // static class: mouse click/touch system for drag and drop
        new BoardInput(this._container, this._emitter);

        // engine to play this current level
        this._engine = new Engine(this._container, this._emitter, this);

        // help system
        this._help = new Help(this._container, this._emitter);

        // missions system (displays game start Mission list)
        this._missions = new MissionPopup(this._container);
        // create the MissionEvents.activeMissions list
        MissionEvents.setActiveMissions();
        // track mission progress during the game via Events
        MissionEvents.addMissionEvents(this._emitter);
        // track wincode number updates via Events
        Wincodes.addWincodeEvents(this._emitter);

        this._emitter.emit("level_changed", this._level, this._moves, this._nextLevelEvent.getScore());
        this._emitter.emit("score_changed", this._score, 0);
        this._emitter.emit("megamatch_changed", this._megaMatches, 0);
        this._emitter.emit("moves_changed", this._moves, 0);
        this._emitter.emit("show_boost_paint", Inventory.powerup_paint);
        this._emitter.emit("show_boost_destroy", Inventory.powerup_remove_doll);
        this._emitter.emit("show_boost_grow", Inventory.powerup_size_up);
        this._emitter.emit("game_start");

        Audio.gameTune();

        // show help at game start if it has not been disabled (true by default, saved in LocalSettings)
        this._help.show(GameSettings.showTutorial, function()
            {
                // only show Missions popup if there are any openedTrophies (mission indices)
                if (GameSettings.openedTrophies.length > 0)
                    _that._missions.show(true);
            });
    }
    

    public update():void
    {
        if (Cheats.pressedKeys[CheatKey.UNLIMITED_MOVES]) {
            this._unlimitedMovesEnabled = !this._unlimitedMovesEnabled;
        }
        if (Cheats.pressedKeys[CheatKey.INSTANT_LOSS] && this._moves > 0) {
            let numMoves = this._moves;
            this._moves = 0;
            this._emitter.emit("moves_changed", this._moves, -numMoves);
        }
        if (Cheats.pressedKeys[CheatKey.RESET_PROGRESS]) {
            SaveObject.reset();
            SaveData.save();
        }
        if (Cheats.pressedKeys[CheatKey.UNLOCK_ALL_TROPHIES]) {
            GameSettings.allmissionsCheat = true;
        }
        if (Cheats.pressedKeys[CheatKey.MOVE_FAKE_20]) {
            this._moves -= 20;
            this._emitter.emit("moves_changed", this._moves, -20);
        }
        this._engine.update();

        if (GameSettings.missionCheat)
        {
            this._emitter.emit("mission_cheat");
        }
        if (GameSettings.allmissionsCheat)
        {
            this._emitter.emit("all_missions_cheat");
        }
    }
    

    public resize(): void
    {
        if (this._help && this._help.visible)
            this._help.resize();
        if (this._engine)
            this._engine.resize();
        if (this._missions)
            this._missions.resize();
    }
    

    public destroy(): void
    {
        this._emitter.removeAllListeners();
        Wincodes.removeEvents();
        MissionEvents.removeEvents();
        BoardInput.destroy();
        this._container.destroy();
        this._emitter.removeAllListeners();
        this._emitter = null;
        this._engine.destroy();
    }


    // specify a negative eventType to find anything *except that eventType*
    private findNextEvent(start: GameEvent, value: number, eventType: GameEventType):GameEvent
    {
        let c:number = 0;
        let ge:GameEvent = null;

        if (start != null)
            c = GameSettings.scoreEvents.indexOf(start) + 1;

        do{
            ge = GameSettings.scoreEvents[c++];
        }while( c < GameSettings.scoreEvents.length
                && ((eventType >= 0 && ge.getType() != eventType)
                    || (eventType < 0 && ge.getType() == -eventType)));

        return ge;
    }


    private triggerEvents():void
    {
        // the score might skip past a number of events in one increment...
        // activate each until we are no longer triggered
        let triggered: boolean;
        do{
            triggered = false;

            // did we trigger the next level-up event?
            while(this._nextLevelEvent && this._nextLevelEvent.triggered(this._score))
            {
                // advance to next level
                this._level++;

                // find the next level trigger
                this._nextLevelEvent = this.findNextEvent(this._nextLevelEvent, this._score, GameEventType.LevelUp);
                //console.log("level up! " + this._level + " at score " + this._score);

                if (this._nextLevelEvent) {
                    this._emitter.emit("level_changed", this._level, this._moves, this._nextLevelEvent.getScore());
                }
                triggered = true;
            }

            // did we trigger the next non-level event?
            while(this._nextGameEvent && this._nextGameEvent.triggered(this._score))
            {
                //console.log("game event! " + this._nextGameEvent.getType() + " at score " + this._score);

                // perform the game event
                switch(this._nextGameEvent.getType())
                {
                    case GameEventType.Multiplier:
                        this._multiplier = this._nextGameEvent.getValue();
                        //console.log("score multiplier = " + this._multiplier);
                        break;

                    case GameEventType.ExtraMoves:
                        let extra:number = this._nextGameEvent.getValue();
                        this._moves += extra;
                        this._emitter.emit("moves_changed", this._moves, extra, false);
                        this._emitter.emit("gain_moves", extra);
                        break;
                }

                // find the next non-level event
                this._nextGameEvent = this.findNextEvent(this._nextGameEvent, this._score, -GameEventType.LevelUp);
                triggered = true;
            }
        }while( triggered );
    }


    public getMegaMatches():number
    {
        return this._megaMatches;
    }
    
    public getScore():number
    {
        return this._score;
    }

    public getMoves():number
    {
        return this._moves;
    }

    public getMultiplier():number
    {
        return this._multiplier;
    }

    public getLevel():number
    {
        return this._level;
    }

}



