import {Devices, GameEventType} from "../const/Enums";



export class GameEvent
{
    private _eventType: GameEventType;
    private _scoreThreshold: number;
    private _eventValue: number;



    constructor(type: GameEventType, threshold: number, value?: number)
    {
        this._eventType = type;
        this._scoreThreshold = threshold;
        this._eventValue = value;
    }


    public triggered(score: number):boolean
    {
        return (score >= this._scoreThreshold);
    }


    public getType():GameEventType
    {
        return this._eventType;
    }


    public getScore():number
    {
        return this._scoreThreshold;
    }


    public getValue():number
    {
        return this._eventValue;
    }
}


