    // TODO: remove for game release, same as above but makes missions complete instantaneously, can also be enabled with the "t" key when cheats are on
    public static allmissionsCheat: boolean = false;

    // TODO: remove for game release, registers key presses and checks for various cheats (add score, unlimited moves, etc.)
