import {MissionData} from "./MissionData";
import {PieceColours} from "../const/Enums";
import {GameSettings} from "../settings/GameSettings";



export class Wincodes
{
    private static _emitter:PIXI.utils.EventEmitter;

    public static _score:number = 0; // player score
    public static _playerWon:number = 0; // whether the player won (there is no 'win' condition so this will always be false)
    public static _levels:number = 0; // Level Reached
    public static _chain:number = 0; // Biggest Chain
    public static _threechain:number = 0; // Number of Three Chains
    public static _powerupRemoveDoll:number = 0; // Remove Doll Power Up used
    public static _powerupSizeUp:number = 0; // Size Up Power Up used
    public static _powerupPaint:number = 0; // Paint Power Up used
    public static _powerups:number = 0; // All Power Ups used
    public static _megamatches:number = 0; // Mega Matches Made
    public static _yellow:number = 0; // Yellow Dolls Matched (Total)
    public static _blue:number = 0; // Blue Dolls Matched (Total)
    public static _green:number = 0; // Green Dolls Matched (Total)
    public static _moves:number = 0; // Number of moves used
    public static _fromthebrink:number = 0; // Did you earn a level up on your final move
    public static _superstar:number = 0; // How many times did you match 5 or more dolls in one move?
    public static _trizzleman:number = 0; // Play a game that reaches or exceeds 75 moves.
    public static _games:number = 0; // How many games have you played


    public static resetWincodesForNewGame():void
    {
        if (GameSettings.consoleLogging)
            console.log("reset Wincodes at start of new Game");
        
        Wincodes._score = 0;
        Wincodes._playerWon = 0;
        Wincodes._levels = 0;
        Wincodes._chain = 0;
        Wincodes._threechain = 0;
        Wincodes._powerupRemoveDoll = 0;
        Wincodes._powerupSizeUp = 0;
        Wincodes._powerupPaint = 0;
        Wincodes._powerups = 0;
        Wincodes._megamatches = 0;
        Wincodes._yellow = 0;
        Wincodes._blue = 0;
        Wincodes._green = 0;
        Wincodes._moves = 0;
        Wincodes._fromthebrink = 0;
        Wincodes._superstar = 0;
        Wincodes._trizzleman = 0;
        Wincodes._games = 0;
    }


    public static addWincodeEvents(emitter: PIXI.utils.EventEmitter):void
    {
        emitter.on("score_changed", function(score, changed) { Wincodes._score = score; });
        emitter.on("level_changed", function(level, moves) { Wincodes._levels = Math.max(Wincodes._levels, level); });
        emitter.on("match_chain", function(chainLength)
            {
                Wincodes._chain = Math.max(Wincodes._chain, chainLength);
                if (chainLength == 3) Wincodes._threechain++;
            });
        emitter.on("boost_used", function(boostType: number)
            {
                switch(boostType)
                {
                    case 0:
                        Wincodes._powerupPaint++;
                        break;
                    case 1:
                        Wincodes._powerupRemoveDoll++;
                        break;
                    case 2:
                        Wincodes._powerupSizeUp++;
                        break;
                }
                Wincodes._powerups++;
            });
        emitter.on("mega_match_add", function(value: number) { Wincodes._megamatches += value; });
        emitter.on("matched_group", function(size: number, colour: number)
            {
                switch(colour)
                {
                    case PieceColours.Blue: Wincodes._blue += size; break;
                    case PieceColours.Green: Wincodes._green += size; break;
                    case PieceColours.Yellow: Wincodes._yellow += size; break;
                }
                if (size >= 5) Wincodes._superstar++;
            });
        emitter.on("move_made", function() { Wincodes._moves++; });
        emitter.on("mission_goal", function(data: MissionData)
            {
                if (data._name == "Trizzleman")
                    Wincodes._trizzleman++;
                if (data._name == "From the Brink")
                    Wincodes._fromthebrink++;
            });
        emitter.on("game_start", function() { Wincodes._games++; });
        Wincodes._emitter = emitter;
    }


    public static removeEvents():void
    {
        Wincodes._emitter.removeAllListeners();
    }


    public static GetWincodes():any
    {
        let o:any = {
            "score": Wincodes._score,
            "playerWon": Wincodes._playerWon,
            "levels": Wincodes._levels,
            "chain": Wincodes._chain,
            "threechain": Wincodes._threechain,
            "powerupRemoveDoll": Wincodes._powerupRemoveDoll,
            "powerupSizeUp": Wincodes._powerupSizeUp,
            "powerupPaint": Wincodes._powerupPaint,
            "powerups": Wincodes._powerups,
            "megamatches": Wincodes._megamatches,
            "yellow": Wincodes._yellow,
            "blue": Wincodes._blue,
            "green": Wincodes._green,
            "moves": Wincodes._moves,
            "fromthebrink": Wincodes._fromthebrink,
            "superstar": Wincodes._superstar,
            "trizzleman": Wincodes._trizzleman,
            "games": Wincodes._games,
        }
        return o;
    }

}



