import * as ArenaHelper from "../util/ArenaHelper";
import * as FontFaceObserver from "fontfaceobserver";

import {Game} from "../Game";
import {Events} from "../const/Events";
import {GameSettings} from "../settings/GameSettings";
import {State} from "./State";
import {StateManager} from "./StateManager";
import {StateNames} from "../const/Enums";
import {Audio} from "../ui/Audio";
import {LoadSave} from "../util/LoadSave";



/**
 * State for loading assets. 
 */
export class AssetLoaderState extends State
{
    public static NumTextures: number = 5;        // how many atlases did TexturePacker make for all of the art

    private _assetsLoaded: boolean = false;
    private _fontsLoaded: boolean = false;
    private _loadingText: PIXI.Text;
    private _textStyle: PIXI.TextStyle;
    private _fontWait: number = 4000;

    
    /**
     * Calls the base State constructor
     * @param {StateManager} controller  A reference to the state controller
     * @param {PIXI.Container} stage        A reference the stage, the main PIXI container
     */
    public constructor(controller: StateManager, stage: PIXI.Container)
    {
        super(controller, stage);
    }

    /**
     * Tells the PIXI.loader to load all of the assets!
     */
    public init():void
    {
        let _that: AssetLoaderState = this;

        // Resets any assets loaded already: this lets us replay
        PIXI.loader.reset();

        //
        // Fonts
        //

        let fontStyle = require("../../styles/fonts.less");
        fontStyle = fontStyle.replace(/%RESOURCE_PATH%/g, ArenaHelper.getInstance().getAbsoluteURL(""));

        let css = document.createElement("style");
        css.setAttribute("type", "text/css");
        css.innerHTML = fontStyle;
        document.getElementsByTagName("head")[0].appendChild(css);

        this.loadFonts();
       

        //
        // Texture atlas
        //

        // NOTE: PIXI handles the loading of the image corresponding to the game_atlas json in weird way, where a 404
        // on the game atlas image will be reported the first time through, but not on subsequent times.
        // I'm choosing to ignore this because the assets should not fail, and there's nothing meaningful we can do if
        // the server goes down after serving the game scripts but before serving its assets.

        // add the soundSprite JSON and load it with the texture atlas descriptors
        PIXI.loader.add("soundSprite", ArenaHelper.getInstance().getAbsoluteURL("assets/sounds/soundsprite.json"))
        for(let i = 0; i < AssetLoaderState.NumTextures; i++)
        {
            let assetName: string = "gameAtlas_" + i.toString();
            let jsonName: string = "game_atlas-" + i.toString();
            PIXI.loader.add(assetName, ArenaHelper.getInstance().getAbsoluteURL("assets/images/" + jsonName + ".json"));
        }
        PIXI.loader.once("complete", this.onAssetsLoaded, this);
        PIXI.loader.on("error", this.onPIXILoaderError, this);
        PIXI.loader.load();

        PIXI.loader.on("progress", function(loader, res)
        {
            if (_that._loadingText)
                _that._loadingText.text = "Loading...\n" + Math.floor(loader.progress) + "%";
        });
    }


    private loadFonts():void
    {
        let _that: AssetLoaderState = this;
        //console.log("loadFonts: GrilledCheese");
        let fontLoader:FontFaceObserver = new FontFaceObserver("GrilledCheese");
        fontLoader.load(null, this._fontWait).then(
                this.onFontsLoaded.bind(this),
                function()
                {
                    console.log("font load failed after " + _that._fontWait + " seconds.");
                    _that._fontWait += 1000;
                    if (_that._fontWait < 7000)
                    {
                        _that.loadFonts.call(_that);
                    }
                    else
                    {
                        console.log("font will not load!");
                        _that.showMessage("Error: font will not load!");
                    }
                }
            );
    }

    
    /**
     * Handles PIXI loader errors
     * @param error
     * @param loader
     * @param resource
     */
    private onPIXILoaderError(error: Error, loader: PIXI.loaders.Loader, resource: PIXI.loaders.Resource):void
    {
        console.log("Error loading asset: " + resource.name);
        if (resource.name.substr(0, 9) === "gameAtlas") {
            this.onLoaderError("Failed to load essential asset: " + resource.name);
        }
    }
    

    /**
     * Logs an error message
     * @param {string} message  The message describing the error
     */
    private onLoaderError(message: string):void
    {
        console.log(message);
    }
    

    /**
     * Post-load handling. Saves a reference to the game atlas in GameSettings, and signals the state controller that
     * asset loading is complete
     * @param {PIXI.loaders.loader} loader                  Reference to the PIXI loader
     * @param {PIXI.loaders.ResourceDictionary} resources  Reference to the loaded resources
     */
    private onAssetsLoaded(loader: PIXI.loaders.Loader, resources: PIXI.loaders.ResourceDictionary):void
    {
        Audio.soundSpriteJSON = resources["soundSprite"].data;

        // create Audio system when resources are available
        // **must be _before_ LoadSave.load which immediately sets the restored volumes**
        new Audio();

        GameSettings.gameAtlas = new Array<PIXI.loaders.TextureDictionary>();
        for(let i = 0; i < AssetLoaderState.NumTextures; i++)
        {
            let assetName: string = "gameAtlas_" + i.toString();
            GameSettings.gameAtlas.push(resources[assetName].textures);
        }

        this._assetsLoaded = true;
        this.checkAssetsAndFontsLoaded();
        Game._stage.emit("preloader_finished");
    }


    private showMessage(msg: string):void
    {
        this._textStyle = new PIXI.TextStyle({
            fontFamily: "GrilledCheese",
            fontSize: 50,
            fontWeight: 'bold',
            fill: ['#ffffff'],
            dropShadow: false,
            align: 'center',
            leading: -8,
            wordWrap: false
        });
        this._loadingText = new PIXI.Text(msg, this._textStyle);
        this._loadingText.x = GameSettings.width / 2;
        this._loadingText.y = GameSettings.height / 2;
        this._loadingText.anchor.set(0.5);
        this._stage.addChild(this._loadingText);
    }


    private onFontsLoaded(): void
    {
        this._fontsLoaded = true;

        // display "loading..." text message now the font is available
        this.showMessage("Loading...");

        this.checkAssetsAndFontsLoaded();
    }


    private checkAssetsAndFontsLoaded(): void
    {
        if (this._assetsLoaded && this._fontsLoaded)
        {
            this._manager.startState(StateNames.TITLE_SCREEN);
            if (this._loadingText && this._loadingText.parent)
                this._stage.removeChild(this._loadingText);
            this._loadingText = null;
        }
    }


    public resize():void
    {
        if (this._loadingText && this._loadingText.parent)
        {
            this._loadingText.x = GameSettings.width / 2;
            this._loadingText.y = GameSettings.height / 2;
        }
    }

}



