"use strict";

var ArenaHelper = function () {
    if (window.ArenaXApi !== undefined && window.ArenaXApi !== null) {
        this.arenaXApi = ArenaXApi.getInstance();
    }

    this.testActionCallback = null;

    // internal callbacks that the game can register for midrolls
    this.pauseCallback = null;
    this.resumeCallback = null;

    // "actions" for arena api's
    this.pauseHandler = this.handleMidrollStart.bind(this);
    this.resumeHandler = this.handleMidrollFinish.bind(this);

    if (this.arenaXApi) {
        this.arenaXApi.addAction("GAME_PAUSE", this.pauseHandler);
        this.arenaXApi.addAction("GAME_RESUME", this.resumeHandler);
    }
    else {
        // TODO(ebuchholz): include this guy for arena4 build
        //ARK_game_arena_connector.init();
        //ARK_game_arena_connector.registerAction("pause", this.pauseHandler);
        //ARK_game_arena_connector.registerAction("resume", this.resumeHandler);
    }
};
module.exports = ArenaHelper;

ArenaHelper.getInstance = function () {
    if (!ArenaHelper.instance) {
        ArenaHelper.instance = new ArenaHelper();
    }
    return ArenaHelper.instance;
};

ArenaHelper.prototype = {

    getParam: function (paramName, defaultValue) {
        if (this.arenaXApi) {
            // TODO(ebuchholz): implement phoenix's way of getting arena parameters. For now, hard-coded values for
            // certain known parameters
            switch (paramName) {
            default:
                return defaultValue;
            case "configURL":
                // TODO(ebuchholz): Host a config file and puzzles somewhere external, rather than packing them with the
                // game
                return "";
            }
        }
        else {
            return ARK_game_arena_connector.getParam(paramName, defaultValue);
        }
    },

    handleMidrollRequest: function () {
        console.log("[ArenaHelper] handleMidrollRequest");
        if (this.arenaXApi) {
            this.arenaXApi.dispatch({ type: "PAUSE_READY", payload: null });
        }
        else {
            ARK_game_arena_connector.fireEventToArena("pause_ready");
        }
    },

    handleMidrollStart: function () {
        console.log("[ArenaHelper] handleMidrollStart");
        // call midroll start callback
        if (this.pauseCallback) {
            this.pauseCallback();
        }
    },

    handleMidrollFinish: function () {
        console.log("[ArenaHelper] handleMidrollFinish");
        // call midroll end callback
        if (this.resumeCallback) {
            this.resumeCallback();
        }
    },

    handleEventChange: function () {
        console.log("[ArenaHelper] handleEventChange");
        if (this.arenaXApi) {
            this.arenaXApi.dispatch({ type: "EVENT_CHANGE", payload: null });
        }
        else {
            ARK_game_arena_connector.fireEventToArena("event_change");
        }
    },

    handleGameStart: function () {
        console.log("[ArenaHelper] handleGameStart");
        if (this.arenaXApi) {
            this.arenaXApi.dispatch({ type: "GAME_START", payload: null });
        }
        else {
            ARK_game_arena_connector.fireEventToArena("game_start");
        }
    },

    handleGameEnd: function (score) {
        console.log("[ArenaHelper] handleGameEnd");
        if (this.arenaXApi) {
            this.arenaXApi.dispatch({ type: "CHANGE_SCORE", payload: score });
            this.arenaXApi.dispatch({ type: "GAME_END", payload: null });
        }
        else {
            ARK_game_arena_connector.changeScore(score);
            ARK_game_arena_connector.fireEventToArena("game_end");
        }
    },

    runningOnArena: function () {
        if (this.arenaXApi) {
            return true;
        }
        else if (ARK_game_arena_connector._params != null && ARK_game_arena_connector._params.length > 0) {
            return true;
        }
        return false;
    },

    shouldShowGameEnd: function () {
        if (this.arenaXApi) {
            return true;
        }
        else {
            ARK_game_arena_connector.showGameEnd() == "true";
        }
    },

    getAbsoluteURL: function (relativeURL) {
        if (this.arenaXApi) {
            return [this.arenaXApi.assetOriginUrl, relativeURL].join("");
        }
        return relativeURL;
    },

    getLocale: function () {
        if (this.arenaXApi) {
            return this.arenaXApi.locale;
        }
        else {
            return ARK_game_arena_connector.getParam("locale", "en-US");
        }
    },
    
    destroy: function () {
        if (this.arenaXApi) {
            this.arenaXApi.removeAction("GAME_PAUSE");
            this.arenaXApi.removeAction("GAME_RESUME");
        }
    },

    initTestingEnvironment:function ()
    {
        if (this.arenaXApi)
        {
            return;
        }
        else
        {
            ARK_game_arena_connector.registerAction("test_action", this.processTestAction.bind(this));
            ARK_game_arena_connector.initTestingEnvironment();
        }
    },

    processTestAction: function (params)
    {
        for (var i = 0; i < params.length; i++) {
            var obj = params[i];
            if (obj.name == "test_object")
            {
                var testingParams = obj.value.split(";");
                var testing_action=testingParams[0];
                if (this.testActionCallback)
                {
                    this.testActionCallback(testing_action);
                }
                break;
            }
        }
    },

};



