


export class AnimationData
{
    public _name: string;
    public _list: Array<string>;
    public _speed: number;

    constructor(name: string, list: Array<string>, speed: number)
    {
        this._name = name;
        this._list = list;
        this._speed = speed;
    }
}


export class TextureData
{
    public _list: Array<PIXI.Texture>;
    public _speed: number;

    constructor(list: Array<PIXI.Texture>, speed: number)
    {
        this._list = list;
        this._speed = speed;
    }
}


