// https://gist.github.com/lsenta/15d7f6fcfc2987176b54



export class RNG
{
    // set the seed directly (RNG.seed = xxxx), it will be used for subsequent RNG.next
    public static seed:number;


    private static next(min:number, max:number):number
    {
        RNG.seed = (Math.abs(RNG.seed) * 9301 + 49297) % 233280;
        var rnd = RNG.seed / 233280;
        return min + rnd * (max - min);
    }

    // http://indiegamr.com/generate-repeatable-random-numbers-in-js/
    public static nextInt(min?:number, max?:number):number
    {
        min = min || 0;
        max = max || 1;
        return Math.floor(RNG.next(min, max));
    }

    public static nextDouble():number
    {
        return RNG.next(0, 1);
    }

    public static pick(collection:any[]):any
    {
        return collection[RNG.nextInt(0, collection.length - 1)];
    }
}



