// keep all the data that must be persisted across multiple play-sessions here
import {APIWrapper} from "../util/APIWrapper";
import {GameSettings} from "../settings/GameSettings";
import {MissionDescriptions, MissionData} from "./MissionData";



export class SaveObject
{


    public static init():void
    {
    }


    public static fromJson(jsonString: string):void
    {
        //console.log("SaveObject fromJson: " + jsonString);
        let o:any = JSON.parse(jsonString);

        // set defaults for openedTrophies (11 trophies representing the first of each 'group')
        GameSettings.openedTrophies = [0, 5, 10, 15, 20, 25, 30, 35, 40, 45, 50];

        // recover the trophy progress values for every trophy
        // build the openedTrophies list (the first uncompleted trophy in each group of 5 trophies)
        if (o._trophyProgress)
        {
            for(let i = 0; i < o._trophyProgress.length; i++)
            {
                let d: MissionData = MissionDescriptions.data[i];
                d._goalProgress = o._trophyProgress[i].goalProgress || 0;
                d._completed = o._trophyProgress[i].completed || 0;

                // update the openedTrophies list for completed trophies
                if (d._completed >= d._required)
                {
                    // trophy is completed
                    let oi: number = GameSettings.openedTrophies.indexOf(i);
                    if (oi != -1)
                    {
                        // trophy is 'open' in list
                        let groupIndex: number = i % 5;
                        if (groupIndex < 4)
                        {
                            // trophy is not last in its group, advance the openedTrophies value
                            GameSettings.openedTrophies[oi]++;
                        }
                        else
                        {
                            // trophy is last in its group, remove it from the opened list entirely
                            GameSettings.openedTrophies.splice(oi, 1);
                        }
                    }
                }
            }
            //console.log("opened trophies = " + JSON.stringify(GameSettings.openedTrophies));
        }
    }

    public static reset(): void 
    {
        let trophyProgress:Array<any> = new Array<any>(MissionDescriptions.data.length);
        for (let i = 0; i < trophyProgress.length; ++i) {
            trophyProgress[i] = {};
        }
        SaveObject.fromJson(JSON.stringify({
            _trophyProgress: trophyProgress
        }));
    }

    public static toJson():string
    {
        // put the current trophy progress into a number array so it can be encoded
        let trophyProgress:Array<any> = new Array<any>(MissionDescriptions.data.length);
        for(let i = 0; i < trophyProgress.length; i++)
        {
            trophyProgress[i] = 
            {
                completed: MissionDescriptions.data[i]._completed,
                goalProgress: MissionDescriptions.data[i]._goalProgress
            }
        }

        let o:any = {
            _trophyProgress: trophyProgress,
        }

        let s:string = JSON.stringify(o);
        //console.log("SaveObject.toJson: " + s);
        return s;
    }
}



export class SaveData
{
    private static readonly _name:string = "1";


    public static load():void
    {
        APIWrapper.loadSaveData(SaveData._name, SaveData.setData);
    }


    private static setData(json: string):void
    {
        if (json)
            SaveObject.fromJson(json);
        else
            console.log("there is no SaveData");
    }


    public static save():void
    {
        APIWrapper.saveSaveData(SaveData._name, SaveObject.toJson());
    }

}



