import {SimpleButton} from "./SimpleButton";
import {Audio, Sfx} from "./Audio";
import {Events} from "../const/Events";
import {GameSettings} from "../settings/GameSettings";
import {Inventory} from "../util/Inventory";
import {APIWrapper} from "../util/APIWrapper";



export class ExtraLives extends PIXI.Container
{
    private _parent: PIXI.Container;
    private _emitter: PIXI.utils.EventEmitter;

    private _background: PIXI.Sprite;

    private _titleTextStyle: PIXI.TextStyle;
    private _titleText: PIXI.Text;
    private _boostTextStyle: PIXI.TextStyle;
    private _boostText: PIXI.Text;
    private _boostCountTextStyle: PIXI.TextStyle;
    private _boostCountText: PIXI.Text;
    private _descriptionTextStyle: PIXI.TextStyle;
    private _descriptionText: PIXI.Text;

    private _buttonTextStyle: PIXI.TextStyle;
    // private _closeButton: SimpleButton;
    private _endButton: SimpleButton;
    private _useButton: SimpleButton;
    private _blanker: PIXI.Graphics;

    private _onUseLives: Function;
    private _onClose: Function;



    constructor(parent: PIXI.Container, emitter: PIXI.utils.EventEmitter, onUseLives: Function, onClose: Function)
    {
        super();
        this._parent = parent;
        this._emitter = emitter;
        this._onUseLives = onUseLives;
        this._onClose = onClose;

        this.createTextStyles();
        this.createLayout();

        this.resize();
        this.tween(true);
        this.visible = true;
    }


    private createTextStyles():void
    {
        this._buttonTextStyle = new PIXI.TextStyle({
            fontFamily: "GrilledCheese",
            fontSize: 30,
            fontWeight: 'bold',
            fill: ['#000000'],
            dropShadow: false,
            align: 'center',
            leading: -8
        });
        this._titleTextStyle = new PIXI.TextStyle({
            fontFamily: "GrilledCheese",
            fontSize: 80,
            fontWeight: 'bold',
            fill: ['#cfcfff', '#9f9fef'],
            stroke: '#000000',
            wordWrap: true,
            wordWrapWidth: 500,
            dropShadow: true,
            dropShadowAlpha: 0.5,
            dropShadowAngle: Math.PI / 2,
            dropShadowBlur: 10,
            dropShadowColor: 'black',
            dropShadowDistance: 3,            
            align: 'center',
            leading: -8
        });
        this._boostTextStyle = new PIXI.TextStyle({
            fontFamily: "GrilledCheese",
            fontSize: 50,
            fontWeight: 'bold',
            fill: ['#cfcfff', '#9f9fef'],
            stroke: '#000000',
            dropShadow: true,
            dropShadowAlpha: 0.5,
            dropShadowAngle: Math.PI / 2,
            dropShadowBlur: 10,
            dropShadowColor: 'black',
            dropShadowDistance: 3,            
            align: 'center',
            leading: -8
        });
        this._boostCountTextStyle = new PIXI.TextStyle({
            fontFamily: "GrilledCheese",
            fontSize: 50,
            fontWeight: 'bold',
            fill: ['#ffff1f', '#af9f0f'],
            stroke: '#000000',
            dropShadow: true,
            dropShadowAlpha: 0.5,
            dropShadowAngle: Math.PI / 2,
            dropShadowBlur: 10,
            dropShadowColor: 'black',
            dropShadowDistance: 3,            
            align: 'center',
            leading: -8
        });
        this._descriptionTextStyle = new PIXI.TextStyle({
            fontFamily: "GrilledCheese",
            fontSize: 50,
            fontWeight: 'bold',
            fill: ['#ffdf0f'],
            stroke: '#000000',
            wordWrap: true,
            wordWrapWidth: 500,
            dropShadow: true,
            dropShadowAlpha: 0.5,
            dropShadowAngle: Math.PI / 2,
            dropShadowBlur: 10,
            dropShadowColor: 'black',
            dropShadowDistance: 3,            
            align: 'center',
            leading: -8
        });
    }

    private createLayout():void
    {
        let _that:ExtraLives = this;

        // the blanker for boost highlights, goes behind the play area and in front of the _ui
        this._blanker = new PIXI.Graphics();
        this._blanker.beginFill(0x000000, 0.5);
        this._blanker.drawRect(0, 0, 32, 32);
        this._blanker.endFill();
        this._blanker.visible = true;
        this._blanker.interactive = true;
        this._blanker.buttonMode = false;
        this._blanker.on(Events.CLICKED, function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); });
        this._blanker
                .on('pointerdown', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('pointerup', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('pointerupoutside', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('pointermove', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('mouseover', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('mouseout', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); });

        this._background = new PIXI.Sprite(PIXI.Texture.fromImage("popups/Missions/Missions_options_quit_panel_grey"));
        this._background.tint = 0xdf6fdf;
        this._background.anchor.set(0.5);
        this._background.interactive = false;
        this._background.buttonMode = false;

        // close button
        // this._closeButton = new SimpleButton(PIXI.Texture.fromImage("popups/Close_all/close_btn_up"));
        // this._closeButton.events.on(Events.CLICKED, this.onCloseButtonClicked.bind(this));
        // this._closeButton.setSfx(Sfx.button_rollover, Sfx.button_press);
        // this._closeButton.x = 340;
        // this._closeButton.y = -245;

        // title text
        this._titleText = new PIXI.Text("You ran out of moves!", this._titleTextStyle);
        this._titleText.anchor.set(0.5, 0);
        this._titleText.x = 0;
        this._titleText.y = -300;

        this._boostText = new PIXI.Text("\"Extra Life\" BOOST", this._boostTextStyle);
        this._boostText.anchor.set(0, 0.5);
        this._boostText.x = -300;
        this._boostText.y = -50;
        this._boostCountText = new PIXI.Text(Inventory.powerup_extra_life.toString(), this._boostCountTextStyle);
        this._boostCountText.anchor.set(1, 0.5);
        this._boostCountText.x = 300;
        this._boostCountText.y = -50;

        this._emitter.on( "show_extra_lives", function( response )
            {
                Inventory.load();
                _that._boostCountText.text = Inventory.powerup_extra_life.toString();
            });

        this._descriptionText = new PIXI.Text("Use an \"Extra Life\" boost to get 5 more moves!", this._descriptionTextStyle);
        this._descriptionText.anchor.set(0.5);
        this._descriptionText.x = 0;
        this._descriptionText.y = 150;

        // buttons
        this._endButton = new SimpleButton(PIXI.Texture.fromImage("popups/Red_btn"));
        this._endButton.events.on(Events.CLICKED, this.onCloseButtonClicked.bind(this));
        this._endButton.addLabel("END GAME", this._buttonTextStyle);
        this._endButton.scale.set(1.0);
        this._endButton.setSfx(Sfx.button_rollover, Sfx.button_press);
        this._endButton.x = -200;
        this._endButton.y = 325;
        this._useButton = new SimpleButton(PIXI.Texture.fromImage("popups/Green_btn"));
        this._useButton.events.on(Events.CLICKED, this.onUseButtonClicked.bind(this));
        this._useButton.addLabel("USE EXTRA\nLIFE", this._buttonTextStyle);
        this._useButton.scale.set(1.0);
        this._useButton.setSfx(Sfx.button_rollover, Sfx.button_press);
        this._useButton.x = 200;
        this._useButton.y = 325;

        // create hierarchy
        this.addChild(this._blanker);
        // this._background.addChild(this._closeButton);
        this._background.addChild(this._titleText);
        this._background.addChild(this._boostText);
        this._background.addChild(this._boostCountText);
        this._background.addChild(this._descriptionText);
        this._background.addChild(this._useButton);
        this._background.addChild(this._endButton);
        this.addChild(this._background);
        this._parent.addChild(this);

        this.resize();
    }


    private tween(appear: boolean, onComplete?: any):void
    {
        //console.log("tween ExtraLives " + appear);
        if (appear)
        {
            Audio.playSfx(Sfx.no_more_moves_panel_open);
            this.resize();
            this.visible = true;

            this._background.x = this._background.y = 0;
            this._background.scale.set(2.0, 0.5);

            let bounceHalfTime = 200;
            let bounceTween = new TWEEN.Tween(this._background.scale);
            bounceTween.to({x: 1.0, y: 1.0}, bounceHalfTime);
            bounceTween.easing(TWEEN.Easing.Quadratic.InOut);

            let overshootTween = new TWEEN.Tween(this._background.scale);
            overshootTween.to({x: 0.85, y: 1.15}, bounceHalfTime);
            overshootTween.easing(TWEEN.Easing.Quadratic.Out);

            let overshoot2Tween = new TWEEN.Tween(this._background.scale);
            overshoot2Tween.to({x: 1.05, y: 0.95}, bounceHalfTime);
            overshoot2Tween.easing(TWEEN.Easing.Quadratic.Out);

            let settleTween = new TWEEN.Tween(this._background.scale);
            settleTween.to({x: 1.0, y: 1.0}, bounceHalfTime);
            settleTween.easing(TWEEN.Easing.Quadratic.Out);

            bounceTween.chain(overshootTween);
            overshootTween.chain(overshoot2Tween);
            overshoot2Tween.chain(settleTween);
            bounceTween.start();
        }
        else
        {
            Audio.playSfx(Sfx.no_more_moves_panel_close);
            this.resize();
            let flyTween = new TWEEN.Tween(this._background);
            flyTween.to({x:-2000}, 150);
            flyTween.start();
            if (onComplete)
            {
                flyTween.onComplete(onComplete);
            }
        }
    }


    private onCloseButtonClicked():void
    {
        this.tween(false, this._onClose.bind(this));
    }


    private onUseButtonClicked():void
    {
        // fix powerup_extra_life in case it somehow became negative
        if (Inventory.powerup_extra_life < 0)
        {
            Inventory.powerup_extra_life = 0;
        }

        var that = this;
        if (Inventory.powerup_extra_life > 0)
        {
            if (GameSettings.consoleLogging)
                console.log("Use extra life...");

            // update Pogo when a life is used
            APIWrapper.consumePowerUp("powerup_extra_life", Inventory.powerup_extra_life, function(response)
                {
                    if (GameSettings.consoleLogging)
                        console.log("Use extra life, consumed... " + JSON.stringify(response.stats));

                    if (response.stats.length > 0)
                    {
                        Inventory.powerup_extra_life = response.stats[0].value;
                        that._emitter.emit("use_extra_lives");

                        if (response.stats[0].failStatus)
                        {
                            if (GameSettings.consoleLogging)
                                console.log("Use extra life, failed: " + response.stats[0].failStatus);
                            APIWrapper.purchaseExtraLives(function(response)
                                {
                                    that._emitter.emit("show_extra_lives");
                                });
                            return;
                        }
                    }
                    else
                    {
                        if (GameSettings.consoleLogging)
                            console.log("Use extra life, response incorrect!");
                        APIWrapper.purchaseExtraLives(function(response)
                            {
                                that._emitter.emit("show_extra_lives");
                            });
                        return;
                    }

                    that.tween(false, that._onUseLives.bind(that));
                });
        }
        else
        {
            if (GameSettings.consoleLogging)
                console.log("Buy Extra Lives...");

            // API: open the store to allow purchase of more lives, returns to this popup
            APIWrapper.purchaseExtraLives(function(response)
                {
                    that._emitter.emit("show_extra_lives");
                });
        }
    }


    private onEndButtonClicked():void
    {
        this.tween(false, this._onClose.bind(this));
    }


    public destroy():void
    {
        this._blanker.removeAllListeners();
        this._blanker.destroy();
        this._titleText.destroy();
        this._boostText.destroy();
        this._boostCountText.destroy();
        this._descriptionText.destroy();
        // this._closeButton.destroy();
        this._endButton.destroy();
        this._useButton.destroy();
        this._background.destroy();
        this._parent.removeChild(this);
        this._parent = null;
        super.destroy();
    }


    public resize():void
    {
        this.x = GameSettings.width / 2;
        this.y = GameSettings.height / 2;

        let scale:number = Math.min(GameSettings.width / this._background.texture.width * 0.90, GameSettings.height / this._background.texture.height * 0.90);
        this.scale.set(scale);

        this._blanker.x = -GameSettings.width / 2 / scale;
        this._blanker.y = -GameSettings.height / 2 / scale;
        this._blanker.width = GameSettings.width / scale;
        this._blanker.height = GameSettings.height / scale;

        // this._endButton.resize(scale);
        // this._useButton.resize(scale);
    }

}



