import {Game} from "../Game";
import {GameSettings} from "../settings/GameSettings";
import {PieceAnimations, PieceColours} from "../const/Enums";
import {BoardInput} from "./BoardInput";
import {PlayArea} from "./PlayArea";
import {AnimatedSprite} from "../ui/AnimatedSprite";
import {TextureData} from "../ui/AnimationData";
import {FloatingTexts, FloatingTextType} from "../ui/FloatingTexts";
import {Effects, EffectType} from "../ui/Effects";
import {Audio, Sfx} from "../ui/Audio";



export class Piece extends PIXI.Container
{
    public static readonly LargestDollSize: number = 2;
    private static readonly FallSpeed: number = 24;

    public static _anyMove: boolean;

    public _colourIndex: number;        // blue, green, yellow = 0, 1, 2 (from PlayArea.AnimationNames)
    public _sizeIndex: number;
    public _offset: PIXI.Container;
    public _row: number;
    public _col: number;
    public _isFalling: boolean;
    public _onTop: boolean;

    private _emitter: PIXI.utils.EventEmitter;
    private _anim: AnimatedSprite;
    private _eventData: PIXI.interaction.InteractionData;
    private _frames: Array<Array<Array<TextureData>>>;

    public _leftTab: number;
    public _topTab: number;
    private _isDropping: boolean;
    private _callAnim: Array<Function>;



    // the doll graphics are not centred and the base is also wrong
    // manually calculated offsets to correct these deficiencies...
    private static readonly _centreCorrection: Array<PIXI.Point> = 
    [
        new PIXI.Point(15, 26), new PIXI.Point(11, 35), new PIXI.Point(20, 70)
    ];



    public constructor(emitter: PIXI.utils.EventEmitter, frames: Array<Array<Array<TextureData>>>, leftTab: number, topTab: number)
    {
        super();
        this._emitter = emitter;
        this._frames = frames;
        this._leftTab = leftTab;
        this._topTab = topTab;

        this._colourIndex = PieceColours.Blue;
        this._sizeIndex = 0;
        this._eventData = null;
        this._offset = new PIXI.Container;
        this._isFalling = false;
        this._isDropping = false;
        this._onTop = false;
        this._callAnim = new Array<Function>();

        this.addChild(this._offset);
    }


    public create(x: number, y: number, pieceCode: string, drop?: boolean):void
    {
        // remember position in the board
        this._row = y;
        this._col = x;
        this.x = x * GameSettings.BoardSquareWide + this._leftTab + GameSettings.BoardSquareWide / 2;
        this.y = y * GameSettings.BoardSquareHigh + this._topTab + GameSettings.BoardSquareHigh / 2;

        let colours: any = {
            y: PieceColours.Yellow,
            g: PieceColours.Green,
            b: PieceColours.Blue
        };
        let sizes: any = {
            s: 0,
            m: 1, 
            l: 2
        }
        let colour: string = pieceCode.charAt(0);
        let size: string = pieceCode.charAt(1);
        this._colourIndex = colours[colour];
        this._sizeIndex = sizes[size];
        let frame: number = this._colourIndex * 3 + this._sizeIndex;
        this.show();

        if (drop)
        {
            // drop in from one square above
            this._offset.y = -GameSettings.BoardSquareHigh / BoardInput.getGlobalScale(this).y;
            this._isDropping = true;
        }
    }


    public destroy():void
    {
        this.removeChild(this._offset);
        this._eventData = null;
        this._frames = null;
        
        this._anim.destroy();
        this._anim = null;

        super.destroy();
    }


    private sign(value: number): number
    {
        return ((value < 0) ? -1:((value > 0) ? 1:0));
    }


    public update(_board: PIXI.Container):void
    {
        // process list of callback delegates
        if (this._callAnim.length > 0)
        {
            // if they return true, they should be removed from the list
            for(let i: number = this._callAnim.length - 1; i >= 0; --i)
                if (this._callAnim[i].call(this))
                    this._callAnim.splice(i, 1);
        }

        if (this._isDropping)
        {
            if (this.fallToSquare(BoardInput.getGlobalScale(this.parent)))
            {
                this._isDropping = false;
            }
        }
        else if (!this._isFalling && !BoardInput.isDragging())
        {
            // this piece is merging into an open doll

            let moved: boolean = false;
            if (this._offset.x != 0)
            {
                // calculate distance to reduce x to 80% at 60hz, then scale for actual framerate
                let dx:number = this._offset.x - this._offset.x * 0.80 / (Game._elapsed / 16.666667);
                this._offset.x -= dx;
                if (Math.abs(this._offset.x) < 1)
                    this._offset.x = 0;
                moved = true;
            }
            if (this._offset.y != 0)
            {
                let dy:number = this._offset.y - this._offset.y * 0.80 / (Game._elapsed / 16.666667);
                this._offset.y -= dy;
                if (Math.abs(this._offset.y) < 1)
                    this._offset.y = 0;
                moved = true;
            }

            if (moved)
            {
                // shrink as we merge
                let scale: PIXI.Point = BoardInput.getGlobalScale(_board);
                if (this.scale.x > 1.0 / scale.x * 0.75)
                    this.scale.set(this.scale.x * 0.90);
                Piece._anyMove = true;
            }
        }
    }


    public resize(_board: PIXI.Container):void
    {
        let scale: PIXI.Point = BoardInput.getGlobalScale(_board);
        this.scale.set(1.0 / scale.x, 1.0 / scale.y);
        this._offset.scale.set(scale.x, scale.y);
    }


    // apply offset, then jump my location to the final desired position
    // (use the offset auto-zeroing in the update() function to move)
    public move(dx: number, dy: number):void
    {
        //console.log("move " + dx + "," + dy + " from " + this._col + "," + this._row);
        this._offset.x -= dx * GameSettings.BoardSquareWide;
        this.x += dx * GameSettings.BoardSquareWide;
        // merge to the middle of the target piece (move upwards as part of the merge)
        let target: number = dy * GameSettings.BoardSquareHigh - [16, 20, 48][this._sizeIndex];
        this._offset.y -= target;
        this.y += target;
    }


    // apply falling velocity until offset reaches the next square
    // then jump the piece into that square and return true on arrival
    public fallToSquare(boardScale: PIXI.Point):boolean
    {
        this._offset.y += Piece.FallSpeed * boardScale.y;

        let target:number = GameSettings.BoardSquareHigh * boardScale.y;
        if (this._isDropping)
            target = 0;

        if (this._offset.y >= target)
        {
            this._offset.y = 0;
            // dropping pieces are already set with their final row value
            if (!this._isDropping)
            {
                this.y += GameSettings.BoardSquareHigh;
                this._row++;
            }
            return true;
        }
        return false;
    }


    public setOffset(x: number, y: number):void
    {
        this._offset.x = x;
        this._offset.y = y;
    }


    public applyOffset(offsetScale: PIXI.Point, snapx: boolean, snapy: boolean):void
    {
        this.x += this._offset.x / offsetScale.x;
        if (snapx)
        {
            let x: number = this.x - this._leftTab - GameSettings.BoardSquareWide / 2;
            x = Math.round(x / GameSettings.BoardSquareWide) * GameSettings.BoardSquareWide;
            this.x = x + this._leftTab + GameSettings.BoardSquareWide / 2;
        }
        this.y += this._offset.y / offsetScale.y;
        if (snapy)
        {
            let y: number = this.y - this._topTab - GameSettings.BoardSquareHigh / 2;
            y = Math.round(y / GameSettings.BoardSquareHigh) * GameSettings.BoardSquareHigh;
            this.y = y + this._topTab + GameSettings.BoardSquareHigh / 2;
        }
        this._offset.x = 0;
        this._offset.y = 0;
    }


    public toBoard(applyChanges: boolean, useOffset?: boolean, offsetScale?: PIXI.Point):PIXI.Point
    {
        let x: number = this.x - this._leftTab - GameSettings.BoardSquareWide / 2;
        let y: number = this.y - this._topTab - GameSettings.BoardSquareHigh / 2;
        if (useOffset == true)
        {
            x += this._offset.x / offsetScale.x;
            y += this._offset.y / offsetScale.y;
        }
        
        // convert pixel position of this piece into the nearest board location, wrap around at edges
        let col: number = Math.round(x / GameSettings.BoardSquareWide);
        while (col < 0) col += GameSettings.BoardWide;
        while (col >= GameSettings.BoardWide) col -= GameSettings.BoardWide;
        let row: number = Math.round(y / GameSettings.BoardSquareHigh);
        while (row < 0) row += GameSettings.BoardHigh;
        while (row >= GameSettings.BoardHigh) row -= GameSettings.BoardHigh;

        if (applyChanges)
        {
            this._col = col;
            this._row = row;
        }
        return new PIXI.Point(col, row);
    }


    public matches(p: Piece):boolean
    {
        return (p._colourIndex == this._colourIndex)
                && (p._sizeIndex == this._sizeIndex);
    }


    public grow(onEnd?: Function, growScore?: boolean):boolean
    {
        let _that: Piece = this;

        // make this piece grow
        if (this._sizeIndex < Piece.LargestDollSize)
        {
            this._sizeIndex++;
            this.show();
            // make the larger replacement piece drop with dust into place
            this.setAnim(PieceAnimations.DropDown, false, function()
                {   // context is the AnimatedSprite of this Piece
                    _that.setAnim(PieceAnimations.Wait);
                    if (growScore)
                    {
                        let score:number = GameSettings.MegaMatchGrowScore * _that._sizeIndex;
                        FloatingTexts.add(FloatingTextType.BigScore, score.toString(), _that.x, _that.y, 0);
                        Effects.addCount(6, _that.parent, EffectType.BigScoreStar, _that.x, _that.y - 70, 0);
                        Audio.playSfx(Sfx.stars_animation);
                        _that._emitter.emit('match_score_add', score);
                    }
                    if (onEnd)
                        onEnd.call(_that);
                });
            return true;
        }
        // already full-size
        if (growScore)
        {
            // bounce and score without growing further
            this.setAnim(PieceAnimations.DropDown, false, function()
                {   // context is the AnimatedSprite of this Piece
                    _that.setAnim(PieceAnimations.Wait);
                    let score:number = GameSettings.MegaMatchGrowScore * (_that._sizeIndex + 1);
                    FloatingTexts.add(FloatingTextType.BigScore, score.toString(), _that.x, _that.y, 0);
                    Effects.addCount(6, _that.parent, EffectType.BigScoreStar, _that.x, _that.y - 70, 0);
                    Audio.playSfx(Sfx.stars_animation);
                    _that._emitter.emit('match_score_add', score);
                });
        }
        // mega-match!
        return false;
    }


    public recolour(ci: number):void
    {
        this._colourIndex = ci;
        this.show();
    }


    private show():void
    {
        let type: number = this._colourIndex * 3 + this._sizeIndex;
        if (!this._anim)
        {
            this._anim = new AnimatedSprite(this._frames[type]);
            this._anim.anchor.set(0.5, 1.0);
            this._offset.addChild(this._anim);
        }

        this._anim.x = Piece._centreCorrection[this._sizeIndex].x;
        this._anim.y = Piece._centreCorrection[this._sizeIndex].y;

        this._anim.setTextures(this._frames[type]);
        this._anim.setAnim(PieceAnimations.Wait);
    }


    public setAnim(animation: number, looping?: boolean, onEnd?: Function):void
    {
        // do not set any animation to replace the dragging 'up' animation except the dropping 'down' animation
        if ((this._anim._animation != PieceAnimations.DragUp)
            || (animation == PieceAnimations.DropDown))
        {
            //console.log("setAnim " + animation + " at " + this._col + "," + this._row);
            this._anim.setAnim(animation, looping, onEnd);
        }
    }


    public setAnimWithDelay(animation: number, delay: number, looping?: boolean, onEnd?: Function):void
    {
        let timeToPlay: number = Game._now + delay;
        this._callAnim.push(function():boolean
            {
                if (Game._now > timeToPlay)
                {
                    this.setAnim(animation, looping, onEnd);
                    return true;
                }
                return false;
            });
    }
  

    // get location including the AnimationSprite local position
    public getX():number
    {
        return this.x - this._anim.x;
    }


    public getAnimatedSprite():AnimatedSprite
    {
        return this._anim;
    }


    public startDragAnim():void
    {
        if (this._anim._animation != PieceAnimations.DragUp)
        {
            this.setAnim(PieceAnimations.DragUp, false);
        }
    }


    public endDragAnim():void
    {
        if (this._anim._animation != PieceAnimations.DropDown)
        {
            this.setAnim(PieceAnimations.DropDown, false, function() { this.setAnim(PieceAnimations.Wait); });
        }
    }


    public setHoverAnim(start: boolean):void
    {
        if (start)
        {
            if (this._anim._animation == PieceAnimations.Wait)
            {
                this.setAnim(PieceAnimations.Hovered, false);
            }
        }
        else
        {
            if (this._anim._animation == PieceAnimations.Hovered)
            {
                this.setAnim(PieceAnimations.Wait, false);
            }
        }
    }


    public isOpened():boolean
    {
        return (this._anim._animation == PieceAnimations.StackOpenWait);
    }


    public isOpening():boolean
    {
        return (this._anim._animation == PieceAnimations.StackOpen);
    }


    public isOpenEnoughToMerge():boolean
    {
        return (this._anim._animation == PieceAnimations.StackOpen && this._anim.currentFrame >= 2);
    }


    public isClosing():boolean
    {
        return (this._anim._animation == PieceAnimations.StackClose);
    }


    public closeTriggersRemove(frames: number = 4):boolean
    {
        // at 'frames' from the end of the close animation, we trigger the rest of the group's removal
        // (the end of the animation squashes down, they will show over the top if left active)
        return (this._anim._animation == PieceAnimations.StackClose && this._anim.currentFrame > this._anim.totalFrames - frames);
    }


    public isWaiting():boolean
    {
        return (this._anim._animation == PieceAnimations.Wait);
    }


    public isMoving():boolean
    {
        return (this._offset.x != 0 || this._offset.y != 0);
    }

};


