import {SimpleButton} from "./SimpleButton";
import {CheckBox} from "./CheckBox";
import {ThumbSlider} from "./ThumbSlider";
import {Audio, Sfx} from "./Audio";
import {GameSettings} from "../settings/GameSettings";
import {Events} from "../const/Events";
import {LoadSave} from "../util/LoadSave";



export class Options extends PIXI.Container
{
    private _parent: PIXI.Container;
    private _emitter: PIXI.utils.EventEmitter;

    private _blanker: PIXI.Graphics;
    private _background: PIXI.Sprite;

    private _closeButton: SimpleButton;

    private _soundTextStyle: PIXI.TextStyle;

    private _sfxText: PIXI.Text;
    private _sfxMute: CheckBox;
    private _sfxSlider: ThumbSlider;
    private _sfxTick: PIXI.Sprite;

    private _musicText: PIXI.Text;
    private _musicMute: CheckBox;
    private _musicSlider: ThumbSlider;
    private _musicTick: PIXI.Sprite;

    private _helpButton: SimpleButton;
    private _helpTextStyle: PIXI.TextStyle;
    private _tutorialText: PIXI.Sprite;
    private _tutorialButton: CheckBox;
    private _okButton: SimpleButton;
    private _okTextStyle: PIXI.TextStyle;

    private _visibleWhenHelpClosed: boolean;



    constructor(parent: PIXI.Container, emitter: PIXI.utils.EventEmitter)
    {
        super();
        this._parent = parent;
        this._emitter = emitter;

        this._visibleWhenHelpClosed = false;

        // the blanker, goes behind the popup
        this._blanker = new PIXI.Graphics();
        this._blanker.beginFill(0x000000, 0.75);
        this._blanker.drawRect(0, 0, 256, 256);
        this._blanker.endFill();
        this._blanker.visible = true;
        this._blanker.interactive = true;
        this._blanker.buttonMode = false;
        this._blanker.on(Events.CLICKED, function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); });
        this._blanker
                .on('pointerdown', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('pointerup', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('pointerupoutside', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('pointermove', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('mouseover', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); })
                .on('mouseout', function(e: PIXI.interaction.InteractionEvent) { e.stopPropagation(); });
        this.addChild(this._blanker);

        this.createTextStyles();
        this.createLayout();

        let _that: Options = this;
        this._emitter.on("show_settings", function()
            {
                _that.resize();
                _that.visible = true;
                _that.tween(true);
            });
        this._emitter.on("close_help", function()
            {
                _that.resize();
                _that.visible = _that._visibleWhenHelpClosed;
                if (_that.visible) _that.tween(true);
                _that._visibleWhenHelpClosed = false;
                _that.showTutorialButton();    // there is another tutorial button in the Help screen, state may have changed
            });

        this.visible = false;
    }


    private createTextStyles():void
    {
        this._helpTextStyle = new PIXI.TextStyle({
            fontFamily: "GrilledCheese",
            fontSize: 44,
            fontWeight: 'bold',
            fill: ['#000000'],
            dropShadow: false,
            align: 'center',
            leading: -8
        });
        this._okTextStyle = new PIXI.TextStyle({
            fontFamily: "GrilledCheese",
            fontSize: 60,
            fontWeight: 'bold',
            fill: ['#000000'],
            dropShadow: false,
            align: 'center',
            leading: -8
        });
        this._soundTextStyle = new PIXI.TextStyle({
            fontFamily: "GrilledCheese",
            fontSize: 60,
            fontWeight: 'bold',
            fill: ['#6fff1f', '#bfbf3f'],
            stroke: '#000000',
            dropShadow: true,
            dropShadowAlpha: 0.5,
            dropShadowAngle: Math.PI / 2,
            dropShadowBlur: 10,
            dropShadowColor: 'black',
            dropShadowDistance: 3,            
            align: 'center',
            leading: -8
        });
    }

    private createLayout():void
    {
        let _that:Options = this;

        this._background = new PIXI.Sprite(PIXI.Texture.fromImage("popups/Missions/Missions_options_quit_panel_grey"));
        this._background.tint = 0x3f8fff;    //0x8fff3f;
        this._background.anchor.set(0.5);
        this._background.interactive = false;

        // close button
        this._closeButton = new SimpleButton(PIXI.Texture.fromImage("popups/Close_all/close_btn_up"));
        this._closeButton.events.on(Events.CLICKED, this.onCloseButtonClicked.bind(this));
        this._closeButton.setSfx(Sfx.button_rollover, Sfx.button_press);
        this._closeButton.x = 340;
        this._closeButton.y = -245;

        // sound controls
        this._sfxText = new PIXI.Text("SOUND", this._soundTextStyle);
        this._sfxText.anchor.set(0.5);
        this._sfxText.x = 0;
        this._sfxText.y = -215;

        this._sfxMute = new CheckBox(PIXI.Texture.fromImage("popups/Check_mark/Invisible-Check-Mark_bg"), !GameSettings.sfxMute);
        this._sfxMute.events.on(Events.CLICKED, this.onSfxButtonClicked.bind(this));
        this._sfxMute.setSfx(Sfx.button_rollover, Sfx.button_press);
        this._sfxMute.x = this._sfxText.x + 200;
        this._sfxMute.y = this._sfxText.y;

        this._sfxSlider = new ThumbSlider(PIXI.Texture.fromImage("popups/Options/SliderBar"), PIXI.Texture.fromImage("popups/Options/SliderDragger"),
                        function(value) { GameSettings.sfxVolume = value * 100; Audio.set(); Audio.playSfx(Sfx.button_rollover); });
        this._sfxSlider.anchor.set(0.5);
        this._sfxSlider.x = 0;
        this._sfxSlider.y = this._sfxText.y + 70;
        this._sfxSlider.set(GameSettings.sfxVolume / 100);

        this.setSfxWithMute();

        // music controls
        this._musicText = new PIXI.Text("MUSIC", this._soundTextStyle);
        this._musicText.anchor.set(0.5);
        this._musicText.x = 0;
        this._musicText.y = -65;

        this._musicMute = new CheckBox(PIXI.Texture.fromImage("popups/Check_mark/Invisible-Check-Mark_bg"), !GameSettings.musicMute);
        this._musicMute.events.on(Events.CLICKED, this.onMusicButtonClicked.bind(this));
        this._musicMute.setSfx(Sfx.button_rollover, Sfx.button_press);
        this._musicMute.x = this._musicText.x + 200;
        this._musicMute.y = this._musicText.y;

        this._musicSlider = new ThumbSlider(PIXI.Texture.fromImage("popups/Options/SliderBar"), PIXI.Texture.fromImage("popups/Options/SliderDragger"),
                        function(value) { GameSettings.musicVolume = value * 100; Audio.set(); });
        this._musicSlider.anchor.set(0.5);
        this._musicSlider.x = 0;
        this._musicSlider.y = this._musicText.y + 70;
        this._musicSlider.set(GameSettings.musicVolume / 100);

        this.setMusicWithMute();

        // help button
        this._helpButton = new SimpleButton(PIXI.Texture.fromImage("popups/Green_btn"));
        this._helpButton.events.on(Events.CLICKED, this.onHelpButtonClicked.bind(this));
        this._helpButton.addLabel("HELP", this._helpTextStyle);
        this._helpButton.setSfx(Sfx.button_rollover, Sfx.button_press);
        this._helpButton.x = 0;
        this._helpButton.y = 145;

        // tutorial text
        this._tutorialText = new PIXI.Sprite(PIXI.Texture.fromImage("popups/How_to_play/Show-tutorial-at-start"));
        this._tutorialText.anchor.set(0.5);
        this._tutorialText.x = -30;
        this._tutorialText.y = 245;

        this.showTutorialButton();

        // ok button
        this._okButton = new SimpleButton(PIXI.Texture.fromImage("popups/Small_Green_btn"));
        this._okButton.events.on(Events.CLICKED, this.onCloseButtonClicked.bind(this));
        this._okButton.addLabel("OK", this._okTextStyle);
        this._okButton.setSfx(Sfx.button_rollover, Sfx.button_press);
        this._okButton.x = 0;
        this._okButton.y = 345;

        // create hierarchy
        this._background.addChild(this._closeButton);
        this._background.addChild(this._sfxText);
        this._background.addChild(this._sfxMute);
        this._background.addChild(this._sfxSlider);
        this._background.addChild(this._musicText);
        this._background.addChild(this._musicMute);
        this._background.addChild(this._musicSlider);
        this._background.addChild(this._helpButton);
        this._background.addChild(this._tutorialText);
        this._background.addChild(this._tutorialButton);
        this._background.addChild(this._okButton);
        this.addChild(this._background);
        this._parent.addChild(this);
    }


    private showTutorialButton():void
    {
        if (this._tutorialButton)
        {
            this._background.removeChild(this._tutorialButton);
            this._tutorialButton.events.off(Events.CLICKED, this.onTutorialButtonClicked.bind(this));
            this._tutorialButton.destroy();
        }

        this._tutorialButton = new CheckBox(PIXI.Texture.fromImage("popups/Check_mark/Check-Mark_bg"), GameSettings.showTutorial);
        this._tutorialButton.events.on(Events.CLICKED, this.onTutorialButtonClicked.bind(this));
        this._tutorialButton.setSfx(Sfx.button_rollover, Sfx.button_press);
        this._tutorialButton.x = this._tutorialText.x + 310;
        this._tutorialButton.y = this._tutorialText.y;
        this._background.addChild(this._tutorialButton);
    }


    private tween(appear: boolean, onComplete?: any):void
    {
        //console.log("tween options " + appear);
        if (appear)
        {
            Audio.playSfx(Sfx.option_panel_open);
            this.resize();
            this._background.x = this._background.y = 0;
            this._background.scale.set(2.0, 0.5);

            let bounceHalfTime = 100;
            let bounceTween = new TWEEN.Tween(this._background.scale);
            bounceTween.to({x: 1.0, y: 1.0}, bounceHalfTime);
            bounceTween.easing(TWEEN.Easing.Quadratic.InOut);

            let overshootTween = new TWEEN.Tween(this._background.scale);
            overshootTween.to({x: 0.85, y: 1.15}, bounceHalfTime);
            overshootTween.easing(TWEEN.Easing.Quadratic.Out);

            let overshoot2Tween = new TWEEN.Tween(this._background.scale);
            overshoot2Tween.to({x: 1.05, y: 0.95}, bounceHalfTime);
            overshoot2Tween.easing(TWEEN.Easing.Quadratic.Out);

            let settleTween = new TWEEN.Tween(this._background.scale);
            settleTween.to({x: 1.0, y: 1.0}, bounceHalfTime);
            settleTween.easing(TWEEN.Easing.Quadratic.Out);

            bounceTween.chain(overshootTween);
            overshootTween.chain(overshoot2Tween);
            overshoot2Tween.chain(settleTween);
            bounceTween.start();
        }
        else
        {
            // NOTE: deliberate use of 'open' instead of 'close' here, this sound is a better fit for the tween
            Audio.playSfx(Sfx.option_panel_open);
            this.resize();
            let flyTween = new TWEEN.Tween(this._background);
            flyTween.to({x:-2000}, 150);
            flyTween.start();
            if (onComplete)
            {
                flyTween.onComplete(onComplete);
            }
        }
    }


    private onCloseButtonClicked():void
    {
        let _that: Options = this;
        this.tween(false, function()
        {
            _that.visible = false;
        });
        // save all settings when Options screen is closed
        LoadSave.save();
    }


    private onHelpButtonClicked():void
    {
        this.visible = false;
        this._visibleWhenHelpClosed = true;
        this._emitter.emit("show_help");
        // save all settings when Options screen is navigated away from
        LoadSave.save();
    }


    private onTutorialButtonClicked():void
    {
        GameSettings.showTutorial = this._tutorialButton.toggle();
    }


    private onSfxButtonClicked():void
    {
        GameSettings.sfxMute = !this._sfxMute.toggle();
        this.setSfxWithMute();
    }


    private setSfxWithMute():void
    {
        if (GameSettings.sfxMute)
        {
            this._sfxSlider.set(0, false);
        }
        else
        {
            this._sfxSlider.set(GameSettings.sfxVolume / 100, true);
        }
        Audio.set();
    }


    private onMusicButtonClicked():void
    {
        GameSettings.musicMute = !this._musicMute.toggle();
        this.setMusicWithMute();
    }


    private setMusicWithMute():void
    {
        if (GameSettings.musicMute)
        {
            this._musicSlider.set(0, false);
        }
        else
        {
            this._musicSlider.set(GameSettings.musicVolume / 100, true);
        }
        Audio.set();
    }


    public destroy():void
    {
        this._blanker.removeAllListeners();
        this._emitter.removeAllListeners();
        this._sfxSlider.destroy();
        this._musicSlider.destroy();
        this._blanker.destroy();
        this._closeButton.destroy();
        this._sfxMute.destroy();
        this._musicMute.destroy();
        this._helpButton.destroy();
        this._okButton.destroy();
        this._tutorialButton.destroy();
        this._parent.removeChild(this);
        this._parent = null;
        super.destroy();
    }


    public resize():void
    {
        this.x = GameSettings.width / 2;
        this.y = GameSettings.height / 2;

        let scale:number = Math.min(GameSettings.width / this._background.width * 0.90, GameSettings.height / this._background.height * 0.90);
        this.scale.set(scale);

        this._blanker.x = -GameSettings.width / 2 / scale;
        this._blanker.y = -GameSettings.height / 2 / scale;
        this._blanker.width = GameSettings.width / scale;
        this._blanker.height = GameSettings.height / scale;
       
        //this._helpButton.resize(scale);
        //this._tutorialText.scale.set(scale);
        //this._okButton.resize(scale);
    }

    

}



