import {State} from "./State";
import {StateManager} from "./StateManager";
import {StateNames} from "../const/Enums";
import {Events} from "../const/Events";
import {GameSettings} from "../settings/GameSettings";
import {SimpleButton} from "../ui/SimpleButton";
import {Audio, Sfx} from "../ui/Audio";
import {TrophyRoom} from "../ui/TrophyRoom";
import {Pather, PathPoint, PathData} from "../util/Pather";
import {LoadSave} from "../util/LoadSave";
import {Game} from "../Game";
import {APIWrapper} from "../util/APIWrapper";
import {Wincodes} from "../util/Wincodes";



export class TitleScreenState extends State
{
    private static readonly _titlePath =
        [
            new PathPoint(2, new PIXI.Point(0.5,0.00), new PIXI.Point(0.50, 0.50)),
            new PathPoint(2, new PIXI.Point(0.5,0.10), new PIXI.Point(0.50, 0.50)),
            new PathPoint(2, new PIXI.Point(0.5,0.20), new PIXI.Point(0.55, 0.55)),
            new PathPoint(2, new PIXI.Point(0.5,0.30), new PIXI.Point(0.60, 0.60)),
            new PathPoint(2, new PIXI.Point(0.5,0.40), new PIXI.Point(0.65, 0.65)),
            new PathPoint(2, new PIXI.Point(0.5,0.45), new PIXI.Point(0.70, 0.70)),
            new PathPoint(2, new PIXI.Point(0.5,0.45), new PIXI.Point(0.75, 0.75)),
            new PathPoint(2, new PIXI.Point(0.5,0.40), new PIXI.Point(0.80, 0.80)),
            new PathPoint(2, new PIXI.Point(0.5,0.35), new PIXI.Point(0.85, 0.85)),
            new PathPoint(2, new PIXI.Point(0.5,0.30), new PIXI.Point(0.90, 0.90)),
            new PathPoint(2, new PIXI.Point(0.5,0.25), new PIXI.Point(1.00, 1.00)),
            new PathPoint(4, new PIXI.Point(0.5,0.25), new PIXI.Point(1.10, 1.10)),
            new PathPoint(4, new PIXI.Point(0.5,0.25), new PIXI.Point(1.00, 1.00)),
            new PathPoint(4, new PIXI.Point(0.5,0.25), new PIXI.Point(0.95, 0.95)),
            //new PathPoint(4, new PIXI.Point(0.5,0.25), new PIXI.Point(1.00, 1.00)),
        ];

    private _container: PIXI.Container;
    private _titleBackground: PIXI.Sprite;
    private _titleLogo: PIXI.Sprite;
    private _scaleLogo: number;
    private _pather: Pather;

    private _playNow: PIXI.Sprite;
    private _startButton: SimpleButton;

    private _trophyBtn: SimpleButton;
    private _trophyText: PIXI.Text;
    private _trophyStyle: PIXI.TextStyle;

    private _trophyRoom: TrophyRoom;

    
    /**
     * Calls the base State constructor
     * @param {StateManager} controller  A reference to the state controller
     * @param {PIXI.Container} stage        A reference the stage, the main PIXI container
     */
    public constructor(controller: StateManager, stage: PIXI.Container)
    {
        super(controller, stage);
    }
    

    public init():void
    {
        // titles are always before the start of a new game, reset the wincodes ready for a game
        Wincodes.resetWincodesForNewGame();

        Game._stage.emit("show_version_number");
        this._container = new PIXI.Container;

        this._titleBackground = new PIXI.Sprite(PIXI.Texture.fromImage("intro/IntroPanel_back"));
        this._titleBackground.anchor.set(0.5);
        this._titleBackground.interactive = true;
        this._titleBackground.on("touchend", this.startTune);
        this._titleBackground.on("mouseup", this.startTune);

        this._titleLogo = new PIXI.Sprite(PIXI.Texture.fromImage("intro/logo"));
        this._titleLogo.anchor.set(0.5);
        this._titleLogo.x = GameSettings.width * 0.5;
        this._titleLogo.y = -150;
        this._titleLogo.scale.set(0.50);

        this._playNow = new PIXI.Sprite(PIXI.Texture.fromImage("play_now"));
        this._playNow.x = 0;
        this._playNow.y = 0;
        this._playNow.anchor.set(0.5);

        this._startButton = new SimpleButton(PIXI.Texture.fromImage("intro/Blue_btn"));
        this._startButton.events.on(Events.CLICKED, this.onStartButtonClicked.bind(this));
        this._startButton.setSfx(Sfx.button_rollover, Sfx.button_press);

        this._trophyStyle = new PIXI.TextStyle({
            fontFamily: "GrilledCheese",
            fontSize: 36,
            fontWeight: 'bold',
            fill: ['#000000'],
            dropShadow: false,
            align: 'center',
            leading: -8,
            wordWrap: true,
            wordWrapWidth: 140
        });

        this._trophyBtn = new SimpleButton(PIXI.Texture.fromImage("popups/Green_btn"));
        this._trophyBtn.events.on(Events.CLICKED, this.onTrophyButtonClicked.bind(this));
        this._trophyBtn.addLabel("TROPHY ROOM", this._trophyStyle);
        this._trophyBtn.setSfx(Sfx.button_rollover, Sfx.button_press);

        // create display hierarchy
        this._startButton.addChild(this._playNow);
        this._container.addChild(this._titleBackground);
        this._container.addChild(this._startButton);
        this._container.addChild(this._trophyBtn);
        this._container.addChild(this._titleLogo);
        this._stage.addChild(this._container);

        this._scaleLogo = Math.min(GameSettings.width / this._titleLogo.texture.width, GameSettings.height / this._titleLogo.texture.height) * 0.75;
        this._pather = new Pather(this._titleLogo, TitleScreenState._titlePath);
        this._trophyRoom = null;

        this.resize();
        Audio.titleTune();
        
        // show the trophy room if requested to at the end of a game
        // but only if the user is logged in and able to view the trophys...
        // (a recent change prevents the user from seeing the Trophy Room unless they are logged in):
        // https://arkadiumarena.visualstudio.com/Phoenix%20Games/_git/trizzle-hd-pixijs/commit/aee21c9c4796b1a0523c423e72bea80da89d107c?_a=compare&path=%2Fsrc%2Fcode%2Fstate%2FTitleScreenState.ts
        if (GameSettings.showTrophyRoom && (!APIWrapper.requiresSignIn() || LoadSave.profile.entitlement != "GUEST"))
        {
            this.onTrophyButtonClicked();
        }
        else
        {
            GameSettings.showTrophyRoom = false;
        }
    }

    
    private onStartButtonClicked():void
    {
        Audio.enable();
        Audio.playSfx(Sfx.VO_Trizzle_titlecheer);
        this._manager.startState(StateNames.GAME);
        Game._stage.emit("hide_version_number");
    }
    
    
    private onTrophyButtonClicked():void
    {
        this.startTune();

        // if required, show the Sign-In popup if the user is not logged in
        if (APIWrapper.requiresSignIn() && LoadSave.profile.entitlement == "GUEST")
        {
            this._manager.startState(StateNames.SIGN_IN);
        }
        else
        {
            if (this._trophyRoom) this._trophyRoom.destroy();
            this._trophyRoom = new TrophyRoom(this._container);
        }
    }
    
    
    private startTune():void
    {
        if (!Audio.isEnabled())
        {
            Audio.enable();
            Audio.titleTune();
        }
    }


    public update():void
    {
        if (this._pather)
        {
            if (!this._pather.update(this._scaleLogo))
            {
                this._pather.destroy();
                this._pather = null;
            }
        }

        if (this._trophyRoom)
        {
            this._trophyRoom.update();
            if (!this._trophyRoom.visible && this._trophyRoom._startGame)
            {
                this.onStartButtonClicked();
                // we just changed state which immediately destroys this class object
                return;
            }
        }

        // if the trophy room isn't displayed currently (end of game sequence does this)
        if (!this._trophyRoom || !this._trophyRoom.visible)
        {
            // if we should trigger the game start immediately from the titles
            if (Game._startImmediately)
            {
                if (GameSettings.consoleLogging)
                    console.log("start immediately from titles");
                // pretend that the start button was just clicked
                this.onStartButtonClicked();
                // once only, clear the flag
                Game._startImmediately = false;
                // we just changed state which immediately destroys this class object
                return;
            }
        }
    }
    

    public resize():void
    {
        this._scaleLogo = Math.min(GameSettings.width / this._titleLogo.texture.width, GameSettings.height / this._titleLogo.texture.height) * 0.75;
        let scale = Math.max(GameSettings.width / this._titleBackground.texture.width, GameSettings.height / this._titleBackground.texture.height);

        this._titleBackground.x = GameSettings.width * 0.5;
        this._titleBackground.y = GameSettings.height * 0.5;
        this._titleBackground.scale.set(scale);

        if (this._pather == null)
        {
            this._titleLogo.x = GameSettings.width * 0.5;
            this._titleLogo.y = GameSettings.height * 0.25;
            this._titleLogo.scale.set(this._scaleLogo);
        }

        // dynamic scale-down for really weird aspect ratios
        // (e.g. Android very tall narrow phone with the Pogo SDK taking up a big chunk of the width)
        this._startButton.resize(scale);
        while(this._startButton.width > GameSettings.width * 0.90)
        {
            scale *= 0.90;
            this._startButton.resize(scale);
        }

        this._startButton.x = GameSettings.width * 0.5;
        this._startButton.y = GameSettings.height * 0.95 - 250 * scale;

        this._trophyBtn.resize(scale);
        this._trophyBtn.x = this._startButton.x;
        this._trophyBtn.y = GameSettings.height * 0.95 - 100 * scale;

        if (this._trophyRoom)
        {
            this._trophyRoom.resize();
        }
    }
    

    public destroy():void
    {
        this._titleBackground.removeAllListeners();
        if (this._trophyRoom)
            this._trophyRoom.destroy();
        this._trophyRoom = null;
        this._titleLogo.destroy();
        this._titleBackground.destroy();
        this._startButton.destroy();
        this._trophyBtn.destroy();
        this._container.destroy();
    }
}



