import {GameSettings} from "../settings/GameSettings";
import {BoardInput} from "./BoardInput";
import {Piece} from "./Piece";



export class BoardSquare extends PIXI.Sprite
{
    public _piece: Piece;
    public _boardPos: PIXI.Point;

    private _left: number;
    private _top: number;
    private _textures: Array<PIXI.Texture>;



    public constructor(textures: Array<PIXI.Texture>, leftTab: number, topTab: number)
    {
        super(textures[0]);
        this._textures = textures;
        this._left = leftTab;
        this._top = topTab;
        this._piece = null;
        this._boardPos = null;
    }


    public create(x: number, y: number, piece: Piece):void
    {
        this._boardPos = new PIXI.Point(x, y);

        this.x = x * GameSettings.BoardSquareWide + this._left;
        this.y = y * GameSettings.BoardSquareHigh + this._top;
        this._piece = piece;

        this.anchor.set(0);

        // squares capture user input and pass it back to the PlayArea
        this.buttonMode = true;
        this.interactive = true;
        this
                .on('pointerdown', BoardSquare.onDragStart)
                .on('pointerup', BoardSquare.onDragEnd)
                .on('pointerupoutside', BoardSquare.onDragEnd)
                .on('pointermove', BoardSquare.onMouseMove)
                .on('mouseover', BoardSquare.onMouseOver)
                .on('mouseout', BoardSquare.onMouseOut);
    }


    public setColour(colour: number):void
    {
        //this.blendMode = PIXI.BLEND_MODES.NORMAL;
        //this.tint = [0xcfcfff, 0xbfefbf, 0xefef7f][colour];
        this.texture = this._textures[colour];
        // this.blendMode = PIXI.BLEND_MODES.OVERLAY;
        // this.tint = [0xbfbfff, 0xbfefbf, 0xefef6f][colour];
    }


    public getScreenLocation():PIXI.Point
    {
        return new PIXI.Point( this.x + this._left, this.y + this._top );
    }


    //
    // event callbacks for input
    //

    private static onDragStart(event: PIXI.interaction.InteractionEvent):void
    {
        let b: BoardSquare = event.currentTarget as BoardSquare;
        if (b)
        {
            BoardInput.dragStart(event.data, b._boardPos);
        }
    }

    private static onDragEnd(event: PIXI.interaction.InteractionEvent):void
    {
        BoardInput.dragEnd(event.data);
    }

    private static onMouseMove(event: PIXI.interaction.InteractionEvent):void
    {
        BoardInput.dragMove(event.data);
    }

    private static onMouseOver(event)
    {
        let b: BoardSquare = event.currentTarget as BoardSquare;
        BoardInput.mouseOver(event.data, b._boardPos);
    }

    private static onMouseOut(event)
    {
        let b: BoardSquare = event.currentTarget as BoardSquare;
        BoardInput.mouseOut(event.data, b._boardPos);
    }

}


