import {SimpleButton} from "./SimpleButton";
import {Audio, Sfx} from "./Audio";



export class CheckBox extends SimpleButton
{
    private _mark: PIXI.Sprite;

    private _checkMark: PIXI.Texture;
    private _crossMark: PIXI.Texture;
    private _checked: boolean;



    constructor(texture: PIXI.Texture, checked: boolean = true)
    {
        super(texture);
        this._checked = checked;

        this._checkMark = PIXI.Texture.fromImage("popups/Check_mark/Check_Mark_up");
        this._crossMark = PIXI.Texture.fromImage("popups/Close_all/disabled");

        this._mark = new PIXI.Sprite(this._checked ? this._checkMark:this._crossMark);
        this._mark.anchor.set(0.5);

        this.addChild(this._mark);
    }


    public toggle():boolean
    {
        this._checked = !this._checked;
        this._mark.texture = (this._checked ? this._checkMark:this._crossMark);
        return this._checked;
    }


    public destroy():void
    {
        super.destroy();
    }
}



