//
// Pete Baron 24th Oct 2018
//
// A wrapper class to separate the API specific code from the game
// in order to make it easier to use a different API (or none at all)
//
// Currently supports APIType: None, Pogo
//

import * as PogoSDK from "../lib/pogoSDK";

import {APIType} from "../const/Enums";
import {GameSettings} from "../settings/GameSettings";
import {LoadSave} from "./LoadSave";
import {Game} from "../Game";
import {Wincodes} from "./Wincodes";
import {SaveObject} from "./SaveData";
import {Inventory} from "./Inventory";



export class APIWrapper
{
    public static apiName:string = "";
    private static apiType:APIType = APIType.NONE;
    private static pogoSdk: any;
    public static saleInProgress: boolean = false;



    public static setAPI(type: APIType):void
    {
        APIWrapper.apiType = type;

        switch(APIWrapper.apiType)
        {
            case APIType.NONE: APIWrapper.apiName = ""; break;
            case APIType.POGO: APIWrapper.apiName = "Pogo"; break;
        }
        //console.log("API set to '" + APIWrapper.apiName + "'");
    }


    public static start(onComplete: Function):void
    {
        switch(APIWrapper.apiType)
        {
            case APIType.NONE: if (onComplete) onComplete(); break;
            case APIType.POGO: APIWrapper.startPogo(onComplete); break;
        }
    }


    private static startPogo(onComplete: Function):void
    {
        //console.log("Initialising Pogo with Game ID = " + GameSettings.PogoGameId);
        APIWrapper.pogoSdk = new PogoSDK({gameId: GameSettings.PogoGameId});
        APIWrapper.pogoSdk.ready()
            .then((stats: any) => {
                if (GameSettings.consoleLogging)
                    console.log("PogoSDK is ready");    //+ "\nStats:\n" + JSON.stringify(stats));
                if (onComplete)
                    onComplete();
            })
            .catch((error) => {
                console.log("PogoSDK Error");
                console.log(error);
            });
    }


    public static systemPreparation(onComplete: Function):void
    {
        switch(APIWrapper.apiType)
        {
            case APIType.NONE:
                APIWrapper.saleInProgress = false;
                if (onComplete)
                    onComplete();
                break;
            case APIType.POGO:
                APIWrapper.saleInProgress = APIWrapper.pogoSdk.getSaleStatus();
                APIWrapper.pogoSdk.showSaleUpsell().then(function() { if (onComplete) onComplete(); });
                break;
        }
    }


    public static systemCallbacks():void
    {
        switch(APIWrapper.apiType)
        {
            case APIType.NONE: break;
            case APIType.POGO: APIWrapper.pogoCallbacks(); break;
        }
    }


    private static pogoCallbacks():void
    {
        // create handlers for Pogo
        APIWrapper.pogoSdk.addPreferenceChangeHandler(LoadSave.prefChanged);
        APIWrapper.pogoSdk.addPauseHandler(Game.pauseGame);
        APIWrapper.pogoSdk.setNewGameHandler(Game.restartGame);
        APIWrapper.pogoSdk.setCatalogChangeHandler(Game.catalogChange);
    }


    public static getRandomSeed():number
    {
        switch(APIWrapper.apiType)
        {
            case APIType.NONE: return Date.now();
            case APIType.POGO: return parseInt(APIWrapper.pogoSdk.getSeed());
        }
    }


    public static requiresSignIn():boolean
    {
        switch(APIWrapper.apiType)
        {
            case APIType.NONE: return false;
            case APIType.POGO: return true;
        }
        return false;
    }


    public static signIn():void
    {
        switch(APIWrapper.apiType)
        {
            case APIType.NONE: break;
            case APIType.POGO: APIWrapper.pogoSdk.redirectTo("signIn"); break;
        }
    }


    public static gameStarted():void
    {
        switch(APIWrapper.apiType)
        {
            case APIType.NONE: break;
            // "Method that should be called everytime a new game round starts. This is used to track game time"
            case APIType.POGO: APIWrapper.pogoSdk.gameStarted(); break;
        }
    }


    public static loadSaveData(name: string, onComplete: Function):void
    {
        switch(APIWrapper.apiType)
        {
            // load from localStorage
            case APIType.NONE:
                APIWrapper.loadFromLocal(name, onComplete);
                break;

            case APIType.POGO:
                APIWrapper.loadFromPogo(name, onComplete, APIWrapper.loadFromLocal);
                break;
        }        
    }


    private static loadFromLocal(name: string, onComplete: Function):void
    {
        //console.log("load 'savedata' from local storage");
        let s: string;

        s = localStorage["savedata" + name];
        if (s == "undefined")
            s = undefined;
        if (onComplete)
            onComplete(s);
    }


    // attempt to "retrieve user data that was previously stored using sendSaveData"
    // if attempt fails or there is no data, call onNoData(name, onComplete) to retreive data from elsewhere
    private static loadFromPogo(name: string, onComplete: Function, onNoData: Function):void
    {
        //console.log("load 'savedata' from Pogo");
        let dataPromise:Promise<string> = APIWrapper.pogoSdk.getSaveData(name);
        if (dataPromise)
        {
            dataPromise
                .then((response) => {
                    if (!response || response == "" || response == "undefined")
                    {
                        console.log("no 'savedata' found in Pogo");
                        // NOTE(ebuchholz): loading from local could cause an issue where one pogo account could load
                        // the local save data of a different pogo account that was used in the same browser. So in the
                        // case where the connection to pogo was successful, but there was no save data, don't try to
                        // use local save data.
                        //onNoData(name, onComplete);
                    }
                    else
                    {
                        if (onComplete)
                            onComplete(response);
                    }
                }, 
                (error) => {
                    // NOTE(ebuchholz): If we couldn't connect to pogo at all, then load local data
                    console.log("error connecting to Pogo,", error);
                    onNoData(name, onComplete);
                });
        }
        else
        {
            onNoData(name, onComplete);
        }
    }


    public static saveSaveData(name: string, json: string):void
    {
        switch(APIWrapper.apiType)
        {
            case APIType.POGO:
                APIWrapper.pogoSdk.sendSaveData(name, json);
                break;
        }

        // always save data to localStorage too
        localStorage["savedata" + name] = json;
    }


    public static getPreferences():any
    {
        let o: any = null;
        let localPreferences = APIWrapper.getPreferencesLocal();
        switch(APIWrapper.apiType)
        {
            case APIType.POGO:
                //console.log("load 'preferences' from Pogo")
                // NOTE(ebuchholz): get preferences doesn't ACTUALLY get the preferences, it only returns a cached
                // "preferences" object that has any values that were already obtained with individual "getPreference"
                // calls. So instead, load the local preferences first, then use the keys in that object to know what
                // keys to load from pogo individually
                
                // NOTE(pjbaron): unfortunately the above is not entirely correct, not all preferences used by Pogo
                // are stored locally, in particular the Pogo specific 'sound' preference reflects the status of the
                // Pogo side-panel mute button.  Using the code as modified causes this value to be ignored entirely
                // (there is no key = 'sound' in localPreferences, so the APIWrapper.pogoSdk.getPreference(key) is
                // never tasked to retrieve it).
                // Triggering getPreferences() as originally written does recover this value, but only this value and
                // not the others, so I believe this combination of both approaches is probably the best way to get
                // all of the preferences we require.

                let pogoPreferences = APIWrapper.pogoSdk.getPreferences();
                let foundPreference = false;
                for (let key in localPreferences) {
                    let preference = APIWrapper.pogoSdk.getPreference(key);
                    if (preference != undefined) { 
                        foundPreference = true; 
                        pogoPreferences[key] = preference;
                    }
                }
                if (foundPreference) {
                    return pogoPreferences;
                }
                console.log("no 'preferences' found in Pogo");
                break;
        }
        //console.log("load 'preferences' from local storage");
        return localPreferences;
    }


    private static getPreferencesLocal():any
    {
        let s: string;
        s = localStorage["preferences"];
        if (s && s != "undefined")
        {
            try
            {
                return JSON.parse(s);
            }
            catch(e)
            {
                console.log(e);
                return null;
            }
        }
        return {};
    }


    public static setPreference(name: string, value: any):void
    {
        switch(APIWrapper.apiType)
        {
            case APIType.POGO:
                APIWrapper.pogoSdk.setPreference(name, value);
                break;
        }

        // always save to localStorage
        APIWrapper.setPreferenceLocal(name, value);
    }


    private static setPreferenceLocal(name: string, value: any):void
    {
        let p: any = null;
        let s: string = localStorage["preferences"];

        if (s && s != "undefined")
        {
            // modify existing preference
            try
            {
                p = JSON.parse(s);
                p[name] = value;
            }
            catch(e)
            {
                console.log(e);
            }
        }
        if (!p)
        {
            // create new preferences object with this name:value entry
            p = JSON.parse("{ \"" + name + "\" : " + value + " }");
        }
        localStorage["preferences"] = JSON.stringify(p);
    }


    public static getProfile():any
    {
        // @property {string} userId userId
        // @property {string} screenName screenName
        // @property {string} entitlement - 'GUEST' 'FREE' or 'CLUB'
        switch(APIWrapper.apiType)
        {
            // Pogo: "Returns an object containing player profile data"
            case APIType.POGO:
                //console.log("load 'profile' from Pogo");
                let p:any = APIWrapper.pogoSdk.getProfile();
                if (p)
                    return p;
                console.log("no 'profile' found in Pogo");
                break;
        }

        // try to load player profile data from localStorage
        // set default values if that fails
        //console.log("load 'profile' from local storage");
        return APIWrapper.getProfileLocal();
    }


    private static getProfileLocal():any
    {
        let s: string = localStorage["profile"];
        if (s && s != "undefined")
            return JSON.parse(s);
        return {
                userId: 0,
                screenName: "Player",
                entitlement: "GUEST"
            };
    }


    public static setProfile(_profile: any):void
    {
        // @property {string} userId userId
        // @property {string} screenName screenName
        // @property {string} entitlement - 'GUEST' 'FREE' or 'CLUB'

        // NOTE: there is no Pogo function for this, the data there is read-only provided by the API

        // save player profile data to localStorage
        localStorage["profile"] = JSON.stringify(_profile);
    }



    public static getStats():any
    {
        if ( GameSettings.consoleLogging )
            console.log("getStats()");

        switch(APIWrapper.apiType)
        {
            case APIType.POGO:
                if ( LoadSave.profile.entitlement != "GUEST" )
                {
                    // "Get the player's cached stats object. This object contains things like user power ups, rank information, active challenges and other stats."
                    //console.log("load 'stats' from Pogo");
                    let s:any = APIWrapper.pogoSdk.getStats();
                    if (s)
                        return s;
                    console.log("no 'stats' found in Pogo");
                }
                break;

            case APIType.NONE:

                break;
        }
        // TODO: try to load player stats data from localStorage
        // case APIType.NONE:
        // NOTE: I don't think this is possible... Pogo says "Wincodes are used by a rule engine to award stats to users"
        // so if we want to keep any of the 'wincode' related data locally, we will need to implement a replica of their rule engine
        /*
            rank    object
                Name    Type    Description
                rankUp    boolean    
                currentRank    number    
                currentRankPoints    number    
                currentTotalRankPoints    number    
                previousRank    number    
                previousRankPoints    number    
                previousTotalRankPoints    number    
                rankPointsText    number    
            inventory    object    inventory
            game    object    game stats
            service    object    service menu stats
            dataslots    object    dataslot stats
        */        
        return null;

    }


    public static endOfGameSequence(onComplete: Function):void
    {
        if ( GameSettings.consoleLogging )
            console.log( "endOfGameSequence" );

        switch(APIWrapper.apiType)
        {
            case APIType.POGO: APIWrapper.pogoEndOfGameSequence(onComplete); break;
            default: if (onComplete) onComplete(); break;
        }
    }


    private static pogoEndOfGameSequence(onComplete: Function):void
    {
        /*
            At end of round:
            Sends Wincodes and the seed to Pogo Servers with sendGameOver().  (done)
            Starts the in-game Game Over Rollup animations (done [this popup])
            When appropriate show tokens awarded using showToast('token') TODO: after GameOver closes?
            When appropriate show rank progress using showToast('rank') TODO: after tokens closes?
            Once game over rollup is done show GOAT (game over award takeover) screens using showGoats() TODO: after rank closes?
            Once goat screens are done call showServiceMenu('gameOver') TODO: after goat
            Once service menu is done, get the new seed and start next round (done, automatic from the state machine)
        */

        if (GameSettings.consoleLogging)
            console.log("Pogo end sequence: showToast(token)");

        let tokenPromise: Promise<any> = APIWrapper.pogoSdk.showToast('token');
        tokenPromise.then(function()
            {
                if (GameSettings.consoleLogging)
                    console.log("pogo showToast rank");
                
                let rankPromise: Promise<any> = APIWrapper.pogoSdk.showToast('rank');
                rankPromise.then(function()
                {
                    if (GameSettings.consoleLogging)
                        console.log("pogo showGoats");

                    let goatsPromise: Promise<any> = APIWrapper.pogoSdk.showGoats();
                    goatsPromise.then(function()
                        {
                            if (GameSettings.consoleLogging)
                                console.log("pogo showServiceMenu");

                            let servicePromise: Promise<any> = APIWrapper.pogoSdk.showServiceMenu("gameOver");
                            servicePromise.then(function()
                                {
                                    if (GameSettings.consoleLogging)
                                        console.log("pogo end sequence completed");

                                    if (onComplete)
                                        onComplete();
                                });
                        });
                });
            });
    }


    public static sendGameOver(onComplete: Function):void
    {
        if ( GameSettings.consoleLogging )
            console.log("APIWrapper.sendGameOver");

        switch(APIWrapper.apiType)
        {
            case APIType.NONE: if (onComplete) onComplete(); break;
            // Pogo: "Get the player's cached stats object. This object contains things like
            // user power ups, rank information, active challenges and other stats."
            // rank    object
                // Name    Type    Description
                // rankUp    boolean    
                // currentRank    number    
                // currentRankPoints    number    
                // currentTotalRankPoints    number    
                // previousRank    number    
                // previousRankPoints    number    
                // previousTotalRankPoints    number    
                // rankPointsText    number    
            // inventory    object    inventory
            // game    object    game stats
            // service    object    service menu stats
            // dataslots    object    dataslot stats
            case APIType.POGO: APIWrapper.pogoSendGameOver(onComplete); break;
        }
    }


    // pogo sendGameOver - requires wincodes and is called before the Engine is destroyed
    private static pogoSendGameOver(onComplete: Function):void
    {
        let winCodes:any = Wincodes.GetWincodes();
        let stringWinCodes:any = {
            "save.slot.1": SaveObject.toJson()
        };

        if (GameSettings.consoleLogging)
        {
            console.log("sending winCodes = " + JSON.stringify(winCodes));
            console.log("stringWinCodes = " + JSON.stringify(stringWinCodes));
        }
        
        let gameOverResponder:Promise<string> = APIWrapper.pogoSdk.sendGameOver(winCodes, Game.seed, stringWinCodes);
        gameOverResponder.then((response: string) =>
            {
                //console.log("Game Over response = " + response);
                if (onComplete)
                    onComplete(response);
            });
    }


    public static showIntermissionAd():void
    {
        switch(APIWrapper.apiType)
        {
            case APIType.POGO:
                APIWrapper.pogoSdk.showIntermission();
                break;

            case APIType.NONE:
                break;
        }
    }


    public static purchaseBoosts(onComplete: Function):void
    {
        if (GameSettings.consoleLogging)
            console.log("purchase boosts");
        switch(APIWrapper.apiType)
        {
            case APIType.POGO:
                APIWrapper.pogoSdk.showStore().then((response: boolean) => {
                        Inventory.load();
                        APIWrapper.pogoUpdateAfterPurchase(response);
                        if (onComplete)
                            onComplete(response);
                    });
                break;

            case APIType.NONE:
                if (onComplete)
                    onComplete(null);
                break;
        }
    }


    public static purchaseExtraLives(onComplete: Function):void
    {
        if (GameSettings.consoleLogging)
            console.log("purchase lives");
        switch(APIWrapper.apiType)
        {
            case APIType.POGO:
                APIWrapper.pogoSdk.showStore().then((response: boolean) => {
                        Inventory.load();
                        APIWrapper.pogoUpdateAfterPurchase(response);
                        if (onComplete)
                            onComplete(response);
                    });
                break;

            case APIType.NONE:
                if (onComplete)
                    onComplete(null);
                break;
        }
    }


    public static pogoUpdateAfterPurchase(success: boolean):void
    {
        if (success)
        {
            APIWrapper.pogoSdk.getWalletBalances((response: number) =>
                {
                    if (GameSettings.consoleLogging)
                        console.log("Pogo wallet balances = " + response);
                });
        }
    }


    // sample responses (sent to onComplete by PogoSDK):
    // success - {gameId: 385, stats: [{value: 16, id: "PWRUP_1"}]}
    // not found - {gameId: 385, stats: [{value: 0, id: "notexist", failStatus: "NOT_FOUND"}]}
    // not enough items owned - {gameId: 385, stats: [{value: 0, id: "PWRUP_3", failStatus: "Stat:PWRUP_3 hit stop limit. Value=0 amount=-1"}]}
    public static consumePowerUp(powerUp: string, prior: number, onComplete: Function):void
    {
        if (GameSettings.consoleLogging)
            console.log("consume power up " + powerUp);

        switch(APIWrapper.apiType)
        {
            case APIType.POGO:
                if ( LoadSave.profile.entitlement != "GUEST" )
                {
                    APIWrapper.pogoSdk.consume(powerUp, 1).then(response => { onComplete(response); });
                }
                else
                {
                    onComplete( {gameId: GameSettings.PogoGameId, stats: [{value: prior - 1, id: powerUp}]} );
                }
                break;

            case APIType.NONE:
                onComplete( {gameId: GameSettings.PogoGameId, stats: [{value: prior - 1, id: powerUp}]} );
                break;
        }
    }


}



