import {FloatingTexts, FloatingTextType} from "./FloatingTexts";
import {Game} from "../Game";



export class FloatingText extends PIXI.Text
{
    public _isDone: boolean;
    public _onTop: boolean;
    public _x: number;
    public _y: number;
    public _z: number;            // height above the ground
    public _startTime: number;

    private _type: number;
    private _life: number;
    private _fade: number;
    public _vx: number;
    public _vy: number;
    public _vz: number;
    private _ax: number;
    private _ay: number;
    private _az: number;
    public _frictionx: number;
    public _frictiony: number;
    public _frictionz: number;
    private _scalex: number;
    private _scaley: number;
    private _maxScalex: number;
    private _maxScaley: number;
    private _rotationSpeed: number;
    private _rotationDamping: number;
    private _container: PIXI.Container;



    public constructor(container: PIXI.Container)
    {
        super();
        this._container = container;
        this._onTop = true;                // default: FloatingText is always over the rest of its container
    }


    public init(type: number, text: string, x: number, y: number, z: number):void
    {
        this._startTime = 0;
        this._type = type;
        this.text = text;
        this._x = x;
        this._y = y;
        this._z = z;

        this._isDone = false;

        let data: any = FloatingTexts.FloatingTextData[this._type];

        if (data.style)
            this._style = data.style;

        this.rotation = 0;
        if (data.rotationMin != undefined)
        {
            if (data.rotationMax != undefined)
            {
                this.rotation = Math.random() * (data.rotationMax - data.rotationMin) + data.rotationMin;
            }
            else
            {
                this.rotation = data.rotationMin;
            }
        }

        this._rotationSpeed = 0;
        if (data.rotationSpeedMin != undefined)
        {
            if (data.rotationSpeedMax != undefined)
            {
                this._rotationSpeed = Math.random() * (data.rotationSpeedMax - data.rotationSpeedMin) + data.rotationSpeedMin;
            }
            else
            {
                this._rotationSpeed = data.rotationSpeedMin;
            }
        }

        this._rotationDamping = 1;
        if (data.rotationDamping != undefined)
            this._rotationDamping = data.rotationDamping;

        this._life = undefined;
        if (data.lifeMin != undefined)
        {
            if (data.lifeMax != undefined)
            {
                this._life = Math.random() * (data.lifeMax - data.lifeMin) + data.lifeMin;
            }
            else
            {
                this._life = data.lifeMin;
            }
        }

        if (data.xMin != undefined)
        {
            if (data.xMax != undefined)
            {
                this._x += Math.random() * (data.xMax - data.xMin) + data.xMin;
            }
            else
            {
                this._x += data.xMin;
            }
        }

        if (data.yMin != undefined)
        {
            if (data.yMax != undefined)
            {
                this._y += Math.random() * (data.yMax - data.yMin) + data.yMin;
            }
            else
            {
                this._y += data.yMin;
            }
        }

        if (data.zMin != undefined)
        {
            if (data.zMax != undefined)
            {
                this._z += Math.random() * (data.zMax - data.zMin) + data.zMin;
            }
            else
            {
                this._z += data.zMin;
            }
        }

        this._vx = 0;
        if (data.vxMin != undefined)
        {
            if (data.vxMax != undefined)
            {
                this._vx = Math.random() * (data.vxMax - data.vxMin) + data.vxMin;
            }
            else
            {
                this._vx = data.vxMin;
            }
        }

        this._vy = 0;
        if (data.vyMin != undefined)
        {
            if (data.vyMax != undefined)
            {
                this._vy = Math.random() * (data.vyMax - data.vyMin) + data.vyMin;
            }
            else
            {
                this._vy = data.vyMin;
            }
        }

        this._vz = 0;
        if (data.vzMin != undefined)
        {
            if (data.vzMax != undefined)
            {
                this._vz = Math.random() * (data.vzMax - data.vzMin) + data.vzMin;
            }
            else
            {
                this._vz = data.vzMin;
            }
        }

        this._ax = 0;
        if (data.axMin != undefined)
        {
            if (data.axMax != undefined)
            {
                this._ax = Math.random() * (data.axMax - data.axMin) + data.axMin;
            }
            else
            {
                this._ax = data.axMin;
            }
        }

        this._ay = 0;
        if (data.ayMin != undefined)
        {
            if (data.ayMax != undefined)
            {
                this._ay = Math.random() * (data.ayMax - data.ayMin) + data.ayMin;
            }
            else
            {
                this._ay = data.ayMin;
            }
        }

        this._az = 0;
        if (data.azMin != undefined)
        {
            if (data.azMax != undefined)
            {
                this._az = Math.random() * (data.azMax - data.azMin) + data.azMin;
            }
            else
            {
                this._az = data.azMin;
            }
        }

        this._frictionx = 1;
        if (data.frictionx != undefined)
            this._frictionx = data.frictionx;
        
        this._frictiony = 1;
        if (data.frictiony != undefined)
            this._frictiony = data.frictiony;

        this._frictionz = 1;
        if (data.frictionz != undefined)
            this._frictionz = data.frictionz;

        this.alpha = 1;
        if (data.alpha != undefined)
            this.alpha = data.alpha;

        this._fade = 0;
        if (data.fade != undefined)
            this._fade = data.fade;

        if (data.scalex)
            this.scale.x = data.scalex;

        if (data.scaley)
            this.scale.y = data.scaley;

        if (data.maxScalex)
            this._maxScalex = data.maxScalex;

        if (data.maxScaley)
            this._maxScaley = data.maxScaley;

        this._scalex = 0;
        if (data.rescalex != undefined)
            this._scalex = data.rescalex;

        this._scaley = 0;
        if (data.rescaley != undefined)
            this._scaley = data.rescaley;

        this.anchor.set(0.5);

        this.x = this._x;
        this.y = this._y - this._z;

        this._container.addChild(this);
    }


    public destroy():void
    {
        this._isDone = true;
        this._container.removeChild(this);
        this._container = null;
    }


    // return false if this effect should be destroyed
    public animate(): boolean
    {
        this._x += this._vx * Game._elapsed / 1000;
        this._vx = this._vx * this._frictionx + this._ax;
        this._y += this._vy * Game._elapsed / 1000;
        this._vy = this._vy * this._frictiony + this._ay;
        this._z += this._vz * Game._elapsed / 1000;
        if (this._z < 0)
        {
            // bounce off ground and apply friction to x/y velocities
            this._z = 0;
            this._vz = -this._vz * 0.25;
            this._vx *= 0.9;
            this._vy *= 0.9;
        }
        this._vz = this._vz * this._frictionz + this._az;

        this.rotation += this._rotationSpeed * Game._elapsed / 1000;
        this._rotationSpeed *= this._rotationDamping;

        if (this._scalex)
            this.scale.x += this._scalex * Game._elapsed / 1000;
        if (this._scaley)
            this.scale.y += this._scaley * Game._elapsed / 1000;
        if (this.scale.x > this._maxScalex)
            this.scale.x = this._maxScalex;
        if (this.scale.y > this._maxScaley)
            this.scale.y = this._maxScaley;

        this.alpha = Math.max(Math.min(this.alpha + this._fade * Game._elapsed / 1000, 1), 0);

        this.x = this._x;
        this.y = this._y - this._z;

        this._life -= Game._elapsed;

        return (this._life == undefined || this._life > 0);
    }

};



