import * as ArenaHelper from "./util/ArenaHelper";
import * as Visibility from "visibilityjs";
import * as Stats from "stats-js";

import {BootManager} from "./settings/BootManager";
import {GameSettings} from "./settings/GameSettings";
import {APIType, StateNames} from "./const/Enums";

import {StateManager} from "./state/StateManager";
import {AssetLoaderState} from "./state/AssetLoaderState";
import {AudioSplashState} from "./state/AudioSplashState";
import {TitleScreenState} from "./state/TitleScreenState";
import {SignInState} from "./state/SignInState";
import {GameState} from "./state/GameState";
import {Cheats} from "./util/Cheats";
import {CheatKey} from "./util/Cheats";
import {LoadSave} from "./util/LoadSave";
import {Audio} from "./ui/Audio";
import {RNG} from "./util/RNG";
import {APIWrapper} from "./util/APIWrapper";





export class Game
{
    public static _stage: PIXI.Container;
    public static _now: number;
    public static _elapsed: number;
    public static _quitGame: boolean;
    public static _startImmediately: boolean;
    public static _visible: boolean;
    public static seed: number;
    private static _that: Game;

    //public static viewport: HTMLElement;

    private _app: PIXI.Application;
    private _renderer: PIXI.WebGLRenderer | PIXI.CanvasRenderer;
    private _resizeCallback: (this: this, ev: Event) => any;
    private _stateManager: StateManager;
    private _lastTime: number;
    private _timeSmoothing: Array<number>;
    private _resizeQueued: boolean;
    private _lastClientWidth: number;
    private _lastClientHeight: number;
    private _versionNumber: PIXI.Text;
    private _versionTextStyle: PIXI.TextStyle;
    private _stats: any;
    private _iosInnerHeight: any;
    private _parentContainer: any;




    public start(elementID: string):void
    {
        //APIWrapper.setAPI(APIType.POGO);
        APIWrapper.setAPI(APIType.NONE);

        this._parentContainer = document.getElementById(elementID);
        this._parentContainer.style.display = "none";

        Game._that = this;
        let _that:Game = this;

        this._resizeQueued = false;
        this._lastClientWidth = window.innerWidth;    //this._parentContainer.clientWidth;
        this._lastClientHeight = this._parentContainer.clientHeight;

        // cap the pixel ratio to reduce pixelisation
        let pixelRatio = window.devicePixelRatio || 1;
        pixelRatio = Math.min(2, pixelRatio);

        let rendererOptions =
        {
            autoResize: true,
            backgroundColor: 0x000000,
            resolution: pixelRatio
        };

        this._app = new PIXI.Application(this._lastClientWidth, this._lastClientHeight, rendererOptions);
        Game._stage = this._app.stage;

        this._renderer = this._app.renderer;
        //document.body.appendChild(this._app.view);
        this._parentContainer.insertAdjacentElement("afterbegin", this._renderer.view);
        this._parentContainer.style.display = "block";

        // visibility support (is the game window visible or hidden)
        if (Visibility.isSupported())
        {
            Visibility.change(function () {
                    Game._visible = !Visibility.hidden();
                    Game._stage.emit("visibility_change", Game._visible);
                });
        }
        else
        {
            console.log("WARNING: the visiblity API is not supported, we cannot detect when the browser loses focus!");
            Game._visible = true;
        }

        // focus support (is the game window focused or defocused)
        window.addEventListener("focus", function() {
                Game._visible = true;
                Game._stage.emit("visibility_change", Game._visible);
            });
        window.addEventListener("blur", function() {
                Game._visible = false;
                Game._stage.emit("visibility_change", Game._visible);
            });

        // initialise the load/save system before 'init' function is called
        new LoadSave();

        // change background colour to dark olive after all assets are loaded
        Game._stage.once("preloader_finished", function() {
            _that._renderer.backgroundColor = 0x2f3803;
            this._versionTextStyle = new PIXI.TextStyle({
                fontFamily: "GrilledCheese",
                fontSize: 18,
                fontWeight: 'normal',
                fill: ['#cccccc'],
                dropShadow: false,
                align: 'left',
                leading: -8,
                wordWrap: false
            });

            this._versionNumber = new PIXI.Text(GameSettings.VersionNumber, this._versionTextStyle);
            this._versionNumber.x = 4;
            this._versionNumber.y = 4;    //GameSettings.height - 20;
            Game._stage.addChild(this._versionNumber);

            // always debug the version number in case the screen doesn't display properly (e.g. iOS zoom bug)
            console.log("Trizzle " + GameSettings.VersionNumber);
        }.bind(this));

        // Version number is always visible when cheats are on
        Game._stage.on("show_version_number", function() {
            if (this._versionNumber && !GameSettings.cheatsEnabled) {
                this._versionNumber.visible = true;
            }
        }.bind(this));
        Game._stage.on("hide_version_number", function() {
            if (this._versionNumber && !GameSettings.cheatsEnabled) {
                this._versionNumber.visible = false;
            }
        }.bind(this));


        // TODO: disable for release.  Cheat key system for QA testing
        if (GameSettings.cheatsEnabled)
        {
            Cheats.init();

            this._stats = new Stats();
            this._stats.showPanel(0);
            document.body.appendChild(this._stats.dom);
        }

        APIWrapper.start(this.init.bind(this));
    }


    // when the API has successfully started, continue with game initialisation
    private init():void
    {
        let bootManager = new BootManager();
        bootManager.init();

        // register callbacks for the current API selection
        APIWrapper.systemCallbacks();
        APIWrapper.systemPreparation(this.ready.bind(this));
    }


    // when the API has been prepared, continue with game initialisation
    private ready():void
    {
        // restore settings from previous session (if available)
        LoadSave.load();

        this._stateManager = new StateManager(Game._stage);
        this._stateManager.registerState(
            StateNames.ASSET_LOADER,
            new AssetLoaderState(this._stateManager, Game._stage)
        );
        this._stateManager.registerState(
            StateNames.TITLE_SCREEN,
            new TitleScreenState(this._stateManager, Game._stage)
        );
        this._stateManager.registerState(
            StateNames.SIGN_IN,
            new SignInState(this._stateManager, Game._stage)
        );
        this._stateManager.registerState(
            StateNames.GAME,
            new GameState(this._stateManager, Game._stage)
        );


        // iOS does not report height correctly, this package fixes it
        this._iosInnerHeight = require('ios-inner-height');

        // resize event can happen on mobile when the window values are not correct yet
        // we use a delayed response to the resize event to give them time to settle
        this._resizeCallback = this.queueResize.bind(this);

        // event listeners to trigger the resize callback
        //window.addEventListener("orientationchange", this._resizeCallback, false);
        //window.addEventListener("resize", this._resizeCallback, false);

        // trigger an immediate resize once at the game start
        this.resizeImmediately();

        PIXI.ticker.shared.add(this.update, this);
        this._stateManager.startState(StateNames.ASSET_LOADER);

        Game._quitGame = false;
        Game._now = 0;
        Game._elapsed = 0;
        this._lastTime = Date.now();
        this._timeSmoothing = new Array<number>();

        // trigger resize regularly to fix Safari notification failures
        // (Safari does not notify resize events for a number of cases including:
        // - rapid orientation changes
        // - open/closing the tab bar
        // )
        //window.setInterval(this.resizeImmediately.bind(this), 2000);
    }


    // many sources report that iOS skips resize events when multiple resizings happen rapidly
    // adding a delay before handling the resize event is actually the _recommended_ solution!
    private queueResize():void
    {
        if (this._resizeQueued) return;
        this._resizeQueued = true;
        setTimeout(this.delayedResize.bind(this), 700);
    }


    // handle a delayed resize, call the resize method then reset the queue flag
    // (we can't reset the flag in resizeImmediately because other sources might call it
    // which would reset the flag and allow a second timer to be set before the first one fires)
    private delayedResize():void
    {
        this.resizeImmediately();
        this._resizeQueued = false;
    }


    private resizeImmediately():void
    {
        let newWidth = this._lastClientWidth = window.innerWidth;    //this._parentContainer.clientWidth;        //window.innerWidth;
        let newHeight = this._lastClientHeight = this._parentContainer.clientHeight;    //this.getInnerHeight();

        if (GameSettings.width != newWidth || GameSettings.height != newHeight)
        {
            if (GameSettings.consoleLogging)
                console.log("v" + GameSettings.VersionNumber + " resize from " + GameSettings.width + "x" + GameSettings.height + " to " + newWidth + "x" + newHeight);

            GameSettings.width = newWidth;
            GameSettings.height = newHeight;

            // copied from Sweet
            let view = this._renderer.view;
            view.width = newWidth;
            view.height = newHeight;
            view.style.width = newWidth + "px";
            view.style.height = newHeight + "px";

            this._renderer.resize(newWidth, newHeight);
            this._stateManager.resize();
        }

        // make sure the versionNumber aligns properly after the size change
        if (this._versionNumber)
        {
            this._versionNumber.x = 4;
            this._versionNumber.y = GameSettings.height - 24;
        }
    }


    private update():void
    {
        if (this._stats)
        {
            this._stats.begin();
        }

        // make sure there isn't a delayed resize pending which will do this soon
        if (!this._resizeQueued)
        {
            let newWidth = this._parentContainer.clientWidth;
            let newHeight = this._parentContainer.clientHeight;

            // manually check for resize which did not trigger the resize event callback
            if ((GameSettings.width != newWidth) || (GameSettings.height != newHeight))
            {
                // NOTE: hack!  Pogo resize bug messes up the values unless we shrink during transition
                let canvas = this._renderer.view;
                canvas.width = canvas.height = 2;
                canvas.style.width = canvas.style.height = "2px";
                if (GameSettings.consoleLogging)
                    console.log("shrink and resize to " + newWidth + "x" + newHeight);
                this.queueResize();
            }
        }

        if (Game._quitGame)
        {
            if (GameSettings.consoleLogging)
                console.log("_quitGame triggers return to Title Screen");
            this._stateManager.startState(StateNames.TITLE_SCREEN);
            Game._quitGame = false;
        }

        let elapsed = Math.min(Date.now() - this._lastTime, 34);
        this._lastTime = Date.now();

        if (this._timeSmoothing.length > 8)
            this._timeSmoothing.shift();
        this._timeSmoothing.push(elapsed);

        Game._elapsed = this.smoothTime();
        Game._now += Game._elapsed;

        this._stateManager.update();
        this._renderer.render(Game._stage);
        TWEEN.update();

        // Make sure the version number is on top
        if (this._versionNumber) {
            Game._stage.addChild(this._versionNumber);
        }

        if (GameSettings.cheatsEnabled) {
            Cheats.resetKeys();
        }

        if (this._stats)
        {
            this._stats.end();
        }
    }


    private getInnerHeight():number
    {
        let h: number = this._iosInnerHeight();
        /*
        if (navigator.userAgent.match(/iphone|ipod|ipad/i))
        {
            // change h a bit, Safari has 'invisible' browser cruft that overlaps the screen area
            // by changing a bit here we make game content scale slightly smaller, hopefully
            // enough to prevent important pieces being off-screen and yet not so much as to
            // leave huge gaping holes in the layout

            // in case of old iOS devices with small screens, don't do this if it will change
            // more than 10% to the height value
            if (h > 480)
                h -= 48;
        }
        */
        return h;
    }


    private smoothTime():number
    {
        let t: number = 0;
        for(let i = 0; i < this._timeSmoothing.length; i++)
            t += this._timeSmoothing[i];
        t /= this._timeSmoothing.length;
        return t;
    }


    public destroy():void
    {
        this._stateManager.destroy();

        window.removeEventListener("resize", this._resizeCallback);
        window.removeEventListener("orientationchange", this._resizeCallback);

        Game._stage.destroy();
        this._renderer.destroy();
    }


    public static restartGame():void
    {
        // trigger game over flow, then start a new round with a fresh seed
        console.log("Game restarted");
        Game._that._stateManager.events.emit('user_quit_game');
        Game._startImmediately = true;
    }


    // accept a promise, that will get resolved when the game should resume
    public static pauseGame(resume: any):void
    {
        //console.log("Game paused");
        resume
            .then(() => {
                //console.log("Game resumed");
            })
    }


    public static catalogChange(_products: Array<any>):void
    {
        if ( GameSettings.consoleLogging )
            console.log("catalog change triggered from Pogo " + _products);
    }


    public static getSeed():void
    {
        // "Get cached seed for your game's PRNG. It is updated during bootstrap and then every gameOver."
        // Note: I'm storing the original seed value because the RNG modifies it and we need the original
        // value to validate the sendGameOver() call
        Game.seed = APIWrapper.getRandomSeed()
        //console.log("Game seed = " + Game.seed);
        RNG.seed = Game.seed;

        /*
        // test that RNG produces a good distribution
        let r:Array<number> = new Array<number>(11);
        for(let i:number = 0; i < 2; i++)
            r[i] = 0;
        for(let i:number = 0; i < 1000000; i++)
            r[RNG.nextInt(0,2)]++;
        for(let i:number = 0; i < 2; i++)
            console.log(i + " = " + r[i]);
        */
    }

}



