import {GameSettings} from "../settings/GameSettings";



export class PathPoint
{
    public _steps: number;
    public _point: PIXI.Point;        // percentage of screen dimensions
    public _scale: PIXI.Point;

    constructor(steps: number, point: PIXI.Point, scale: PIXI.Point = null)
    {
        this._steps = steps;
        this._point = point;
        this._scale = scale;
    }
}


export class PathData extends Array<PathPoint>
{

}


export class Pather
{
    private _sprite: PIXI.Sprite;
    private _pathDetails: PathData;
    private _pathIndex: number;
    private _nextPoint: PathPoint;
    private _dx: number;
    private _dy: number;
    private _dsx: number;
    private _dsy: number;
    private _count: number;
    private _scale: number;



    constructor(sprite: PIXI.Sprite, pathDetails: PathData)
    {
        this._sprite = sprite;
        this._pathDetails = pathDetails;
        this._pathIndex = 0;
        this._scale = pathDetails[0]._scale.x;
        this.setNextPoint();
        if (!this.update(this._scale))
            this.destroy();
    }


    public destroy():void
    {
        this._sprite = null;
        this._pathDetails = null;
    }


    public update(_scaleFactor: number):boolean
    {
        this._scale = _scaleFactor;
        if (this._sprite)
        {
            this._sprite.x += this._dx;
            this._sprite.y += this._dy;
            this._sprite.scale.x += this._dsx;
            this._sprite.scale.y += this._dsy;
            if (this._count-- <= 0)
                if (!this.setNextPoint())
                    return false;
            return true;
        }
        return false;
    }


    private setNextPoint():boolean
    {
        if (this._pathIndex >= this._pathDetails.length)
            return false;
        this._nextPoint = this._pathDetails[this._pathIndex];
        this._count = this._nextPoint._steps;
        this._dx = (this._nextPoint._point.x * GameSettings.width - this._sprite.x) / this._count;
        this._dy = (this._nextPoint._point.y * GameSettings.height - this._sprite.y) / this._count;
        this._dsx = (this._nextPoint._scale.x * this._scale - this._sprite.scale.x) / this._count;
        this._dsy = (this._nextPoint._scale.y * this._scale - this._sprite.scale.y) / this._count;
        this._pathIndex++;
        return true;
    }


}


