import {Events} from "../const/Events";
import {Audio, Sfx} from "./Audio";



/**
 * Simple button class with optional label
 */
export class SimpleButton extends PIXI.Sprite
{
    public static allowRollovers: boolean = true;
    public static useMobileEvents: boolean = false;

    public events: PIXI.utils.EventEmitter;

    private _overTint: number;
    private _downTint: number;

    private _onDownHandler: () => void;
    private _onUpHandler: () => void;
    private _onOverHandler: () => void;
    private _onOutHandler: () => void;
    private _onUpOutsideHandler: () => void;

    private _label: PIXI.Text;
    private _labelFontSize: number;

    private _overSfx: Sfx;
    private _downSfx: Sfx;



    constructor(texture: PIXI.Texture)
    {
        super(texture);

        this.events = new PIXI.utils.EventEmitter();

        this.anchor.set(0.5);

        this._onDownHandler = this.onDown.bind(this);
        this._onUpHandler = this.onUp.bind(this);
        this._onOverHandler = this.onOver.bind(this);
        this._onOutHandler = this.onOut.bind(this);
        this._onUpOutsideHandler = this.onUpOutside.bind(this);

        this.interactive = true;
        this.buttonMode = true;
        this._overSfx = this._downSfx = Sfx.None;

        if (SimpleButton.useMobileEvents)
		{
            this.on("touchstart", this._onDownHandler);
            this.on("touchend", this._onUpHandler);
            this.on("touchendoutside", this._onUpOutsideHandler);
        }
        else
		{
            this.on("mousedown", this._onDownHandler);
            this.on("mouseup", this._onUpHandler);
            this.on("mouseover", this._onOverHandler);
            this.on("mouseout", this._onOutHandler);
            this.on("mouseupoutside", this._onUpOutsideHandler);
        }

        // set default tint values
        this.setTint();
    }


    // public addFilter(overFilter: PIXI.Filter):void
    // {
    //     this._overFilter = overFilter;
    // }


    public setSfx(hover: Sfx, click: Sfx):void
    {
        this._overSfx = hover;
        this._downSfx = click;
    }


    public setTint(overTint: number = 0xcccccc, downTint: number = 0x999999):void
    {
        this._overTint = overTint;
        this._downTint = downTint;
    }

    
    /**
     * Adds a label to the button, uses PIXI.Text
     * @param {string} text                         The text the label should show
     * @param {PIXI.TextStyleOptions} fontStyle     The font style used for the label. The font size is saved separately
     *                                              to be used in the resize function.
     */
    public addLabel(text: string, fontStyle: PIXI.TextStyleOptions)
    {
        this._labelFontSize = +fontStyle.fontSize;
        this._label = new PIXI.Text(text, fontStyle);
        this._label.x = Math.round(-this._label.width / 2.0);
        this._label.y = Math.round(-this._label.height / 2.0);
        this.addChild(this._label);
    }
    

    /**
     * Changes a default event handler for this button only
     *
     * @param {string} type                     The type of the button handler to replace, currently "over" only
     * @param {()  =>   void} callback          The callback to use instead of this.onOver()
     */
    public addHandler(type: string, callback: () => void)
    {
        switch(type)
        {
            case "over":
                this._onOverHandler = callback.bind(this);
                this.on("mouseover", this._onOverHandler);
                break;

            case "out":
                this._onOutHandler = callback.bind(this);
                this.on("mouseout", this._onOutHandler);
                break;
        }
    }


    /**
     * Updates the visuals for when he button is pressed
     */
    private onDown(): void
    {
        Audio.playSfx(this._downSfx);
        this.updateVisualState("down");
    }
    
    /**
     * Emits a clicked event, and updates visuals for the up state.
     */
    private onUp(): void
    {
        this.events.emit(Events.CLICKED);
        this.updateVisualState("up");
    }
    
    /**
     * Updates visuals for the over state
     */
    public onOver(): void
    {
        Audio.playSfx(this._overSfx);
        this.updateVisualState("over");
    }
    
    /**
     * Updates visuals for the out state
     */
    private onOut(): void
    {
        this.updateVisualState("out");
    }
    
    /**
     * Doesn't emit a clicked event, since the up event was outside the bounds of the button. Also, updates visuals for 
     * the up state
     */
    private onUpOutside(): void
    {
        this.updateVisualState("up");
    }
    
    /**
     * Sets the tint for the sprite depending on the current state. Also does this for the label if there is one
     * @param {string} state    The current state of the button
     */
    private updateVisualState(state: string): void
    {
        let tint: number = 0xffffff;
        switch (state)
        {
            case "up":
                tint = 0xffffff;
                break;
            case "down":
                tint = this._downTint;
                break;
            case "over":
                if (SimpleButton.allowRollovers)
                {
                    tint = this._overTint;
                }
                break;
            case "out":
                tint = 0xffffff;
                break;
        }
        this.tint = tint;
        if (this._label)
        {
            this._label.tint = tint;
        }
    }
    
    /**
     * Sets the scale for the button, and resizes the label if there is one
     * @param {number} scale    The scale to set for the button */
    public resize(scale: number)
    {
        this.scale.set(scale, scale);
        if (this._label)
        {
            let s: string = this._label.text;
            // attempt to force PIXI to reformat text
            this._label.text = "";
            this._label.text = s;
            this._label.x = Math.round(-this._label.width / 2.0);
            this._label.y = Math.round(-this._label.height / 2.0);
            /*
        //  this._label.style.fontSize = this._labelFontSize * scale;
            */
        }
    }

    
    /**
     * Destroys the sprite and label, and removes the event listeners
     */
    public destroy()
    {
        if (this._label)
        {
            this._label.destroy();
        }
        this.events.removeAllListeners();
        this.removeAllListeners();
    }

}



