import {Game} from "../Game";
import {GameSettings} from "../settings/GameSettings";
import {GameState} from "../state/GameState";
import {GameData} from "../../data/GameData";
import {Options} from "../ui/Options";
import {GameOver} from "../ui/GameOver";
import {ExtraLives} from "../ui/ExtraLives";
import {FUE} from "../ui/FUE";
import {APIWrapper} from "../util/APIWrapper";
import {LoadSave} from "../util/LoadSave";
import {PlayArea} from "./PlayArea";
import {Background} from "./Background";



// the 'engine' that drives an instance of the game
// owned by GameState, destroyed and recreated when game ends
export class Engine
{
    private _gameState: GameState;
    private _container: PIXI.Container;
    private _playArea: PlayArea;
    private _background: Background;
    private _emitter: PIXI.utils.EventEmitter;
    private _options: Options;
    private _extraLivesPopup: ExtraLives;
    private _gameOverPopup: GameOver;
    private _fue: FUE;



    public constructor(container: PIXI.Container, emitter: PIXI.utils.EventEmitter, gameState: GameState)
    {
        let _that: Engine = this;

        this._gameState = gameState;
        this._container = container;
        this._emitter = emitter;
        this._options = new Options(this._container, this._emitter);

        this._extraLivesPopup = null;
        this._gameOverPopup = null;

        // when the game ends, show the game over popup
        this._emitter.on("no_moves_left", function()
            {
                _that.endOfGameSequence();
            });
        this._emitter.on("user_quit_game", function()
            {
                _that.quitGameSequence();
            });

        this.createPlayArea();
        this._fue = null;
        if (GameSettings.showFUE || GameSettings.showFUEdebug)
        {
            this._fue = new FUE(this._container, this._emitter, this._playArea);
        }
    }


    public destroy():void
    {
        this._emitter.removeListener("no_moves_left");
        this._emitter.removeListener("user_quit_game");

        if (this._fue)
            this._fue.destroy();
        this._fue = null;
        this._gameOverPopup = null;
        this._extraLivesPopup = null;
        if (this._background)
            this._background.destroy();
        this._background = null;
        if (this._playArea)
            this._playArea.destroy();
        this._playArea = null;
        if (this._options)
            this._options.destroy();
        this._options = null;
    }


    private createPlayArea():void
    {
        // create and layout the background images
        this._background = new Background(this._container);

        // create the game play area
        var layout: string;
        if (GameSettings.showFUE)
            layout = GameData.Layouts[0];
        else
            layout = this.createBoardLayout();
        this._playArea = new PlayArea(this._container, this._emitter, this._gameState);
        this._playArea.createBoard(layout);

        // Resize these guys once at the start so visibilty of the left background grass can be determined
        let playAreaRect = this._playArea.resize();
        this._background.resize(playAreaRect);

        // create hierarchy
        this._container.addChild(this._options);    // popups go above the playarea
    }


    // create a board with no sequences of three the same colour either vertically or horizontally
    private createBoardLayout():string
    {
        var layout: string = "";
        var board = [];
        for(let y = 0; y < GameSettings.BoardHigh; y++)
        {
            board[y] = [];
            for(let x = 0; x < GameSettings.BoardWide; x++)
            {
                var col: number, size: number;
                do{
                    do{
                        col = Math.floor(Math.random() * 3);    // any colour
                        size = Math.floor(Math.random() * 4);   // mostly small, some medium
                        board[y][x] = col;
                    }while (x > 1 && board[y][x - 1] == col && board[y][x - 2] == col);    // remove horizontal lines
                }while(y > 1 && board[y - 1][x] == col && board[y - 2][x] == col);         // remove vertical lines
                layout += ["b","g","y"][col] + ["s","s","s","m"][size];
            }
        }
        if (GameSettings.consoleLogging)
            console.log("Created board layout: " + layout);
        return layout;
    }


    public resize():void
    {
        let playAreaRect: PIXI.Rectangle = null;
        if (this._playArea)
            playAreaRect = this._playArea.resize();
        if (this._background)
            this._background.resize(playAreaRect);
        if (this._options)
            this._options.resize();
        if (this._gameOverPopup)
            this._gameOverPopup.resize();
        if (this._extraLivesPopup)
            this._extraLivesPopup.resize();
        if (this._fue)
            this._fue.resize();
    }


    public update():void
    {
        // run the FUE if it exists
        if (this._fue)
        {
            if (!this._fue.update())
            {
                this._fue.destroy();
                this._fue = null;
            }
        }

        // update the play area
        this._playArea.update();
    }


    private endOfGameSequence():void
    {
        let _that: Engine = this;

        if (GameSettings.consoleLogging)
            console.log("end of game sequence begins");
        
        this._extraLivesPopup = new ExtraLives(this._container, this._emitter, this.returnToGame.bind(this), function()
            {
                // on GameOver complete, trigger the end sequence then follow it with finishedEndSequence
                _that._gameOverPopup = new GameOver(_that._container, _that._gameState, function()
                    {
                        // send data to the API about the game just completed
                        APIWrapper.sendGameOver( function() {
                            // ticket 236, call showToast etc *after* snedGameOver is fulfilled
                            APIWrapper.endOfGameSequence(_that.finishedEndSequence.bind(_that));
                        });
                    });
            });
    }


    private quitGameSequence():void
    {
        let _that: Engine = this;

        if (GameSettings.consoleLogging)
            console.log("quit game sequence begins");
        
        // on GameOver complete, trigger the end sequence then follow it with finishedEndSequence
        this._gameOverPopup = new GameOver(this._container, this._gameState, function()
            {
                // send data to the API about the game which was just quitted
                APIWrapper.sendGameOver(null);
                APIWrapper.endOfGameSequence( function()
                    {
                        _that.finishedEndSequence();
                        // this must be done last, because it causes a state change which will destroy the current game instance (including Engine)
                        Game._quitGame = true;
                    });
            });
    }


    private returnToGame():void
    {
        //console.log("Engine.returnToGame: back to game");
        if (this._extraLivesPopup) this._extraLivesPopup.destroy();
        if (this._gameOverPopup) this._gameOverPopup.destroy();
    }


    private finishedEndSequence():void
    {
        if (this._extraLivesPopup) this._extraLivesPopup.destroy();
        if (this._gameOverPopup) this._gameOverPopup.destroy();

        // NOTE: state machine will *instantly* destroy this class instance so don't try anything after!
        this._emitter.emit("show_trophy_room");

        // save progress (do this last to ensure that everything has finished being signalled and handled)
        LoadSave.save();
    }

    public getPlayArea():PlayArea
    {
        return this._playArea;
    }

}



