import {TextureData} from "./AnimationData";



export class AnimatedSprite extends PIXI.extras.AnimatedSprite
{
    public _animation: number;
    public _stoppedAtEnd: boolean;

    // indexed by animation type, holds a list of image refs for that animation
    private _textureList: Array<Array<TextureData>>;
    private _onEnd: Function;
    private _animIndex: number;



    constructor(textureList: Array<Array<TextureData>>)
    {
        let td: TextureData = textureList[0][0];
        super(td._list);
        this._textureList = textureList;
    }


    public setTextures(textureList: Array<Array<TextureData>>)
    {
        this.stop();
        this._textureList = textureList;
        this._animation = -1;
        this._animIndex = 0;
        this._stoppedAtEnd = true;
    }


    public setAnim(animation: number, looped: boolean = true, onEnd?: Function):void
    {
        let firstFrame: number = 0
        if (animation == this._animation)
            firstFrame = this.currentFrame;

        if (animation != this._animation)
        {
            this.loop = looped;
            if (this.loop)
                this.onLoop = this.animEnded;
            else
                this.onComplete = this.animEnded;
            this._animIndex = 0;
            this._animation = animation;
            this._stoppedAtEnd = false;

            this.playSubAnimation(firstFrame);
        }

        this._onEnd = onEnd;
    }


    private playSubAnimation(firstFrame: number = 0):void
    {
        if (this._textureList && this._textureList[this._animation])
        {
            let td: TextureData = this._textureList[this._animation][this._animIndex];
            this.textures = td._list;
            this.animationSpeed = td._speed;
            this.gotoAndPlay(firstFrame);
        }
    }


    private animEnded():void
    {
        // reached the end of the whole animation sequence?
        if (++this._animIndex >= this._textureList[this._animation].length)
        {
            // looping?
            if (this.loop)
            {
                // yes, loop from start of animation sequence
                this._animIndex = 0;
                this.playSubAnimation();
                this._stoppedAtEnd = false;
            }
            else
            {
                // nope, hold it right there
                this.stop();
                this._stoppedAtEnd = true;
            }

            // trigger the callback (which might switch us to another animation, so it must be last)
            if (this._onEnd)
                this._onEnd.call(this);
        }
        else
        {
            // we're not at end of sequence yet
            // start the next animation in the sequence
            this.playSubAnimation();
            this._stoppedAtEnd = false;
        }
    }


}


