import {MissionPopup} from "../ui/MissionPopup";
import {SaveObject} from "./SaveData";
import {GameSettings} from "../settings/GameSettings";
import {PieceColours} from "../const/Enums";



/*
done: chain = chain reaction sequence length (successive matches, not simultaneous ones), compare with goal num
done: move = total moves count in this game after each turn, compare with goal num
done: levelUp = level up event sending moves remaining
done: match = match event with number of dolls in each group, compare with goal num
done: megaMatch = mega match event, keep count
done: score = score increment event with current score, compare with goal num
done: completeGameAll = game ended event, keep count, compare with goal num.
done: moveAll = total moves count in all games after each turn, compare with goal num
done: reachLevel = level up event, add one to goal progress if level is now equal to the target for this mission
done: matchBlue = if match was blue dolls, keep count
done: matchGreen = ditto green
done: matchYellow = ditto yellow
done: matchAll = keep count
done: chain2 = if this is the first move, add one when the chain reaches the goal num
*/


export class MissionData
{
    // describe the mission
    public _id:number;
    public _type:string;
    public _required:number;
    public _name:string;
    public _description:string;
    public _goalNum:number;
    public _imgUrl:string;

    // measures progress towards the goal completion
    public _goalProgress:number;
    // measures progress towards the mission completion by counting the number of times the goal has been achieved
    public _completed:number;
    // set true when the mission is completed, reset when the trophy room shows the trophy flashing
    public _newlyCompleted:boolean;



    constructor(id:number, type:string, numOfActions:number, name:string, description:string, goalNum:number, imgUrl:string)
    {
        this._id = id;
        this._type = type;
        this._required = numOfActions;
        this._name = name;
        this._description = description;
        this._goalNum = goalNum;
        this._imgUrl = imgUrl;

        this._completed = 0;
        this._goalProgress = 0;
        this._newlyCompleted = false;
    }


    // work out which trophies should be open from the MissionDescription progress
    public static openTrophies():void
    {

    }
}


