import {GameSettings} from "../settings/GameSettings";



export class ThumbSlider extends PIXI.Sprite
{
    private _thumb: PIXI.Sprite;
    private _drag: boolean;
    private _adjusted: Function;


    constructor(sliderPic: PIXI.Texture, thumbPic: PIXI.Texture, adjust: Function)
    {
        super(sliderPic);

        this._adjusted = adjust;
        this._drag = false;
        this.buttonMode = true;
        this.interactive = true;
        
        this._thumb = new PIXI.Sprite(thumbPic);
        this._thumb.anchor.set(0.5);
        this._thumb.scale.set(0.5);
        this._thumb.x = GameSettings.sfxVolume / 100 * this.width - this.width / 2;
        this._thumb.y = 0;

        let _that: ThumbSlider = this;
        this.on("pointerdown", function(e) { _that._drag = true; _that.dragSet(e); });
        this.on("pointerup", function(e) { _that._drag = false; });
        this.on("pointerupoutside", function(e) { _that._drag = false; });
        this.on("mouseout", function(e) { _that._drag = false; });
        this.on("pointermove", this.dragSet);

        this.addChild(this._thumb);
    }


    private dragSet(e: PIXI.interaction.InteractionEvent)
    {
        if (this._drag)
        {
            let pos: PIXI.Point = e.data.getLocalPosition(this);
            this._thumb.x = Math.min(Math.max(pos.x, -this.width / 2), this.width / 2);
            this._adjusted.call(this, (this._thumb.x + this.width / 2) / this.width);
        }
    }


    /// @param: value = { 0.0 ... 1.0 }
    public set(value: number, call: boolean = false):void
    {
        this._thumb.x = value * this.width - this.width / 2;
        if (call)
            this._adjusted.call(this, (this._thumb.x + this.width / 2) / this.width);        // 0.0 .. 1.0
    }


    public destroy()
    {
        this.removeAllListeners();
    }

}


