


/**
 * Simple button class with optional label
 */
export class Tooltips
{
    private static container: PIXI.Container;

    private static list: Array<BubbleSprite>;
    private static scale: PIXI.Point;
    private static textStyle: PIXI.TextStyle;



    public static create(_parent: PIXI.Container)
    {
        Tooltips.container = _parent;
        Tooltips.list = [];
        Tooltips.scale = new PIXI.Point(1, 1);


        // small devices appear to scale the text differently relative to the speech bubbles
        // we use a smaller font on these to prevent the text overlapping at the edges
        if (PIXI.utils.isMobile.phone)
        {
            Tooltips.textStyle = new PIXI.TextStyle(
                {
                    fontFamily: "GrilledCheese",
                    fontSize: 34,
                    fontWeight: 'bold',
                    fill: ['#000000'],
                    dropShadow: false,
                    align: 'center',
                    leading: -7,
                    wordWrap: true,
                    wordWrapWidth: 250
                });
        }
        else
        {
            Tooltips.textStyle = new PIXI.TextStyle(
                {
                    fontFamily: "GrilledCheese",
                    fontSize: 36,
                    fontWeight: 'bold',
                    fill: ['#000000'],
                    dropShadow: false,
                    align: 'center',
                    leading: -10,
                    wordWrap: true,
                    wordWrapWidth: 250
                });
        }
    }


    public static add(x: number, y: number, tip: string):void
    {
        // don't create duplicates
        let bs: BubbleSprite = Tooltips.find(tip);
        if (bs != null)
        {
            window.clearTimeout(bs._timer);
            bs._timer = window.setTimeout(Tooltips.remove.bind(this, tip), 2000);
            return;
        }

        let s: BubbleSprite = new BubbleSprite(PIXI.Texture.fromImage("Bubble_01"));
        s.anchor.set(0.3, 1.0);
        s.x = x;
        s.y = y;
        s.scale.set(-Tooltips.scale.x, Tooltips.scale.y);
        s._onTop = true;
        s._text = new PIXI.Text(tip, Tooltips.textStyle);
        s._text.anchor.set(0.5, 0.0);
        s.setText(60, -s.texture.height + 0, tip);
        Tooltips.list.push(s);
        Tooltips.container.addChild(s);
        s._timer = window.setTimeout(Tooltips.remove.bind(this, tip), 2000);
    }


    private static find(_tip: string):BubbleSprite
    {
        if (Tooltips.list && Tooltips.container)
        {
            for(let i = 0; i < Tooltips.list.length; i++)
            {
                let bs: BubbleSprite = Tooltips.list[i];
                if (bs._text && bs._text.text == _tip)
                {
                    return bs;
                }
            }
        }
        return null;
    }


    public static remove(_tip: string):void
    {
        if (Tooltips.list && Tooltips.container)
        {
            for(let i = 0; i < Tooltips.list.length; i++)
            {
                let bs: BubbleSprite = Tooltips.list[i];
                if (bs._text && bs._text.text == _tip)
                {
                    window.clearTimeout(bs._timer);
                    Tooltips.container.removeChild(bs);
                    Tooltips.list.splice(i, 1);
                    break;
                }
            }
        }
    }


    public static destroy():void
    {
        if (Tooltips.list && Tooltips.container)
        {
            for(let i = 0; i < Tooltips.list.length; i++)
            {
                let bs: BubbleSprite = Tooltips.list[i];
                Tooltips.container.removeChild(bs);
            }
            Tooltips.list = null;
            Tooltips.container = null;
        }
    }


    /**
     * Sets the scale for the speech bubbles and resizes their labels.
     * @param {number} scale    The scale to set. */
    public static resize(scale: number)
    {
        Tooltips.scale.set(scale * 0.75, scale * 0.75);
        for(let i = 0; i < Tooltips.list.length; i++)
        {
            let bs: BubbleSprite = Tooltips.list[i];
            if (bs._text)
            {
                let s: string = bs._text.text;
                // attempt to force PIXI to reformat text
                bs._text.text = "";
                bs._text.text = s;
                bs._text.x = Math.round(-bs._text.width / 2.0);
                bs._text.y = Math.round(-bs._text.height / 2.0);
            }
        }
    }
    
}



class BubbleSprite extends PIXI.Sprite
{
    public _onTop: boolean;
    public _text: PIXI.Text;
    public _timer: number;


    constructor(texture: PIXI.Texture)
    {
        super(texture);
        this._timer = -1;
    }


    public setText(x: number, y: number, msg: string):void
    {
        if (this._text)
        {
            this._text.text = msg;
            this._text.x = x;
            this._text.y = y;
            if (this.scale.x < 0)
                this._text.scale.x = -Math.abs(this._text.scale.x);
            this.addChild(this._text);
        }
    }
}



