//
// LoadSave.ts
//
// Pete Baron 2018
//
// load or save game variables
//
//
// Terminology (from Pogo API):
//
// preferences = player preferences, default includes mute/volume settings only
// savedata = user data that won't affect a user's progress if lost, e.g. tutorial seen flags
// profile = player's profile object: userId, screenName, entitlement (GUEST, FREE, CLUB)
// stats = player stats data: rank, inventory, game, service, dataslots
//

import {GameSettings} from "../settings/GameSettings";
import {APIWrapper} from "../util/APIWrapper";
import {Audio} from "../ui/Audio";
import {SaveData, SaveObject} from "./SaveData";
import {Inventory} from "./Inventory";
import {PrefObject} from "./PrefData";



export class LoadSave
{
    public static prefs: PrefObject;
    public static profile: any;



    constructor()
    {
        // initialise the preferences object using defaults GameSettings
        LoadSave.prefs = new PrefObject();
        LoadSave.profile = null;

        // initialise the savedata object using defaults from GameSettings
        SaveObject.init();
    }


    public static load():boolean
    {
        //console.log("LoadSave.load");
        if (!LoadSave.prefs)
            console.error("LoadSave constructor was not called!");

        LoadSave.profile = APIWrapper.getProfile();
        if (GameSettings.consoleLogging)
        {
            console.log("player profile received: " + LoadSave.profile.entitlement);
            console.log("userId = " + LoadSave.profile.userId);            // undefined if FREE or GUEST entitlement
            console.log("screenName = " + LoadSave.profile.screenName);    // undefined if FREE or GUEST entitlement
        }

        // set the data object from the player preferences
        LoadSave.prefs.setFromPreferences();

        // try to get saved data for the player
        SaveData.load();

        // get player stats including inventory (boosts + lives)
        Inventory.load();

        return true;
    };


    public static save():boolean
    {
        if (GameSettings.consoleLogging)
            console.log("LoadSave.save");
        
        if (!LoadSave.prefs)
            console.error("LoadSave constructor was not called!");

        let i: string = Inventory.toJson();

        // save the player profile (to localStorage only, there is no Pogo equivalent)
        APIWrapper.setProfile(LoadSave.profile);
        // sync the prefs object with the current GameSettings
        LoadSave.prefs.setFromSettings();
        // save the prefs object
        LoadSave.prefs.saveToPreferences();
        // save the data object
        SaveData.save();

        return true;
    };


    // called when a preference is changed
    // get the new value and apply it to the game
    public static prefChanged(name: string, value: any):void
    {
        if ( GameSettings.consoleLogging )
            console.log("LoadSave.prefChanged " + name + " = " + value);
        switch(name)
        {
            case "sound": GameSettings.APIMute = !value; break;
            case "musicMute": GameSettings.musicMute = value; break;
            case "musicVolume": GameSettings.musicVolume = value; break;
            case "sfxMute": GameSettings.sfxMute = value; break;
            case "sfxVolume": GameSettings.sfxVolume = value; break;
            case "showTutorial": GameSettings.showTutorial = value; break;
            case "showFUE": GameSettings.showFUE = value; break;
            case "sound volume": break;
            case "music volume": break;
            default: console.log("Warning: unknown type in Preferences " + name + " = " + value);
        }

        // apply new audio settings
        Audio.set();
    }

}



