import {GameSettings} from "../settings/GameSettings";
import {Howl} from "howler";
import {Game} from "../Game";



export enum Sfx
{
    None = -1,

    // music
    titlescreen_music = 0,
    ingame_music,
    FirstSfx,

    // sound effects
    boost_activate = FirstSfx,
    boost_cb_changing_color,
    boost_cb_panel_open,
    boost_remove_doll,
    boost_size_up_doll,
    button_press,
    button_rollover,
    doll_big_complete,
    doll_close,
    doll_drop,
    doll_level_up_1,
    doll_level_up_2,
    doll_open,
    doll_overmatch,
    doll_pick,
    doll_spin_out,
    doll_tile_power_up,
    help_panel_close,
    help_panel_open,
    item_timer,
    level_bar,
    level_up,
    level_up_with_cutecheer,
    moves_dec,
    moves_inc,
    nine_moves_left,
    no_more_moves_panel_close,
    no_more_moves_panel_open,
    option_panel_close,
    option_panel_open,
    out_of_moves_placard,
    play_button_click,
    quit_panel_close,
    quit_panel_open,
    stars_animation,
    talkbubble_popup,

    VO_doll_cheer_1,
    VO_doll_cheer_2,
    VO_doll_cheer_3,
    VO_doll_cheer_4,
    VO_doll_voice_1,
    VO_doll_voice_10,
    VO_doll_voice_2,
    VO_doll_voice_3,
    VO_doll_voice_4,
    VO_doll_voice_5,
    VO_doll_voice_6,
    VO_doll_voice_7,
    VO_doll_voice_8,
    VO_doll_voice_9,
    VO_doll_voice_uh_uh,
    VO_Trizzle_titlecheer,

    LastSfx
}



export class Audio
{
    private static readonly _path:string = "./assets/sounds/";
    private static readonly _sfxNames:Array<string> = [
        // *MUST* match order of Sfx enum
            "titlescreen_music",
            "ingame_music",

            "boost_activate",
            "boost_cb_changing_color",
            "boost_cb_panel_open",
            "boost_remove_doll",
            "boost_size_up_doll",
            "button_press",
            "button_rollover",
            "doll_big_complete",
            "doll_close",
            "doll_drop",
            "doll_level_up_1",
            "doll_level_up_2",
            "doll_open",
            "doll_overmatch",
            "doll_pick",
            "doll_spin_out",
            "doll_tile_power_up",
            "help_panel_close",
            "help_panel_open",
            "item_timer",
            "level bar ",
            "level_up",
            "level_up_with_cutecheer",
            "moves_dec",
            "moves_inc",
            "nine_moves_left",
            "no_more_moves_panel_close",
            "no_more_moves_panel_open",
            "option_panel_close",
            "option_panel_open",
            "out_of_moves_placard",
            "play_button_click",
            "quit_panel_close",
            "quit_panel_open",
            "stars_animation",
            "talkbubble_popup",

            "doll_cheer_1",
            "doll_cheer_2",
            "doll_cheer_3",
            "doll_cheer_4",
            "doll_voice_1",
            "doll_voice_10",
            "doll_voice_2",
            "doll_voice_3",
            "doll_voice_4",
            "doll_voice_5",
            "doll_voice_6",
            "doll_voice_7",
            "doll_voice_8",
            "doll_voice_9",
            "doll_voice_uh_uh",
            "Trizzle_titlecheer",
        ];

    // private static _titleTune:Howl;
    // private static _gameTune:Howl;
    // private static _sfx:Array<Howl>;


    public static soundSpriteJSON:any;
    private static _allSounds:Howl;
    private static _currentTune:Sfx;
    private static _enabled:boolean = false;
    private static _visibilityMute:boolean = false;
    private static _titleScreenMusicId: number;
    private static _inGameMusicId: number;
    private static _sfxVolume: number;


    constructor()
    {
        Audio._enabled = false;
        Audio._visibilityMute = false;
        Audio._titleScreenMusicId = -1;
        Audio._inGameMusicId = -1;
        Audio._sfxVolume = 0.5;

        Howler.volume(GameSettings.globalVolume);

        // fix paths to sound sprite files
        for(let i: number = 0; i < Audio.soundSpriteJSON.urls.length; i++)
            Audio.soundSpriteJSON.urls[i] = Audio._path + Audio.soundSpriteJSON.urls[i];

        // use the sound sprite
        Audio._allSounds = new Howl({
            src: Audio.soundSpriteJSON.urls,
            sprite: Audio.soundSpriteJSON.sprite
        });

        Audio._currentTune = Sfx.None;

/*
// old approach using individual sound files

        Audio._sfx = new Array<Howl>();
        Audio._titleTune = new Howl({
            src: [Audio._path + 'titlescreen_music.mp3'],
            html5: false,
            loop: true
        });
        Audio._sfx.push(Audio._titleTune);
        Audio._gameTune = new Howl({
            src: [Audio._path + 'ingame_music.mp3'],
            html5: false,
            loop: true
        });
        Audio._sfx.push(Audio._gameTune);

        for(let i:number = Sfx.FirstSfx; i < Sfx.LastSfx; i++)
        {
            let h:Howl = new Howl({
                src: [Audio._path + Audio._sfxNames[i]],
                html5: false,
                loop: false
            });
            Audio._sfx.push(h);
        }
*/
        // visibility switch signal handler
        Game._stage.on("visibility_change", function(visible){
                if (visible)
                {
                    Audio._visibilityMute = false;
                    //console.log("unmute (visible)");
                    switch(Audio._currentTune)
                    {
                        case Sfx.titlescreen_music:
                            if (Audio._titleScreenMusicId >= 0 && !GameSettings.musicMute && !GameSettings.APIMute)
                                Audio._allSounds.mute(Audio._visibilityMute, Audio._titleScreenMusicId);
                            break;
                        case Sfx.ingame_music:
                            if (Audio._inGameMusicId >= 0 && !GameSettings.musicMute && !GameSettings.APIMute)
                                Audio._allSounds.mute(Audio._visibilityMute, Audio._inGameMusicId);
                            break;
                    }
                }
                else
                {
                    Audio._visibilityMute = true;
                    //console.log("mute (hidden)");
                    if (Audio._titleScreenMusicId >= 0 && !GameSettings.musicMute && !GameSettings.APIMute)
                        Audio._allSounds.mute(Audio._visibilityMute, Audio._titleScreenMusicId);
                    if (Audio._inGameMusicId >= 0 && !GameSettings.musicMute && !GameSettings.APIMute)
                        Audio._allSounds.mute(Audio._visibilityMute, Audio._inGameMusicId);
                }
            });
    }


    // call from any user interaction to signal that audio is allowed now
    public static enable():void
    {
        if (!Audio._enabled)
        {
            Audio._enabled = true;
            //console.log("Audio is enabled by user interaction");
            // play a quiet click sound to ensure things are started
            Audio.playSfx(Sfx.play_button_click, 0.1);
        }
    }


    public static playSfx(sfx: Sfx, vol?: number):void
    {
        if (Audio._enabled && !Audio._visibilityMute)
        {
            if (!GameSettings.sfxMute && !GameSettings.APIMute && sfx != Sfx.None)
            {
                let id: number = Audio._allSounds.play(Audio._sfxNames[sfx]);
                Audio._allSounds.volume(vol || Audio._sfxVolume, id);
                //console.log("playSfx " + Audio._sfxNames[sfx] + " " + (vol || Audio._sfxVolume) + " id=" + id);
            }
        }
    }


    // private static sfxVolume(volume:number):void
    // {
    //     for(let i:number = Sfx.FirstSfx; i < Sfx.LastSfx; i++)
    //     {
    //         Audio._allSounds[Audio._sfxNames[i]].volume(volume);
    //         //Audio._sfx[i].volume(volume);
    //     }
    // }


    public static titleTune():void
    {
        if (Audio._enabled)
        {
            //console.log("play title tune");

            if (Audio._inGameMusicId >= 0)
                Audio._allSounds.stop(Audio._inGameMusicId);
            Audio._inGameMusicId = -1;

            if (Audio._titleScreenMusicId >= 0)
                Audio._allSounds.stop(Audio._titleScreenMusicId);
            Audio._titleScreenMusicId = Audio._allSounds.play(Audio._sfxNames[Sfx.titlescreen_music]);
            Audio._allSounds.loop(true, Audio._titleScreenMusicId);

            Audio.set();
            if (Audio._inGameMusicId >= 0)
                Audio._allSounds.mute(true, Audio._inGameMusicId);
            Audio._inGameMusicId = -1;
            
            Audio._currentTune = Sfx.titlescreen_music;
        }
    }


    public static gameTune():void
    {
        if (Audio._enabled)
        {
            //console.log("play game tune");

            if (Audio._titleScreenMusicId >= 0)
                Audio._allSounds.stop(Audio._titleScreenMusicId);
            Audio._titleScreenMusicId = -1;

            if (Audio._inGameMusicId >= 0)
                Audio._allSounds.stop(Audio._inGameMusicId);
            Audio._inGameMusicId = Audio._allSounds.play(Audio._sfxNames[Sfx.ingame_music]);
            Audio._allSounds.loop(true, Audio._inGameMusicId);

            Audio.set();
            if (Audio._titleScreenMusicId >= 0)
                Audio._allSounds.mute(true, Audio._titleScreenMusicId);
            Audio._titleScreenMusicId = -1;
            
            Audio._currentTune = Sfx.ingame_music;
        }
    }


    public static set():void
    {
        if (Audio._allSounds === undefined) return;
        if (Audio._titleScreenMusicId >= 0)
        {
            Audio._allSounds.volume(GameSettings.musicVolume / 100, Audio._titleScreenMusicId);
        }
        if (Audio._inGameMusicId >= 0)
        {
            Audio._allSounds.volume(GameSettings.musicVolume / 100, Audio._inGameMusicId);
        }

        Audio._sfxVolume = GameSettings.sfxVolume / 100;
        //console.log("Audio._sfxVolume = " + Audio._sfxVolume);

        // if API sets mute 'on', ignore local settings and mute everything...
        if (!GameSettings.APIMute)
        {
            if (Audio._titleScreenMusicId >= 0)
                Audio._allSounds.mute(GameSettings.musicMute || GameSettings.APIMute, Audio._titleScreenMusicId);
            if (Audio._inGameMusicId >= 0)
                Audio._allSounds.mute(GameSettings.musicMute || GameSettings.APIMute, Audio._inGameMusicId);
        }
        else
        {
            if (Audio._titleScreenMusicId >= 0)
                Audio._allSounds.mute(true, Audio._titleScreenMusicId);
            if (Audio._inGameMusicId >= 0)
                Audio._allSounds.mute(true, Audio._inGameMusicId);
        }
    }


    public static isEnabled():boolean
    {
        return Audio._enabled;
    }


}



