import {APIWrapper} from "../util/APIWrapper";
import {GameSettings} from "../settings/GameSettings";



export class Inventory
{
    public static powerup_paint:number;
    public static powerup_size_up:number;
    public static powerup_remove_doll:number;
    public static powerup_extra_life:number;


    public static init():void
    {
        if (Inventory.powerup_paint === undefined)
            Inventory.powerup_paint = GameSettings.startBoosts;
        if (Inventory.powerup_size_up === undefined)
            Inventory.powerup_size_up = GameSettings.startBoosts;
        if (Inventory.powerup_remove_doll === undefined)
            Inventory.powerup_remove_doll = GameSettings.startBoosts;
        if (Inventory.powerup_extra_life === undefined)
            Inventory.powerup_extra_life = GameSettings.startLives;
    }


    public static fromJson(jsonString: string):void
    {
        if (GameSettings.consoleLogging)
            console.log("Inventory fromJson: " + jsonString);

        let o:any = JSON.parse(jsonString);
        // first time through, all these will be undefined... hold on to init values
        if (o.powerup_paint != undefined) Inventory.powerup_paint = Math.max(o.powerup_paint, 0);
        if (o.powerup_size_up != undefined) Inventory.powerup_size_up = Math.max(o.powerup_size_up, 0);
        if (o.powerup_remove_doll != undefined) Inventory.powerup_remove_doll = Math.max(o.powerup_remove_doll, 0);
        if (o.powerup_extra_life != undefined) Inventory.powerup_extra_life = Math.max(o.powerup_extra_life, 0);
    }


    public static toJson():string
    {
        let o:any = {
            powerup_paint: Inventory.powerup_paint,
            powerup_size_up: Inventory.powerup_size_up,
            powerup_remove_doll: Inventory.powerup_remove_doll,
            powerup_extra_life: Inventory.powerup_extra_life
        }

        let s:string = JSON.stringify(o);
        if (GameSettings.consoleLogging)
            console.log("Inventory.toJson: " + s);
        return s;
    }


    public static load():void
    {
        let stats:any = APIWrapper.getStats();

        if (GameSettings.consoleLogging)
            console.log("Inventory.load() player stats received...");

        if (stats)
        {
            if (GameSettings.consoleLogging)
            {
                //console.log("stats.rank: " + JSON.stringify(stats.rank));
                console.log("stats.inventory: " + JSON.stringify(stats.inventory));
                //console.log("stats.game: " + JSON.stringify(stats.game));
                //console.log("stats.service: " + JSON.stringify(stats.service));
                //console.log("stats.dataslots: " + JSON.stringify(stats.dataslots));
            }
            Inventory.fromJson(JSON.stringify(stats.inventory));
        }
        else
        {
            if (GameSettings.consoleLogging)
                console.log("player stats are undefined, using default values");
        }
    }

}



